/** *
 * @version 20190401
 * @author wuwg
 * @createTime  20190401
 * @updateTime  20190430
 * @decription  树性能优化，拖拽事件
 */
import dragHandler from './dragHandler.js';
import dragPreview from './dragPreview.js';
import dragDragNodeDescendantState from './dragDragNodeDescendantState.js';
import fdEvent from '../../utils/tools/event.js';

export default {
    props: {},
    mixins: [
        dragHandler,
        dragPreview,
        dragDragNodeDescendantState
    ],
    data() {
        return {
            // 是否是本树的拖拽
            isSelfTreeDrag: true,
            // 是否正在拖拽
            edDragging: false,
            // 当前拖动数据
            edCurrentDragDataItem: {},
            // 当前拖动数据
            edCurrentDropDataItem: null,
            // 长按时间
            edLongTapTime: this.longTapTime,
            // 鼠标的timer
            edMouseTimer: null,
            // 鼠标松开的定时器
            edMouseUpTimer: null,
            // 复制当前的数据列表
            edCopyCurrentDataList: [],
            // 放置条目内部的条目id
            edDropInnerItemId: '',
            // 放置数据的位置
            edDropPostion: '',
            // 【originTree】 2019-05-21
            targetPostion: '',
            // 是否是多选拖拽
            edMultiple: false,
            // 多选拖拽数据
            edMultipleDragData: []
        };
    },
    methods: {
        // 重置拖拽后的回调函数
        edResetDragResetCallback() {
            if (typeof(this.edDragResetCallback) === 'function') {
                this.edDragResetCallback();
            }
            // 拖拽完成后的回调函数
            this.edDragResetCallback = null;
        },
        // 设置放置位置
        edSetDropPostion(position) {
            this.edDropPostion = position;
            this.targetPostion = position;
        },
        // 设置是否是多选拖拽
        edSetMultiple(flag) {
            this.edMultiple = flag;
        },
        // 设置多选拖拽数据
        edSetMultipleDragData(dataList) {
            // 多选
            this.edSetMultiple(dataList.length > 0);
            // 多选的数据
            this.edMultipleDragData = dataList;
        },
        // 设置是否正在拖拽
        edSetDragging(flag) {
            // 拖拽状态设置
            this.edDragging = flag;
            // 未在拖拽
            // 给body增加状态类名
            document.body.setAttribute('dragging', flag);
        },
        // 设置放置条目内部的条目id
        edSetDropInnerItemId(id) {
            // 可以拖动
            this.edDropInnerItemId = id;
        },
        // 复制列表源数据
        edSetCopyCurrentDataList() {
            // 复制源数据列表，保证拖动过程中不会发生变化
            this.edCopyCurrentDataList = JSON.parse(JSON.stringify(this.currentDataList));
        },
        // 设置当前拖拽数据
        edSetCurrentDragDataItem(dataItem, isOutSide) {
            // 当前拖动数据  todo 【originTree】 2019-05-21
            this.edCurrentDragDataItem = !isOutSide ? dataItem : dataItem.treeNodeData;
            // 【originTree】 2019-05-21
            this.dragNode = dataItem[this.dKey]
                ? (this.getTreeCacheObjectNode(dataItem[this.dKey]) ? this.getTreeCacheObjectNode(dataItem[this.dKey]) : dataItem) : dataItem;
        },
        // 设置当前放置区域的数据
        edSetCurrentDropDataItem(dataItem) {
            // 当前拖动数据
            this.edCurrentDropDataItem = dataItem;
            // 【originTree】 2019-05-21
            this.hoverNode = dataItem ? this.getTreeCacheObjectNode(dataItem[this.dKey]) : {};
        },

        // 清除与鼠标拖拽相关的状态
        edClearMouseDragStatus() {
            // 1.还原数据
            this.resetDragNodeDescendantState();
            // 2.清除定时器
            this.edClearMouseTimer();
            // 3.设置拖拽状态(不可拖动)
            this.edSetDragging(false);
            // 4.设置当前被拖拽的数据
            this.edSetCurrentDragDataItem({});
            // 5.设置当前放置的数据
            this.edSetCurrentDropDataItem(null);
            // 6.设置放置位置
            this.edSetDropPostion('');
            // 7.取消绑定拖拽事件
            this.edUnbindDragEvent();
            // 8.设置缩略图隐藏
            this.edSetDragPreviewStatus(false);
            // 9.设置放置条目内部的条目id
            this.edSetDropInnerItemId('');
            // 10.清除mouseup定时器
            this.edClearMouseUpTimer();
            // 11.设置是否是多选拖拽（不是多选）
            this.edSetMultiple(false);
            // 12.设置多选拖拽数据
            this.edSetMultipleDragData([]);
            // 13.是自己树的拖拽
            this.isSelfTreeDrag = true;
            // 禁止文字选中
            window.fdGlobal.removeClass(window.fdGlobal.body, 'fd-user-select-none');
            window.fdGlobal.removeClass(window.fdGlobal.body, 'fd-dragging');
            // 14.重置拖拽后的回调函数
            this.edResetDragResetCallback();
        },
        // 当某被拖动的对象在另一对象容器范围内拖动时触发此事件
        edDragover(event, item) {
            // 在dragover中一定要执行preventDefault()，否则ondrop事件不会被触发 ！important
            // 阻止默认事件
            this.ePreventDefault(event);
            // 触发事件
            this.emitEvent(event, 'dragover', item);
            return true;
        },
        // 清除定时器
        edClearMouseTimer() {
            // 清除定时器
            if (this.edMouseTimer) {
                // 清除定时器
                clearTimeout(this.edMouseTimer);
                this.edMouseTimer = null;
            }
        },
        // 鼠标按下，超时说明是鼠标拖动【一、dragstart】
        edMousedown(event, item) {
            // 清除与鼠标拖拽相关的状态
            this.edClearMouseDragStatus();
            // 优先判断是否可以拖拽
            // 拖拽函数允许，配置允许， 条目未不允许，
            // 用户函数返回true, 可以拖拽，不禁用节点，节点不禁用拖拽
            if (this.edCanDragCallback(item, event, this) && this.edDraggable && !item.notDrag && !item.disabled) {
                // 设置定时器
                this.edMouseTimer = setTimeout(() => {
                    // 开始拖拽
                    this.edDragstartHandler(event.item);
                }, this.edLongTapTime);
            }
        },
        // 取消绑定拖拽事件
        edUnbindDragEvent() {
            // 绑定移动事件
            fdEvent.off(document, 'mousemove', this.edDrag, true);
            // 绑定松开事件
            fdEvent.off(document, 'mouseup', this.edDragend, true);
        },
        // 绑定拖拽事件
        edBindDragEvent() {
            // 允许拖拽
            // if (this.edDraggable) {
            if (this.draggable) {
                // 注意全局事件采用的事捕获，而非冒泡， 因为部分后代元素采用阻止冒泡
                // 绑定移动事件 【二、drag】
                fdEvent.on(document, 'mousemove', this.edDrag, true);
                // 绑定松开事件 【三，dragend】
                fdEvent.on(document, 'mouseup', this.edDragend, true);
            }
        },
        // 拖拽中
        edDrag(event) {
            // 获取event对象
            const _event = this.eGetEvent(event);
            // 拖拽中处理函数
            this.edDragHandler(_event);
        },
        // 清除mouseup定时器
        edClearMouseUpTimer() {
            // 鼠标松开的定时器
            if (this.edMouseUpTimer) {
                clearTimeout(this.edMouseUpTimer);
                this.edMouseUpTimer = null;
            }
        },
        // 拖拽结束
        edDragend() {
            // 获取event对象
            const _event = this.eGetEvent(event);
            // 设置定时器
            this.edMouseUpTimer = setTimeout(() => {
                // 拖拽结束处理函数
                this.edDragendHandler(_event);
            }, 0);
        },
        // 是否可以触发dragenter
        edCanDrop(dataItem) {
            // 是否可以放置在该区域
            // 1. 正在拖拽
            return this.edCanDragReadyed(dataItem) &&
                // 目标区域是否可以放下回调函数，返回一个boolean（默认返回true）
                this.edCanDropCallback(this.edCurrentDropDataItem, this.edCurrentDragDataItem, dataItem, this) || dataItem.type === 'placeholder';
        },
        // drag事件是否已经就绪
        edCanDragReadyed(dataItem) { // eslint-disable-line
            // 可以拖拽
            return this.edDragging &&
                // 非禁用元素
                !dataItem.disabled &&
                // 可以拖拽
                this.edDraggable &&
                // 不是正在拖拽的元素的后代元素
                !dataItem.draggingancestors &&
                // 【originTree】 2019-05-21
                // 是否允许拖拽
                this.edCanDragCallback(dataItem) &&
                // 非拖拽目标元素
                this.edCurrentDragDataItem[this.dKey] !== dataItem[this.dKey] &&
                // 非占位元素
                dataItem.type !== 'placeholder';
        },
        // 鼠标进入某个容器【四、dragenter】
        edMouseenter(event, item, index) {
            // 清除定时器
            this.edClearMouseTimer();
            // 获取event对象
            // 如果drag事件已经就绪
            if (this.edCanDragReadyed(item)) {
                // 拖拽进入某个容器处理函数
                this.edDragenterHandler(event, item, index);
            }
        },
        // 鼠标在某个容器内移动 【五、dragover】
        edMousemove(event, item, index) {
            // 如果drag事件已经就绪
            if (this.edCanDragReadyed(item)) {
                // 拖拽在某个容器内移动处理函数
                this.edDragoverHandler(event, item, index);
            }
        },
        // 鼠标离开某个容器 【六、dragleave】
        edMouseleave(event) {
            // 清除定时器
            this.edClearMouseTimer();
            // 必须是正在拖拽状态
            if (this.edDragging) {
                // 拖拽离开某个容器处理函数
                this.edDragleaveHandler(event);
            }
        },
        // 鼠标在某个容器松开 【七、drop】
        edMouseup(event, item) {
            // 清除定时器
            this.edClearMouseTimer();
            // 如果可以放置
            if (this.edCanDrop(item)) {
                // 拖拽离开某个容器处理函数
                this.edDropHandler(event, item);
            } else {
                // 1.拖拽失败
                this.emitEvent(event, 'drop-fail', null, item);
                // 2. 清空状态
                this.edClearMouseDragStatus();
            }
        }
    },
    created() {
        // 创建视图
        this.edMakeDragPreview();
    },
    beforeDestroy() {
        // 销毁拖拽视图
        this.edDestroyDragPreview();
    }
};



