/**
 * @version 20200815
 * @author wuwg
 * @createTime  20200815
 * @updateTime  20200816
 * @decription  【多选数据】处理模块
 *@namespace  dm:   d:data  m :multuple   Operate Tree Data
 */


export default {
    methods: {

        // 初始化多选的数据
        dmInitCheckedData() {
            this.dmCheckedData = new Map();
        },

        // 添加复选框的数据，（key:value）
        dmAddCheckedData(item) {
            this.dmCheckedData.set(item[this.dKey], item);
        },

        // 删除复选框的数据，（key）,  返回一个boolean值，删除成功返回 true
        dmDeleteCheckedData(key) {
            this.dmCheckedData.delete(key);
        },

        // 判断目标节点是否选中，（key）
        dmAdjustChecked(key) {
            this.dmCheckedData.has(key);
        },

        // 获取当前选中的keys
        dmGetCheckedKeys() {
            const _dmCheckedKeys = [];
            // 循环当前选中的数据对象
            this.dmCheckedData.forEach((value, key) => {
                // 获取当前选中的key
                _dmCheckedKeys.push(key);
            });
            return _dmCheckedKeys;
        },

        // 获取当前选中的value
        dmGetCheckedData() {
            const _dmCheckedData = [];
            // 循环当前选中的数据对象
            this.dmCheckedData.forEach((value) => {
                // 获取当前选中的数据
                _dmCheckedData.push(value);
            });
            return _dmCheckedData;
        },

        // 更新当前选中的数据
        dmUpdateCheckedData(checkedKeys) {
            // 先判断当前的参数是否是是数组
            if (Array.isArray(checkedKeys)) {
                // 1. 先重置数据
                this.dmResetCheckedData();
                // 2. 处理当前选中数据
                this.dmOparteCheckedData(checkedKeys);
            }
        },

        // 重置当前的数据
        dmResetCheckedData() {
            // 如果存在选中的对象
            if (this.dmCheckedData.size) {
                // 循环当前选中的数据对象
                this.dmCheckedData.forEach((value, key) => {
                    // 创建数据条目
                    const _dataItem = {
                        checked: false,
                        [this.dKey]: key
                    };
                    // 更新目标缓存数据
                    this.cUpdateCache(_dataItem, 'checked');
                    // 更新目标性能缓存数据
                    this.dpcUpdateCache(_dataItem, 'checked');
                });
                //  清空对象
                this.dmCheckedData.clear();
            }
        },

        // 处理当前选中的数据
        dmOparteCheckedData(checkedKeys) {
            checkedKeys.forEach((key) => {
                // 创建数据条目
                const _dataItem = {
                    checked: true,
                    [this.dKey]: key
                };
                // 更新目标缓存数据
                this.cUpdateCache(_dataItem, 'checked');
                // 更新目标性能缓存数据
                this.dpcUpdateCache(_dataItem, 'checked');
            });
        }

    }
};



