/**
 *@version 1.0.1
 *@author wuwg
 *@createTime 2020/09/25 - 10:57
 *@updateTime 2020/09/25 - 10:57
 *@see [jsDoc中文文档]{@link  http://www.dba.cn/book/jsdoc/JSDOCKuaiBiaoQianBLOCKTAGS/CONSTRUCTS.html}
 @description 全局事件模块
 */
export default {
    data() {
        return {commonActiveNodeInfo: {}};
    },
    created() {
        // 绑定全局事件
        this.bindGlobalEvent();
    },
    beforeDestroy() {
        // 取消全局事件
        this.unbindGlobalEvent();
    },
    methods: {
        // 设置当前选中节点信息
        setCommonActiveNodeInfo(event, data) {
            // debugger
            // 当前的选择节点
            this.commonActiveNodeInfo = data.treeItem;
            // 改变主路由参数
            this.changeMainRouter(data, event);
        },
        // 绑定全局事件
        bindGlobalEvent() {
            if (this.globalBus) {
                // 绑定设置当前选中节点信息
                this.globalBus.$on('global-common-activeNodeInfo', this.setCommonActiveNodeInfo);
                // 绑定处理下钻处理事件
                this.globalBus.$on('global-cxtj-main-operate', this.gOperateGoDown);
                // 绑定查询按钮和分页事件
                this.globalBus.$on('global-query-search', this.gSearchPaging);
                // 绑定下载事件
                this.globalBus.$on('global-xz-main-operate', this.gDownload);
                // 绑定导出事件
                this.globalBus.$on('global-export-main-operate', this.gExport);
            }
        },
        // 取消全局事件
        unbindGlobalEvent() {
            if (this.globalBus) {
                // 取消绑定设置当前选中节点信息
                this.globalBus.$off('global-common-activeNodeInfo', this.setCommonActiveNodeInfo);
                // 取消绑定处理下钻处理事件
                this.globalBus.$off('global-cxtj-main-operate', this.gOperateGoDown);
                //  取消绑定查询按钮和分页事件
                this.globalBus.$off('global-query-search', this.gSearchPaging);
                // 取消绑定下载事件
                this.globalBus.$off('global-xz-main-operate', this.gDownload);
                // 取消导出事件
                this.globalBus.$off('global-export-main-operate', this.gExport);
            }
        },
        // 触发全局事件
        emitGlobalEvent(callback) {
            if (this.globalBus) {
                callback();
            }
        }
    }
};



