/**
 * @version 20191110
 * @author wuwg
 * @createTime  20191110
 * @updateTime  20191110
 * @decription  树组件性能优化，获取当前视图需要展示的列表数据
 */

/**
 * @name   type
 * @time   2019-04-10
 * @param {object}   obj 要检测的对象
 * @description  判断数据类型类型判断
 * @returns {*} 返回boolean值
 */
const type = (obj) => {
    const toString = Object.prototype.toString;
    const map = {
        '[object Boolean]': 'boolean',
        '[object Number]': 'number',
        '[object String]': 'string',
        '[object Function]': 'function',
        '[object Array]': 'array',
        '[object Date]': 'date',
        '[object RegExp]': 'regExp',
        '[object Undefined]': 'undefined',
        '[object Null]': 'null',
        '[object Object]': 'object'
    };
    return map[toString.call(obj)];
};

class GetCurrentDataList {
    // 构造器

    /**
     * @param {object} options 默认的参数
     * @description   构造器
     * @returns {*} 无返回值
     */
    constructor(options) {
        // 分类的高
        const _categoryHeight = 30;
        // 可是区域的数据条目
        const _clientCount = 50;
        // 参数
        const _options = options || {};
        this.author = 'wuwg';
        this.time = '2019-04-01';
        // 是否有线
        this.hasLine = _options.hasLine ? _options.hasLine : false;
        // 数据条目的id
        this.id = _options.id ? _options.id : 'id';
        // 数据条目的子数据
        this.children = _options.children ? _options.children : 'children';
        // 数据条目的父id
        this.pid = _options.pid ? _options.pid : 'pid';
        // 当前的滚动条位置
        this.currentScrollTop = _options.currentScrollTop ? _options.currentScrollTop : 0;
        // 分类的高
        this.categoryHeight = _options.categoryHeight ? _options.categoryHeight : _categoryHeight;
        // 可视数据条数
        this.clientCount = _options.clientCount ? _options.clientCount : _clientCount;
        // 滚条应该【减去的数值】， 也就是当前第一个节点滚掉了部分！
        this.scrollTopReduce = 0;
        // 最终数据
        this.resultDataList = [];
        // 开始计算滚动条的索引值
        this.startIndex = _options.startIndex ? _options.startIndex : 0;
        // 预加载数量
        this.prepareCount = _options.prepareCount ? _options.prepareCount : 5; // eslint-disable-line
        // 预加载的实际数量
        this.prepareActCount = 0;
    }

    /**
     *
     * @param {object} options 需要修改的参数
     * @description   修改默认的参数
     * @returns {*} 无返回值
     */
    setOptions(options) {
        for (const _name in options) {
            // 分类的高|可视数据条数
            this[_name] = options[_name];
        }
    }

    /**
     * @param {object}   obj 要检测的对象
     * @description  深度拷贝对象
     * @returns {*} 返回拷贝后的对象
     */
    deepClone(obj) {
        const result = type(obj) === 'array' ? [] : {};
        for (const _key in obj) {
            const _copy = obj[_key];
            if (_key !== this.children) {
                if (type(_copy) === 'object') {
                    result[_key] = this.deepClone(_copy);
                } else if (type(_copy) === 'array') {
                    result[_key] = this.deepClone(_copy);
                } else {
                    result[_key] = obj[_key];
                }
            }
        }
        return result;
    }

    /**
     *
     * @param{ 'array'}  _dataList 当前的数据列表
     * @description 更新dataList数据列表
     * @returns {*} 无返回值
     */
    update(_dataList) {
        const _this = this;
        // 总的数据列表
        _this.dataList = _dataList;
    }

    /**
     *
     * @param{number}  currentScrollTop 当前滚动条的值
     * @description 设置当前滚动条的值
     * @returns {*} 无返回值
     */
    setCurrentScrollTop(currentScrollTop) {
        this.currentScrollTop = currentScrollTop;
    }

    /**
     *
     * @description 设置当前数据（resultDataList）
     * @returns {*} 无返回值
     */
    setCurrentDataList() {
        // 开始的索引值= （当前滚动条的top值 / 节点的高）， 向下取整数, 作为数据的开始位置
        let _startIndex = Math.floor(this.currentScrollTop / this.categoryHeight);
        // _startIndex = Math.max(0, _startIndex - 2);
        // 开始的索引值
        _startIndex = Math.max(0, _startIndex - this.startIndex);
        // 重新计算数量
        _startIndex = _startIndex - this.prepareCount;
        // 默认等于预加载的数量
        this.prepareActCount = this.prepareCount;
        // 数组下标最小为0， 所以小于0就得重新取值，并且实际预加载的数量会发生变更
        if (_startIndex < 0) {
            // 取出实际需要预加载的数量条目
            this.prepareActCount = this.prepareCount + _startIndex;
            // 索引清空
            _startIndex = 0;
        }
        // 结束的索引
        const _endIndex = _startIndex + this.clientCount;
        // 当前索引为开始索引
        let _currentIndex = _startIndex;
        // 获取当前数据列表
        const startDataList = this.dataList.slice(_startIndex);
        // 清空结果列表
        this.resultDataList = [];
        // 遍历将满足条件的数据，添加到 resultDataList 中
        startDataList.every((dataItem) => {
            // 如果当前索引超过结束索引，则跳出
            if (_currentIndex > _endIndex) {
                return false;
            }
            // 如果展现才设置
            if (dataItem.show) {
                //  克隆数据
                const _dataItem = this.deepClone(dataItem);
                // 此处不用转义
                this.resultDataList[this.resultDataList.length] = (_dataItem);
                _currentIndex++;
            }
            return true;
        });
        // 如果存在线,并且数组大于0
        if (this.hasLine) {
            const _startTime2 = window.fdGlobal.performance.getCurrentTime();
            // 设置数据的高度
            this.setResultDataListLineHeight();
            _getCurrentDataList.showLog && window.fdGlobal.performance.execute('【getCurrentDataList.setResultDataListLineHeight】 计算线的时间 :', _startTime2);
        }
    }

    // 设置数据的高度
    setResultDataListLineHeight() {
        // 循环数据计算每个数据条目需要展示得高度
        const _resultDataObject = {};
        // 缓存数据对象
        const _cacheDataObject = {};
        // 无数据的对象
        const _noDataLineObject = {
            value: [],
            level: {}
        };
        // 如果数组大于0
        if (this.resultDataList.length) {
            _noDataLineObject.value = [true, true, true, true, true, true];
        }
        // 【一】循环数据列表，获取该段数据的分类对象
        this.resultDataList.forEach((item, index) => {
            // 当前的索引值
            item.currentIndex = index;
            // 缓存数据
            _cacheDataObject[item[this.id]] = item;
            // 默认线得高度 ！important
            item.lineStyle = {
                height: '30px',
                marginTop: '0'
            };
            _noDataLineObject.value[item.level] = false;
            _noDataLineObject.level[item.level] = true;
            const _level = `level${item.level}`;
            // 如果不是数组，那么就优先创建一个数组
            if (!Array.isArray(_resultDataObject[_level])) {
                _resultDataObject[_level] = [[]];
            }
            // 如果是第一个节点
            if (item.first) {
                // 如果数组第一个下标下的数组长度为0，那么说明是初始化数组，直接覆盖即可，否则新建一个数组
                if (_resultDataObject[_level][0].length > 0) {
                    _resultDataObject[_level][_resultDataObject[_level].length] = [item];
                } else {
                    _resultDataObject[_level][0] = [item];
                }
            } else {
                const _target = _resultDataObject[_level][_resultDataObject[_level].length - 1];
                // 不是最后一个节点，那么在数组最后追加
                _target[_target.length] = (item);
            }
        });
        // 【二】处理各类数据的线样式
        this.operateLineStyle(_resultDataObject, _cacheDataObject);
        // 【三】设置无数据的线对象
        this.noDataLineObject = _noDataLineObject;
    }

    // 处理线样式
    operateLineStyle(resultDataObject, cacheDataObject) {
        const _len = this.resultDataList.length;
        // 计算第一条数据的lineHeight
        for (const _name in resultDataObject) {
            // 循环每个属性下的数组对象
            resultDataObject[_name].forEach((item) => {
                // 第一个数据条目
                const _firstItem = item[0];
                const _pid = _firstItem[this.pid];
                // 如果不是第一条数据
                if (!_firstItem.first) {
                    _firstItem.showLine = true;
                }
                // 【1】.第一种情况，每个数组中包含开始和结束点。
                if (item.length > 1) {
                    const _lastItem = item[item.length - 1];
                    let _reduceDistance = 0;
                    // 如果_lastItem是最后一个数据条目
                    if (_lastItem.last) {
                        _reduceDistance = _lastItem.currentIndex - _firstItem.currentIndex + 1;
                    } else {
                        _reduceDistance = _len - _firstItem.currentIndex;
                    }
                    // 1.1不存在父节点，
                    // 不存在父节点需要设置其margin-top值，因为上面还有内容，需要用线连接起来
                    // 其高度 = 距离顶部的高度+距离下面的高度 - 一个节点的行高
                    if (typeof cacheDataObject[_pid] === 'undefined') {
                        const _distance = (_firstItem.currentIndex + 1) * this.categoryHeight;
                        _firstItem.lineStyle.height = `${_reduceDistance * this.categoryHeight + _distance - this.categoryHeight}px`;
                        _firstItem.lineStyle.marginTop = `-${_distance - this.categoryHeight}px`;
                    } else {
                        // 1.2存在父节点
                        _firstItem.lineStyle.height = `${_reduceDistance * this.categoryHeight}px`;
                    }
                } else if (_firstItem.first && !_firstItem.last) {
                    // 【2】. 当只有一条数据，如果该条数据是【第一条数据】,但是非最后一条数据
                    _firstItem.lineStyle.height = `${(_len - _firstItem.currentIndex) * this.categoryHeight}px`;
                } else if (_firstItem.last) {
                    // 【3】. 当只有一条数据，如果该条数据是第【最后一条数据】
                    // 3.1不存在父节点
                    if (typeof cacheDataObject[_pid] === 'undefined') {
                        const _distance = (_firstItem.currentIndex + 1) * this.categoryHeight;
                        _firstItem.lineStyle.height = `${_distance}px`;
                        _firstItem.lineStyle.marginTop = `-${_distance - this.categoryHeight}px`;
                        // 3.2存在父节点
                    } else {
                        _firstItem.lineStyle.height = `${this.categoryHeight}px`;
                    }
                } else {
                    const _distance = (_firstItem.currentIndex + 1) * this.categoryHeight;
                    const _reduceDistance = _len - _firstItem.currentIndex;
                    // 【4】. 当只有一条数据，如果该条数据不是【最后一条数据】 也不是【第一条数据】
                    _firstItem.lineStyle.height = `${_reduceDistance * this.categoryHeight + _distance - this.categoryHeight}px`;
                    _firstItem.lineStyle.marginTop = `-${_distance - this.categoryHeight}px`;
                }
            });
        }
    }

    /**
     *
     * @description 设置 scrollTopReduce的值
     * @returns {*} 无返回值
     */
    setScrollTopReduce() {
        // 应该减去的top值
        this.scrollTopReduce = this.currentScrollTop % this.categoryHeight + this.prepareActCount * this.categoryHeight;
    }

    /**
     * @description 获取最终的结果值
     * @returns {{resultData: (*|Array), scrollTopReduce: (number|*)}}  返回一个对象，包含resultData和scrollTopReduce
     */
    getResultValue() {
        const _startTime1 = window.fdGlobal.performance.getCurrentTime();
        // 1. 设置当前数据
        this.setCurrentDataList();
        _getCurrentDataList.showLog && window.fdGlobal.performance.execute('【_getCurrentDataList.setCurrentDataList】 设置当前数据 :', _startTime1);
        const _startTime2 = window.fdGlobal.performance.getCurrentTime();
        // 2. 设置 scrollTopReduce的值
        this.setScrollTopReduce();
        _getCurrentDataList.showLog && window.fdGlobal.performance.execute('【_getCurrentDataList.setScrollTopReduce】 设置 scrollTopReduce的值 :', _startTime2);
        // 返回结果
        return {
            resultData: this.resultDataList,
            scrollTopReduce: this.scrollTopReduce,
            noDataLine: this.noDataLineObject
        };
    }
}

// 创建实例
const _getCurrentDataList = new GetCurrentDataList({
    // 分类的高
    categoryHeight: 30,
    // 数据id
    id: 'id',
    pid: 'pid',
    children: 'children',
    hasLine: true,
    // 可是区域的数据条目
    clientCount: 50
});

/**
 * @param { 'array'} dataList 当前的数据列表
 * @param {number} currentScrollTop 当前的scollTop值
 * @param {object} options 需要设置的默认参数
 * @returns {{resultData: Array, scrollTopReduce: number, firstDataItem: null}} 返回一个对象，包含当前滚动下，需要展示的数据，需要减去的滚动条值，和第一天
 */
export default function (dataList, currentScrollTop, options) {
    _getCurrentDataList.showLog = false;
    // 优先判断是否有options
    if (Object.prototype.toString.call(options) === '[object Object]') {
        // 设置其他的参数
        _getCurrentDataList.setOptions(options);
    }
    const _startTime = window.fdGlobal.performance.getCurrentTime();
    // 更新数据
    _getCurrentDataList.update(dataList);
    _getCurrentDataList.showLog && window.fdGlobal.performance.execute('【_getCurrentDataList.update】 更新数据 :', _startTime);
    const _startTime2 = window.fdGlobal.performance.getCurrentTime();
    // 当前的值
    _getCurrentDataList.setCurrentScrollTop(currentScrollTop);
    _getCurrentDataList.showLog && window.fdGlobal.performance.execute('【_getCurrentDataList.setCurrentScrollTop】 设置当前的值 :', _startTime2);
    const _startTime3 = window.fdGlobal.performance.getCurrentTime();
    // 获取当前的数据
    const _resultValue = _getCurrentDataList.getResultValue();
    _getCurrentDataList.showLog && window.fdGlobal.performance.execute('【_getCurrentDataList.getResultValue】 获取当前的数据 :', _startTime3);
    // 返回结果
    return _resultValue;
}



