<template>
    <div class="fd-empty-component" v-show="show">
        <slot><span>暂无数据！</span></slot>
    </div>
    <!--fd-empty-component end-->
</template>
<script>
//  导出模块
export default {
    props: {
        show: {
            type: Boolean,
            default: true
        }
    }
};
</script>
<style lang="less">
    .fd-empty-component {
        padding: 20px;
        width: 100%;
        height: 100%;
        font-size: 16px;
        color: #657180;
    }
</style>



