export default {

    methods: {
        // 处理全局事件
        gOperateGoDown(dataType, itemC, itemP, index) {
            switch (dataType) {
                case 'table':
                    this.changeGoDownRouter(dataType, itemC, itemP);
                    break;
                case 'form-table':
                    this.changeGoDownRouter(dataType, itemC, itemP, index);
                    break;
                default:
                    break;
            }
        },
        // 处理分页查询事件
        gSearchPaging(data) {
            const _this = this;
            // 合并参数
            const optionsData = {
                ...window.fdConfig.queryCriteria,
                ...data
            };
            // ajax 参数
            const ajaxOptions = {
                url: window.fdConfig.url.pagingSearch,
                data: optionsData,
                method: window.fdConfig.methodPost
            };
            // 请求数据
            this.requestGetData(ajaxOptions)
                .then(resultData => {
                    _this.globalBus.$emit('global-change-table-data', resultData);
                });
        },
        // 下载
        gDownload(arg) {
            const tempForm = document.createElement('form');
            tempForm.style.display = 'none';
            tempForm.method = 'POST';
            tempForm.action = window.fdConfig.url.download;
            // tempForm.action = 'http://172.16.193.53:8082/api/download';
            tempForm.target = '_self';
            document.body.appendChild(tempForm);
            for (const item in arg) {
                const hideInput = document.createElement('input');
                hideInput.type = 'hidden';
                hideInput.name = item;
                hideInput.value = arg[item];
                tempForm.appendChild(hideInput);
            }
            // document.querySelector('body').appendChild(tempForm);
            tempForm.submit();
            tempForm.remove();
        },
        // 导出
        gExport(arg) {
            const exportForm = document.createElement('form');
            exportForm.style.display = 'none';
            exportForm.method = 'POST';
            exportForm.action = window.fdConfig.url.exportzip;
            exportForm.target = '_self';
            document.body.appendChild(exportForm);
            for (const item in arg) {
                const hideInput = document.createElement('input');
                hideInput.type = 'hidden';
                hideInput.name = item;
                hideInput.value = arg[item];
                exportForm.appendChild(hideInput);
            }
            exportForm.submit();
            exportForm.remove();
        }

    }

};



