/**
 * @version 20190401
 * @author wuwg
 * @createTime  20190401
 * @updateTime  20190410
 * @decription  树组件性能优化，全局方法
 */
// 类型判断工具
import type from '../utils/tools/type.js';
import setScrollBarPosition from '../utils/tools/setScrollBarPosition.js';

export default {
    data() {
        return {
            // 当前激活的条目
            gActiveItem: {},
            // 当前获得焦点的条目
            gFocusItem: {}
        };
    },
    methods: {
        // 判断是否出现...
        gAdjustEllipsis(item) {
            return typeof this.gAdjustEllipsisHandler === 'function' ? this.gAdjustEllipsisHandler(item, this) : true;
        },
        // 设置当前的滚动条位置
        gSetScrollBar(top) {
            if (typeof top === 'number') {
                // 当前的滚动条滚动的距离
                this.$refs.jsScrollContain.scrollTop = top;
            }
        },
        // 重置当前获得焦点的数据条目
        gResetFocusItem() {
            this.gFocusItem = {};
        },
        // 设置当前获得焦点的条目
        gSetFocusItem(dataItem) {
            this.gFocusItem = dataItem;
            // 2.更新当前数据
            this.gUpdateCurrentDataList(dataItem);
        },
        // 需要调用滚动条
        gNeedUpdateScrollBar(flag) {
            this.updateScrollBarFlag = flag;
        },
        // 更新当前数据列表
        gUpdateCurrentDataList(dataItem) {
            // 1.获取目标元素
            let _targetEle = this.$refs[`js${dataItem[this.dKey]}`];
            const _this = this;
            // 【滚动条容器】对象
            const _jsScrollContain = this.treeScrollbar.$refs.scrollArea;
            // 1.1.当前数据中已经存在这条数据，那么我们需要判断它【是否在可视范围之内】
            if (_targetEle && _targetEle.length) {
                if (type(_targetEle) === 'array') {
                    _targetEle = _targetEle[0];
                }
                // 如果在可视范围内，设置滚动条位置，阻断程序，否则，继续向下执行
                if (_targetEle.clientHeight) {
                    // 设置滚动条位置(滚动条滚动会自带触发scroll事件，所以无需自动计算当前列表)
                    setScrollBarPosition(_targetEle, _jsScrollContain, _this);
                    return false;
                }
            }
            // else {
            // 1.2. 不在当前数据中，那么就需要更新当前数据，而且定位到这里
            // 1.2.1 设置该数据的父元素显示，以及同级元素显示
            _this.dSetCurrentNodeConnectNodeStatus(dataItem);
            // 1.2.2 重置所有数据中显示的数据列表
            _this.dResetResultShowDataList();
            let _targetIndex = -1;
            this.dResultShowDataList.every((item, index) => {
                if (dataItem[this.dKey] === item[this.dKey]) {
                    _targetIndex = index;
                    return false;
                }
                return true;
            });
            // 如果显示的数据，不需要出现滚动条，那么就需要设置当前展示的数据
            if (_jsScrollContain.clientHeight > this.dResultShowDataList.length * this.hItemHeight) {
                // 1.2.3 刷新当前视图
                _this.refreshCurrentView();
            } else {
                // 需要更新滚动条
                _this.gNeedUpdateScrollBar(true);
                // 等数据加载完，执行滚动条滚动
                this.$nextTick(() => {
                    if (_targetIndex !== -1) {
                        // 1.2.3 执行滚动条滚动
                        _jsScrollContain.scrollTop = _targetIndex * this.hItemHeight;
                        this.eUpdateView();
                    }
                });
            }
        },
        // 设置当前激活的条目的id
        gSetActiveId(id, callback, forceSetActiveId = true) {
            const _this = this;
            // 如果id不是字符串，则跳出
            if (typeof id !== 'string') {
                return;
            }
            // 当前激活id 和传入的id不等，才需要进行重新赋值
            if (this.gActiveItem[this.dKey] !== id || forceSetActiveId) {
                // 获取缓存数据条目
                const _dataItem = this.cGetCache(id);
                // 如果存在，那么就激活
                if (type(_dataItem) === 'object') {
                    // 1.设置当前的激活条目
                    this.gSetActiveItem(_dataItem, null, forceSetActiveId);
                    // 2.更新当前数据
                    this.gUpdateCurrentDataList(_dataItem);
                } else {
                    // 1.设置当前的激活条目
                    this.gSetActiveItem({}, null, forceSetActiveId);
                }
                // 3.执行回调函数
                if (type(callback) === 'function') {
                    // 需要更新滚动不同
                    if (this.updateScrollBarFlag) {
                        // 滚动后的函数
                        this.isScrollAfterCallback = () => {
                            callback(_this);
                        };
                    } else {
                        _this.$nextTick(() => {
                            callback(_this);
                        });
                    }
                }
            } else if (type(callback) === 'function') {
                callback(_this);
            }
        },
        // 获取当前激活的条目的id
        gGetActiveId() {
            return this.gActiveItem[this.dKey];
        },
        // 重置当前激活的数据条目
        gResetActiveItem() {
            this.gActiveItem = {};
        },

        /**
         *
         * @param {object}dataItem 当前需要激活的数据条目
         * @param {function}callback 设置后的回调函数
         * @param {boolean}forceTriggerSelect  是触发 select 事件
         * @param {boolean} isTurnPage 是否触发设置分册
         * @return {*} 无返回值
         * @description   设置当前激活的数据条目
         */
        gSetActiveItem(dataItem, callback, forceTriggerSelect, isTurnPage = true) {
            const _this = this;
            // 当前节点的条目
            let _currentItem = this.gActiveItem;
            // 当前激活节点如果和目标节点不一致
            if (this.gActiveItem[this.dKey] !== dataItem[this.dKey]) {
                // 当前激活节点赋值
                this.gActiveItem = dataItem;
                // 获取缓存数据
                _currentItem = this.cGetCache(dataItem[this.dKey]);
                // 如果设置当前页
                if (isTurnPage && _currentItem) {
                    // 设置当前的分册列表
                    this.caclSetcaclCurrentPagesList(_currentItem);
                }
            }
            // 默认触发select事件
            if ((type(forceTriggerSelect) === 'boolean' && forceTriggerSelect) || type(forceTriggerSelect) !== 'boolean') {
                // 触发事件
                this.emitEvent('select', null, _currentItem);
            }
            // 执行回调函数
            if (type(callback) === 'function') {
                _this.$nextTick(() => {
                    callback(_this);
                });
            }
        },
        // 获取当前激活的数据条目
        gGetActiveItem() {
            return this.gActiveItem;
        },
        // 根据id获取数据条目
        gGetItem(id) {
            return this.cGetCache(id) ? this.cGetCache(id) : null;
        },
        // 更新数据条目
        gUpdateItem(dataItem, keyArray) {
            // 获取真实数据
            const _targetItem = this.gGetItem(dataItem[this.dKey]);
            // 修改真实数据名称
            if (_targetItem) {
                // 更新缓存对象的数据
                this.cUpdateCache(dataItem, keyArray);
                // 更新当前数据列表中的某条数据
                this.currentDataList.every((item) => {
                    if (item[this.dKey] === dataItem[this.dKey]) {
                        const _keyArray = typeof keyArray === 'string' ? [keyArray] : keyArray;
                        // 循环需要变更的对象数据
                        _keyArray.forEach((itemC) => {
                            item[itemC] = dataItem[itemC];
                        });
                        return false;
                    }
                    return true;
                });
            }
        },
        // 修改目标数据条目名称
        gMendName(id, _name) {
            // 获取真实数据
            const _targetItem = this.gGetItem(id);
            // 修改真实数据名称
            if (_targetItem) {
                _targetItem[this.dName] = (_name);
                // 修改页面展现数据名称
                this.currentDataList.every((item) => {
                    if (item[this.dKey] === id) {
                        item[this.dName] = _name;
                        return false;
                    }
                    return true;
                });
            }
        },
        // 【1维数据】转换成【树数据】的方法
        gTransformToTreeFormat(dataList) {
            const newArray = [];
            const templateObj = {};
            // 1. 数据列表中的所有数据放到一个对象中
            dataList.forEach((item, index) => {
                // 并且把 数组中的数据id作为对象的的key
                // 本身的数据作为value
                templateObj[dataList[index][this.dKey]] = item;
            });

            /*
             * 1.数组进行循环
             * 2. 取对象中的parentKey 查看是否存在，存在说明存在子类
             *    比较该条数据的 key 和parentKey
             *    2.1 key= parentKey && templateObj.parentKey  同时成立的情况下，那么 说明该条数据是，此对象的子类
             *    2.2. 如果该对象存在 childKey那么  该直接 push() 这条数据到这个数组
             *    2.3. 如果不存在那么新建一个空数组 [],然后 push() 这条数据到数组
             *    2.4 如果2.1 不成立，那么说明是第一级，直接push到 新数组  newArray
             * 3. 需要注意的是，这里的数据是联动的，当父级增加一个子元素数组，然后把数据加进去，此时数组中就一个对象
             *     当数组中的内容发生变化的时候，这时候父级会跟着发生变化。
             *     原因是引用对象的地址是一样的。
             *     引用地址发生了变化，所有引用该对象的变量都会发生变化
             */
            dataList.forEach((item) => {
                const _pItem = templateObj[item[this.dpKey]];
                if (_pItem && item[this.dKey] !== item[this.dpKey]) {
                    // 如果父节点没有children key,那么久给其重新赋值
                    if (!Array.isArray(_pItem[this.dChildren])) {
                        _pItem[this.dChildren] = [];
                    }
                    _pItem[this.dChildren].push(item);
                } else {
                    newArray.push(item);
                }
            });
            return newArray;
        }
    }
};



