/**
 *@version 1.0.1
 *@author wuwg
 *@createTime 2020/03/08 - 10:57
 *@updateTime 2020/03/08 - 10:57
 *@see [jsDoc中文文档]{@link  http://www.dba.cn/book/jsdoc/JSDOCKuaiBiaoQianBLOCKTAGS/CONSTRUCTS.html}
 @description tab  组件
 @example
 tabList 数据结构
 ```
 [{
    key: 'all',
    count: 999,
    value: '全部'
}]
 ```
 // 数据双向绑定 tabList list
 <cc-tab v-model="activeKey"  :tabList="tabList"></cc-tab>
 // 绑定事件，接受当前选中的数据条目
 <cc-tab v-model="activeKey"  :tabList="tabList" @select="selectTab"></cc-tab>
 // 自定义tab 条目， 可以自定义tab里面的内容
 <cc-tab v-slot="props"><span v-text="props.item.value"></span></cc-tab>
 */
// 导出模块
export default {
    model: {
        // 当前激活的key
        prop: 'activeKey',
        event: 'changeTab'
    },
    props: {
        // 数据的lable字段
        dKey: {
            type: String,
            default: 'key'
        },
        // 数据值字段
        dName: {
            type: String,
            default: 'name'
        },
        // 判断是否展示总数
        dShow: {
            type: String,
            default: 'show'
        },
        // 总数
        dSize: {
            type: String,
            default: 'size'
        },
        // 当前激活的key
        activeKey: {type: [String, Number]},
        // 当前的数据列表
        tabList: {
            type: Array,
            default() {
                return [
                    {
                        key: 'all',
                        name: '全部'
                    },
                    {
                        key: 'ml',
                        name: '目录'
                    },
                    {
                        key: 'nr',
                        name: '内容'
                    },
                    {
                        key: 'pz',
                        name: '笔记'
                    }
                ];
            }
        }
    },
    watch: {
        // 监听key值
        activeKey: {
            handler(newValue) {
                // 当前的key
                if (this.currentKey !== newValue) {
                    // 设置激活的key
                    this.setActiveKey(newValue);
                }
            },
            // 直接执行
            immediate: true
        }
    },
    data() {
        return {
            // 当前的key
            currentKey: '',
            // 当前激活的条目
            activeItem: {}
        };
    },
    // 方法
    methods: {

        /**
         * @function
         * @memberof module:tab
         * @description 点击页签
         * @return {undefined} 无返回值
         */
        // 点击tab
        clickTabItem(item) {
            if (this.activeItem.key !== item.key) {
                // 设置当前的激活条目
                this.setActiveItem(item);
            }
        },
        // 设置激活的key
        setActiveKey(key) {
            // 激活的条目
            let _activeItem = null;
            // 循环tabList
            this.tabList.every((item) => {
                if (item.key === key) {
                    _activeItem = item;
                    return false;
                }
                return true;
            });
            // 设置当前激活的条目
            if (_activeItem) {
                this.setActiveItem(_activeItem);
            } else if (!this.currentKey && this.tabList.length) {
                // 默认第一个激活
                this.setActiveItem(this.tabList[0]);
            }
        },

        /**
         * @function
         * @memberof module:tab
         * @description  设置当前的激活条目
         * @param {object} item 激活条目
         * @return {undefined} 无返回值
         */
        setActiveItem(item) {
            // 当前激活的条目
            this.activeItem = item;
            // 设置当前选中的key
            this.setCurrentKey(this.activeItem.key);
            // 触发事件
            this.$emit('select', this.activeItem);
        },
        // 设置当前的key
        setCurrentKey(key) {
            // 当前的key
            this.currentKey = key;
            // 触发事件
            this.$emit('changeTab', this.currentKey);
        }
    }
};




