/**
 * @version 20200816
 * @author wuwg
 * @createTime  20200816
 * @updateTime  20200816
 * @decription  性能优化的缓存模块数据处理
 */
// 导出缓存模块
export default {
    methods: {
        // 初始化缓存
        dpcInitCache() {
            this.dpcCache = new Map();
        },
        // 重置缓存对象
        dpcResetCache() {
            this.dpcCache.clear();
        },
        // 设置缓存数据，（key:value）
        dpcSetCache(item) {
            this.dpcCache.set(item[this.dKey], item);
        },

        // 获取缓存数据，（key:value）
        dpcGetCache(key) {
            return this.dpcCache.get(key);
        },

        /**
         *
         * @param {object} dataItem 当前这条数据
         * @param { Array<String>|String} keyArray 该条数据要变更的字段
         * @description 更新缓存对象的数据
         * @returns {*} 无返回值
         */
        dpcUpdateCache(dataItem, keyArray) {
            // 目标缓存
            const _targetCahceItem = this.dpcGetCache(dataItem[this.dKey]);
            // 如果存在缓存数据
            if (_targetCahceItem) {
                const _keyArray = typeof keyArray === 'string' ? [keyArray] : keyArray;
                // 循环需要变更的对象数据
                _keyArray.forEach((item) => {
                    _targetCahceItem[item] = dataItem[item];
                });
                // 更新页面数据（只要是更新的性能缓存数据，那就一定需要更新页面数据）
                this.dpUpdateDataItem(_targetCahceItem, _targetCahceItem.dpIndex);
            }
        },

        // 获取所有缓存缓存数据，（key:value）
        dpcGetAllCache() {
            return this.dpcCache;
        },
        // 新增缓存
        dpcAddAllCache(currentList) {
            // 循环当前的数据
            currentList.forEach((item, index) => {
                // 添加缓存索索引
                item.dpIndex = index;
                // 添加缓存
                this.dpcSetCache(item);
            });
        }
    }
};



