/**
 * @author    niufj
 * @createTime    2019-11-1
 * @updataTime    2019-11-1
 * @description   【案件动态】 =》 【地图数据】
 */
// 模拟的数据
// import mockData from './mockData.js';
// 全局事件
import globalEvent from '../common/globalEvent.js';
// 模块
const module = {
    mixins: [globalEvent],
    props: {
        // 数据值字段
        dCategory: {
            type: String,
            default: 'category'
        },
        // 数据值字段
        dValue: {
            type: String,
            default: 'value'
        },
        // 数据对象
        dataObject: {
            type: Array,
            // 默认值
            default() {
                return [];
            }
        },
        isNavigation: {
            type: Boolean,
            default: false
        }
    },
    // watch: {
    //     // 监听参数变化，请求数据
    //     dataObject(newVal, oldVal) {
    //         if (newVal !== oldVal) {
    //             this.update(this.dataObject);
    //         }
    //     }
    // },

    data() {
        return {
            // 是否有序号
            hasOrder: true,
            // 当前的数据
            currentDataList: [],
            // 右侧楼梯导航的数据
            fixNavDataList: [],
            // 滚动容器
            connectId: '#jsMainInBox > .fd-scroll-area',
            // 固定导航对象
            fixNavObject: null,
            // 每页条数
            limit: window.fdConfig.limit,
            // 点击分页时的index下标
            clickIndex: 0,
            dataList: [],
            // 是否存在图片
            hasImg: false,
            // form-table下面table列表分页时
            xh: ''
        };
    },

    computed: {
        // // 监听参数变化，请求数据
        // hasFixNav() {
        //     // console.log('this.fixNavDataList', this.fixNavDataList);
        //     return this.fixNavDataList.length > 10; // eslint-disable-line
        // },
        // 总列数
        totalCol() {
            // console.log('this.fixNavDataList', this.fixNavDataList);
            const totalCol = this.isNavigation ? 4 : 6; // eslint-disable-line
            return this.hasImg ? totalCol - 2 : totalCol; // eslint-disable-line
        }
    },

    // 创建
    created() {
        // 绑定全局事件
        this.bindGlobalEvent();
        // 初始化的方法
        this.init();
    },
    beforeDestroy() {
        // 取消全局事件
        this.unbindGlobalEvent();
    },
    activated() {
        this.init();
    },
    methods: {

        /**
         * @description 初始化的方法
         * @name init
         * @return {*} 无
         */
        init() {
            // 更新数据列表
            this.update(this.dataObject);
        },
        readyFixNav(fixNav) {
            // 固定导航对象
            this.fixNavObject = fixNav;
        },
        // 当前的数据列表
        update(dataList, type = 'form-table-group') {
            this.dateSection = '';
            if (!(Array.isArray(dataList) && dataList.length && (dataList[0].category === 'form-table') && type === 'form-table-group')) {
                return false;
            }
            // 更新滚动条
            this.$emit('scrollUpdate');
            this.dataList = dataList;
            // 循环数据列表
            const _dataList = dataList.map((item) => {
                // console.log(item);
                // 添加显示属性
                item.show = true;
                // 条目下的子数据, !important, 这是约定字段不能修改！
                const _cDataList = item.data;
                // 是否是二维数组
                // 先判断数组的长度，再判断数组的第一个值是否是数组， 程序只支持二维
                item.isTwoDimensionArray = _cDataList.length > 0 ? Array.isArray(_cDataList[0]) : false;
                item.count = _cDataList.length;
                return item;
            });
            // 设置当前的数据
            this.setCurrentDataList(_dataList);
        },
        // 设置当前的数据
        setCurrentDataList(dataList) {
            const _this = this;
            // 为了清理组件残留数据
            this.currentDataList = [];
            // 为了让数据清空，并且判断出是否有导航
            this.$nextTick(() => {
                _this.currentDataList = dataList;
                // 判断是否有图片
                _this.currentDataList.every((item) => {
                    let _flag = true;
                    if (item.category === 'form-table') {
                        _flag = false;
                        let imgFlag = true;
                        item.data.every((itemC) => {
                            if (itemC.type === 'img') {
                                imgFlag = false;
                                //  有图片的话显示4列，无图片显示6列
                                // @fixme 可能存在问题，当前业务无影响
                                // 一个form-table有图片的话，其他的form-table都会受影响
                                _this.hasImg = true;
                            }
                            return imgFlag;
                        });
                    }
                    return _flag;
                });
                _this.$nextTick(() => {
                    // 设置楼梯导航的数据
                    _this.setFixNavDataList(dataList);
                });
            });
        },
        // 设置楼梯导航的数据
        setFixNavDataList(dataList) {
            const _this = this;
            _this.fixNavDataList = [];
            dataList.forEach((item, index) => {
                const obj = {
                    hide: false,
                    name: item.categoryName,
                    key: `form-table-${index}`,
                    moduleId: `#js-form-table-${index}`
                };
                _this.fixNavDataList.push(obj);
            });
            // 数据更新后，再掉刷新方法
            this.$nextTick(() => {
                if (this.fixNavObject && this.isNavigation) {
                    this.fixNavObject.refresh();
                }
            });
        },
        // 切换显示隐藏状态
        clickToggle(item) {
            item.show = !item.show;
            this.$forceUpdate();
        },
        //  操作按钮---form-table
        clickOperateFormTable(itemCC, item, index) {
            item.sourceType = 'form-table';
            // 触发事件
            this.emitGlobalEvent(() => {
                // 组件内部不做处理，全部交给外围处理
                this.globalBus.$emit('global-cxtj-main-operate', 'form-table', itemCC, item, index);
            });
        },
        //  操作按钮---table
        clickOperateTable(itemCC, item) {
            item.sourceType = 'table';
            // 触发事件
            this.emitGlobalEvent(() => {
                // 组件内部不做处理，全部交给外围处理
                this.globalBus.$emit('global-cxtj-main-operate', 'table', itemCC, item);
            });
        },
        // 更新table数据
        updateTableData(data) {
            this.total = data.total;
            this.xh = data.xh;
            // 子集是数组且有table列表展示
            if (this.xh === 0 && !(this.currentDataList[this.clickIndex].children)) {
                this.currentDataList[this.clickIndex].data = data.data;
                this.currentDataList[this.clickIndex].offset = data.offset;
            } else if (this.currentDataList[this.clickIndex].children[0] && this.xh === 0) {
                this.currentDataList[this.clickIndex].children[0].data = data.data;
                this.currentDataList[this.clickIndex].children[0].offset = data.offset;
            } else if (this.currentDataList[this.clickIndex].children[1] && this.xh === 1) {
                this.currentDataList[this.clickIndex].children[1].data = data.data;
                this.currentDataList[this.clickIndex].children[1].offset = data.offset;
            }
        },
        // 点击分页时触发的事件
        paginationChange(page, item, index) {
            this.offset = page.offset;
            this.limit = page.limit;
            this.clickIndex = index;
            this.componentId = item.componentId;
            this.mainId = item.mainId;
            const pageRequest = {
                offset: page.offset,
                limit: page.limit,
                componentId: this.componentId,
                mainId: this.mainId
            };
            this.globalBus.$emit('global-query-search', pageRequest);
        },
        // 绑定全局事件
        bindGlobalEvent() {
            if (this.globalBus) {
                // 绑定更新表格的事件
                this.globalBus.$on('global-change-table-data', this.updateTableData);
            }
        },
        // 取消全局事件
        unbindGlobalEvent() {
            if (this.globalBus) {
                // 取消绑定更新table表格的事件
                this.globalBus.$off('global-change-table-data', this.updateTableData);
            }
        }
    }
};
export default module;



