/**
 * @version 20191109
 * @author wuwg
 * @createTime  20191109
 * @updateTime  20191109
 * @decription  数据处理模块
 */
// 类型判断工具
import type from '../../utils/tools/type.js';
// 导出缓存模块
export default {
    data() {
        return {};
    },
    methods: {
        // 重置缓存对象
        cResetCache() {
            this.cache = new Map();
        },
        // 设置缓存数据，（key:value）
        cSetCache(item) {
            this.cache.set(item[this.dKey], item);
        },

        /**
         *
         * @param {object} dataItem 当前这条数据
         * @param { Array<String>|String} keyArray 该条数据要变更的字段
         * @description 更新缓存对象的数据
         * @returns {*} 无返回值
         */
        cUpdateCache(dataItem, keyArray) {
            // 目标缓存
            const _targetCahceItem = this.cGetCache(dataItem[this.dKey]);
            // 如果存在缓存数据
            if (_targetCahceItem) {
                const _keyArray = typeof keyArray === 'string' ? [keyArray] : keyArray;
                // 循环需要变更的对象数据
                _keyArray.forEach((key) => {
                    _targetCahceItem[key] = dataItem[key];
                    // 如果有复选框，复选框
                    if (this.multiple && key === 'checked') {
                        // 根据状态，调用处理函数
                        _targetCahceItem.checked ? this.dmAddCheckedData(_targetCahceItem) : this.dmDeleteCheckedData(_targetCahceItem[this.dKey]);
                    }
                });
            }
        },
        // 获取缓存数据，（key:value）
        cGetCache(key) {
            // return typeof key === 'string' ? this.cache[key] : null;
            return this.cache.get(key);
        },
        // 获取所有缓存缓存数据，（key:value）
        cGetAllCache() {
            return this.cache;
        },
        // 添加tree的全局缓存
        createWindowCacheObject() {
            // 全局树缓存对象
            if (type(window.globalTreeCacheObject) !== 'object') {
                window.globalTreeCacheObject = {};
            }
            // 创建缓存对象
            window.globalTreeCacheObject[this.id] = this;
        },
        // 销毁tree的全局缓存
        destroyWindowCacheObject() {
            if (type(window.globalTreeCacheObject[this.id]) === 'object') {
                delete window.globalTreeCacheObject[this.id];
            }
        }
    },
    beforeDestroy() {
        // 销毁缓存对象
        this.destroyWindowCacheObject();
    }
};



