/**
 * @author    niufj
 * @createTime    2019-11-1
 * @updataTime    2019-11-1
 * @description   【案件动态】 =》 【地图数据】
 */

import request from './request/request.js';
import globalEvent from '../table/single-table/extend/globalEvent';
// 模块
const module = {
    mixins: [request, globalEvent],
    props: {
        // 路由中的数据
        id: null,
        // 数据的lable字段
        dKey: {
            type: String,
            default: 'key'
        },
        // 数据的lable字段
        dName: {
            type: String,
            default: 'name'
        }
    },
    data() {
        return {
            fkType: 1,
            // 是否有序号
            hasOrder: true,
            // 头部的key，用户渲染数据
            tableTh: [{
                key: 'xh',
                name: '序号'
            }, {
                key: 'xcdwName',
                name: '协查单位'
            }, {
                key: 'cxxName',
                name: '查询项'
            }, {
                key: 'cxztName',
                name: '查询主体'
            }, {
                key: 'bcxdxmc',
                name: '被查询对象名称'
            }, {
                key: 'zjlxName',
                name: '证件类型'
            }, {
                key: 'zjh',
                name: '证件号'
            }, {
                key: 'sfyjg',
                name: '是否有结果'
            }, {
                key: 'fksj',
                name: '反馈时间'
            }],
            // ajax 配置
            ajaxOptions: {
                url: window.fdConfig.url.allScan + window.fdConfig.jbxxbhId,
                data: {id: window.fdConfig.jbxxbhId},
                method: window.fdConfig.methodPost
            },
            // 刷新页面后每一页有15条数据
            pagesize: 15,
            // 刷新页面后当前页为第一页
            currentPage: 1,
            result: [],
            total: 0,
            zt: '',
            ztList: [{
                code: '1',
                name: '是'
            }, {
                code: '2',
                name: '否'
            }]
        };
    },
    computed: {
        // tableData() {
        //     return this.result.slice(
        //         this.pagesize * (this.currentPage - 1),
        //         this.pagesize * this.currentPage
        //     );
        // }
    },
      // 创建
    created() {
        // 绑定全局事件
        this.bindGlobalEvent();
    },
    // 创建
    mounted() {
        this.init();
        // console.log('window.fdConfig.jbxxbhId',window.fdConfig.jbxxbhId);
    },
    methods: {
        handleSizeChange(val) {
            // 修改每一页的数据量
            this.pagesize = val;
            this.handleCurrentChange(1)
        },
        handleCurrentChange(val) {
            // 修改当前页
            this.currentPage = val;
            this.indexCrement = (val - 1) * this.pagesize + 1;

            this.ajaxOptions.data = {
                // 限制页数
                limit: this.pagesize,
                // 当前页数
                offset: (val-1)*this.pagesize,
                // id标识
                id: window.fdConfig.jbxxbhId,

            };
            this.globalBus.$emit('global-query-search-zl', this.fkType);
        },
        // 绑定全局事件
         bindGlobalEvent() {
            if (this.globalBus) {
                // 绑定更新表格的事件
                this.globalBus.$on('global-change-table-data', this.updateTableData);

                this.globalBus.$on('global-query-search-zl', this.fkClick);
            }
        },
        // 取消全局事件
        unbindGlobalEvent() {
            if (this.globalBus) {
                // 取消绑定更新table表格的事件
                this.globalBus.$off('global-change-table-data', this.updateTableData);

                this.globalBus.$on('global-query-seaarch-zl', this.fkClick);
            }
        },
         // 更新table数据
         updateTableData(data) {
            // 更新滚动条
            this.$emit('scrollUpdate');
            this.total = data.total;
            this.result[this.clickIndex].offset = data.offset;
            // 设置当前激活的数据
            this.result[this.clickIndex].data = data.data;
        },
        fkClick(val) {
            if(this.fkType !== val){
                this.currentPage = 1;
                this.ajaxOptions.data.offset=0;
            }
            this.zt = '';
            this.fkType = val;
            if (val === 1) {
                this.initYfk();
            } else {
                this.initWfk();
            }
        },
        /**
         * 初始化已反馈列表信息
         */
        initYfk: function () {
            const _this = this;
            _this.ajaxOptions.url = window.fdConfig.url.allScan + window.fdConfig.jbxxbhId + '?status=yfk&sfyjg=' + _this.zt;
            this.requestDataList(this.ajaxOptions)
                .then(data => {
                    _this.result = data.data;
                    _this.total = data.total;
                });
        },

        /**
         * 初始化未反馈列表信息
         */
        initWfk: function () {
            const _this = this;
            _this.ajaxOptions.url = window.fdConfig.url.allScan + window.fdConfig.jbxxbhId + '?status=wfk&sfyjg=';
            this.requestDataList(this.ajaxOptions)
                .then(data => {
                    _this.result = data.data;
                    _this.total = data.total;

                });
        },

        /**
         * @description 初始化的方法
         * @name init
         * @return {*} 无
         */
        init() {
            const _this = this;
            // 更新数据
            // this.update(mockData.tableData.data);
            // this.requestDataList(this.ajaxOptions)
            //     .then(data => {
            //         _this.update(data.tableData.data);
            //         _this.summary = data.summary;
            //     });
            _this.fkClick(1);
        },
        serachCxjg() {
            this.initYfk(1);
        }

    }
};
export default module;



