import setScrollBarPosition from './setScrollBarPosition.js';

/**
 * @namespace   atc   auto-component
 */
export default {
    props: {
        // 是否是本地搜索
        localSearch: {
            type: Boolean,
            default: true
        },
        // 防抖的时间间隔
        delay: {
            type: Number,
            default: 200
        }
    },
    data() {
        return {
            // 是否获得焦点
            focusing: false,
            // 激活条目的索引
            atcActiveIndex: -1,
            // 正在远程搜索
            remoting: false
        };
    },
    methods: {
        // 设置是否正在远程搜索
        setRemoting(flag) {
            this.remoting = flag;
        },
        // 设置是否获得焦点
        setFocusing(flag) {
            this.focusing = flag;
        },
        // 隐藏搜索列表
        hideSearchList() {
            // 1. 取消获得焦点
            this.setFocusing(false);
            // 2.隐藏【搜索列表】
            this.slSetVisible(false);
            // 3.input失去焦点
            this.$refs.jsInput.blur();
        },
        // 设置激活条目的索引index
        atcSetActiveIndex(index) {
            this.atcActiveIndex = index;
        },
        // 重置激活条目
        atcResetActiveItem() {
            // 设置激活条目的索引index
            this.atcSetActiveIndex(-1);
        },
        // 上箭头
        atcUp() {
            if (!this.slCurrentData.length) {
                return false;
            }
            // 索引减去 -
            this.atcSetActiveIndex(this.atcActiveIndex - 1);
            // 判断索引 -
            if (this.atcActiveIndex < 0) {
                // 小于0 设置为0
                this.atcSetActiveIndex(0);
            }
            // 滚动条动画
            this.atcScrollBarAnimate();
        },

        // 下箭头
        atcDown() {
            if (!this.slCurrentData.length) {
                return false;
            }
            // 索引减去 -
            this.atcSetActiveIndex(this.atcActiveIndex + 1);
            if (this.atcActiveIndex > this.slCurrentData.length - 1) {
                this.atcActiveIndex = this.slCurrentData.length - 1;
            }
            // 滚动条动画
            this.atcScrollBarAnimate();
        },
        // 滚动条动画
        atcScrollBarAnimate() {
            this.$nextTick(() => {
                // 激活的元素
                const _activeElement = this.$refs[`jsSearch${this.atcActiveIndex}`];
                // 如果有激活的元素
                if (_activeElement && Array.isArray(_activeElement)) {
                    setScrollBarPosition(_activeElement[0], this.$refs.jsSearchListContain);
                }
            });
        },
        //  键盘松开
        handleKeyup($event) {
            if (this.slVisible) {
                if (/38|40|13/igm.test($event.keyCode)) {
                    $event.preventDefault();
                }
            }
        },
        // 键盘按下
        handleKeydown($event) {
            //  上箭头38， 下箭头40  //  回车键13
            switch ($event.keyCode) {
                case 38: // eslint-disable-line
                    if (this.slVisible) {
                        // 上箭头38
                        this.atcUp();
                        $event.preventDefault();
                    }
                    break;
                case 40: // eslint-disable-line
                    if (this.slVisible) {
                        // 下箭头40
                        this.atcDown();
                        $event.preventDefault();
                    }
                    break;
                case 13: // eslint-disable-line
                    if (this.slVisible) {
                        // 回车事件处理函数
                        this.atcEnterHandle();
                    }
                    //  隐藏搜索列表
                    this.hideSearchList();
                    break;
                default :
                    break;
            }
        },
        // 获取焦点
        handleFocus() {
            // 设置是否获得焦点
            this.setFocusing(true);
            // 如果有值
            if (this.currentValue) {
                //  设置搜索列表显示状态 true , 显示
                this.slSetVisible(true);
            }
            // focus聚焦事件
            this.$emit('focus', this.currentValue);
        },
        // 值发生变动
        handleInput() {
            // 必须是输入发生的变动，也就是获得焦点
            if (this.currentValue) {
                // 如果获得焦点
                if (this.focusing) {
                    //  设置搜索列表显示状态 true , 显示
                    this.slSetVisible(true);
                }
                // 清除定时器
                if (this.inputTimer) {
                    clearTimeout(this.inputTimer);
                }
                // 函数防抖
                this.inputTimer = setTimeout(() => {
                    // 每次变动都重置数据
                    this.atcResetActiveItem();
                    // 如果是本地搜索
                    if (this.localSearch) {
                        // 处理当前数据
                        this.slOperateData(this.dataList);
                    } else {
                        // 远程搜索
                        this.remoteSearch(this.currentValue);
                    }
                }, this.delay);
            } else {
                // 设置搜索列表显示状态 false , 隐藏
                this.slSetVisible(false);
            }
            // 发送事件
            this.$emit('input', this.currentValue);
        },
        // 失去焦点
        handleBlur() {
            // 1. 隐藏搜索列表
            this.hideSearchList();
            // 2. 失去焦点默认按一下回车键
            this.atcEnterHandle();
            // 3.blur失焦事件.失去焦点
            this.$emit('blur', this.currentValue);
        },
        // 回车事件
        atcEnterHandle() {
            // 当前索引不为 -1， 说明有选中的条目 &&  并且当前有数据
            if (this.atcActiveIndex !== -1 && this.slCurrentData.length) {
                // 选中搜索列表的条目
                this.slSelectListItem(this.slCurrentData[this.atcActiveIndex]);
            } else {
                // 重置当前索引
                this.atcSetActiveIndex(-1);
            }
        }
    }
};



