export default {
    data() {
        return {
            // 是否显示空文件夹
            eToggleEmptyFolderFlag: true
        };
    },
    methods: {
        // 展开收起的回调函数
        eToggleEmptyFolderHandler(flag) {
            this.eToggleEmptyFolderFlag = flag;
            // 2.更新数据
            this.refresh();
        }

        /*     // 切换空目录
        eToggleShowEmptyFolder(){
            // 1.回调函数
           this[this.eToggleEmptyFolderFlag ? 'eToggleShowEmptyFolderHandler' : 'eToggleHideEmptyFolderHandler']() ;
        },
        // 显示空文件夹
        eToggleShowEmptyFolderHandler(){
            this.dEmptyFolderList.forEach((item, index) => {
                // 1.显示状态切换
                item.show = true;
                // 2.更新缓存
                this.cUpdateCache(item, 'show');
            })
        },
        // 隐藏空文件夹
        eToggleHideEmptyFolderHandler(){
            this.dEmptyFolderList.forEach((item, index) => {
                // 1.显示状态切换
                item.show = false;
                // 2.更新缓存
                this.cUpdateCache(item, 'show');
            })
        }*/
    }
};



