/**
 * @version 20190401
 * @author wuwg
 * @createTime  20201127
 * @updateTime  20201127
 * @decription  事件统一管理
 */
export default {
    methods: {
        // 触发事件事件函数
        emitEvent(eventName, event, targetNode, targetPNode, targetIndex) {
            switch (eventName) {
                // 用户开始拖动元素时触发 （在拖动目标上触发事件 (源元素):）
                case 'dragstart':

                    /**
                     * #event
                     *
                     * @name    dragstart
                     * @param   targetNode   目标节点
                     * @param   event       event对象
                     * @body    console.log("开始拖动元素");
                     * @description    用户开始拖动元素时触发
                     */
                    this.$emit('dragstart', event, targetNode, this);
                    break;
                // 元素正在拖动时触发 （在拖动目标上触发事件 (源元素):）
                case 'drag':

                    /**
                     * #event
                     *
                     * @name    drag
                     * @param   targetNode   目标节点
                     * @param   event       event对象
                     * @body    console.log("正在拖动");
                     * @description    元素正在拖动时触发
                     */
                    this.$emit('drag', event, targetNode, this);
                    break;
                // 用户完成元素拖动后触发 （在拖动目标上触发事件 (源元素):）
                case 'dragend':

                    /**
                     * #event
                     *
                     * @name    dragend
                     * @param   targetNode   目标节点
                     * @param   hoverNode   被鼠标拖动的对象划过时所在对象
                     * @param   dragNode    在拖动目标
                     * @param   targetPostion    位置
                     * @param   event       event对象
                     * @body    console.log("拖动结束！");
                     * @description    用户完成元素拖动后触发
                     */
                    this.$emit('dragend', event, targetNode, this.hoverNode, this.dragNode, this.targetPostion, this);
                    break;
                // 当被鼠标拖动的对象进入其容器范围内时触发此事件  (释放目标时触发的事件:)
                case 'dragenter':

                    /**
                     * #event
                     *
                     * @name    dragenter
                     * @param   targetNode   目标节点
                     * @param   dragNode    被拖动的元素
                     * @param   event       event对象
                     * @body    console.log("dragenter!");
                     * @description    当被鼠标拖动的对象进入其容器范围内时触发此事件
                     */
                    this.$emit('dragenter', event, targetNode, this.dragNode, this);
                    break;
                // 当某被拖动的对象在另一对象容器范围内拖动时触发此事件  (释放目标时触发的事件:)
                case 'dragover':

                    /**
                     * #event
                     *
                     * @name    dragover
                     * @param   targetNode   目标节点(被鼠标拖动的对象划过时所在对象)
                     * @param   dragNode    被拖动的元素
                     * @param   event       event对象
                     * @body    console.log("dragover")!
                     * @description    当某被拖动的对象在另一对象容器范围内拖动时触发此事件
                     */
                    this.$emit('dragover', event, targetNode, this.dragNode, this);
                    break;
                // 当被鼠标拖动的对象离开其容器范围内时触发此事件  (释放目标时触发的事件:)
                case 'dragleave':

                    /**
                     * #event
                     *
                     * @name    dragleave
                     * @param   targetNode   目标节点 (  被鼠标拖动的对象离开时所在对象)
                     * @param   dragNode    被拖动的元素
                     * @param   event       event对象
                     * @body    console.log("dragleave")!
                     * @description    当被鼠标拖动的对象离开其容器范围内时触发此事件
                     */
                    this.$emit('dragleave', event, targetNode, this.dragNode, this);
                    break;

                // 在一个拖动过程中，释放鼠标键时触发此事件  (释放目标时触发的事件:)
                case 'drop':

                    /**
                     * #event
                     *
                     * @name    drop
                     * @param   targetNode   目标节点 (   被鼠标拖动的对象放置时所在对象)
                     * @param   hoverNode    被鼠标拖动的对象放置时所在对象
                     * @param   dragNode    被拖动的元素
                     * @param   targetPostion    被拖动的元素放置的位置，取值有 middle（内部） ,top（上方）， bottom（下方）
                     * @param   event       event对象
                     * @body     console.log("drop")!
                     * @description    当被鼠标拖动的对象离开其容器范围内时触发此事件
                     */
                    this.$emit('drop', event, targetNode, this.hoverNode, this.dragNode, this.targetPostion, this);
                    break;
                // 右键菜单
                case 'contextmenu':

                    /**
                     * #event
                     *
                     * @name    contextmenu
                     * @param   targetNode   目标节点
                     * @param    event       event对象
                     * @body     console.log("contextmenu")!
                     * @description    右键树菜单
                     */
                    this.$emit('contextmenu', event, targetNode, this);
                    break;
                // 修改名称
                case 'mend-name':

                    /**
                     * #event
                     *
                     * @name    mend-name
                     * @param   targetNode    当前的树节点
                     * @param   event    event对象
                     * @body    console.log("mend-name")!
                     * @description    修改名称
                     */
                    this.$emit('mend-name', event, targetNode, this);
                    break;
                // 修改名称失败
                case 'mend-name-fail':

                    /**
                     * #event
                     *
                     * @name    mend-name-fail
                     * @param   targetNode    当前的树节点
                     * @param   event    event对象
                     * @body    console.log("mend-name-fail")!
                     * @description    修改名称失败
                     */
                    this.$emit('mend-name-fail', event, targetNode, this);
                    break;
                // 点击条目，触发点击事件，并且可能会触发select事件， 点击条目一定会触发 click事件
                case 'click':
                    // 触发事件
                    this.$emit('click', event, targetNode, this);
                    break;
                // 选中节点
                case 'select':
                    // 触发选中事件
                    this.$emit('select', event, targetNode, this);
                    break;
                // 点击扩展图标触发的事件
                case 'clickExtendIcon':
                    // 触发事件
                    this.$emit('clickExtendIcon', event, targetNode, targetPNode, targetIndex, this);
                    break;
                //  双击事件
                case 'dblclick':
                    // 触发事件
                    this.$emit('dblclick', event, targetNode, this);
                    break;
                // 放置失败事件
                case 'drop-fail':
                    // 触发事件
                    this.$emit('drop-fail', event, targetNode, this);
                    break;
                //  失去焦点
                case 'blur':
                    // 触发事件
                    this.$emit('blur', event, targetNode, this);
                    break;
                //  获得焦点
                case 'focus':
                    // 触发事件
                    this.$emit('focus', event, targetNode, this);
                    break;
                //  回车事件
                case 'enter':
                    // 触发事件
                    this.$emit('enter', event, targetNode, this);
                    break;
                // 组件dom加载完成
                case 'ready':
                    // 触发事件
                    this.$emit('ready', this);
                    break;
                //  数据更新完毕
                case 'update':
                    // 触发事件
                    this.$emit('update', this);
                    break;
                //  数据刷新完毕
                case 'refresh':
                    // 触发事件
                    this.$emit('refresh', this);
                    break;
                //  点击复选框的触发的事件
                case 'checked':
                    // 触发事件
                    this.$emit('checked', targetNode, this);
                    break;
                //  展开 ，触发事件 extend
                case 'extend':
                    // 触发事件
                    this.$emit('extend', targetNode, this);
                    break;
                //  收起 ，触发事件collapse
                case 'collapse':
                    // 触发事件
                    this.$emit('collapse', targetNode, this);
                    break;
                //  收起 ，触发事件collapse
                case 'scroll':
                    // 触发事件
                    this.$emit('scroll', this.sCurrentScrollTop, 0, this);
                    break;
                default :
                    break;
            }
        }
    }
};



