/**
 * #info
 *
 *@author   wuwg
 *@createTime   2020/08/12
 *@updateTime   2020/08/12
 *@description  auto-complete  component 自动完成组件
 */

// 指令
import clickoutside from './extend/clickoutside/index.js';
import request from './request/request.js';
import autocomplete from './extend/autocomplete.js';
import searchList from './extend/search-list.js';
// module
export default {
    // 混入
    mixins: [request, autocomplete, searchList],
    // 内置指令
    //  p ： private  私有
    directives: {pclickoutside: clickoutside},
    // 数据双向绑定
    model: {
        prop: 'value',
        event: 'input'
    },
    props: {

        // 搜索框中的值
        value: {
            type: [String, Number],
            default: ''
        },

        // 数据节点的名称
        dName: {
            type: String,
            default: 'text'
        },
        // 数据列表 特定的数据，用于静态查询
        dataList: {
            // 必传且是数组
            type: Array,
            // 多种类型 [String,Number]
            default: function () {
                return [];
            }
        },

        // 是否禁用
        disabled: {
            type: Boolean,
            default: false
        },


        // 是否有清除按钮
        clearable: {
            type: Boolean,
            default: true
        },

        // 占位文本
        placeholder: {
            type: String,
            default: '请输入需要查询的内容'
        },

        /*    是否将弹层放置于 body 内，
         *     在 Tabs、带有 fixed 的 Table 列内使用时，建议添加此属性，它将不受父级样式影响，从而达到更好的效果
         */
        transfer: {
            type: Boolean,
            default: false
        },

        // 远程搜索的方法
        remoteMethod: {
            // 注意这里这个要用的话必须是一个方法。
            type: String,
            default: 'get'
        },

        // 远程搜索的url
        remoteUrl: {
            type: String,
            default: './static/json/auto-complete/dataList.json'
        },

        // 远程搜索的额外参数
        remoteExtendsParams: {type: Object},

        // 远程请求的方法啊
        remoteRequestMethod: {type: Function}
    },
    data() {
        return {
            // 滚动条对象
            scrollBar: null,
            // 激活条目
            currentItem: {},
            // 当前的值
            currentValue: this.value
        };
    },
    computed: {},
    watch: {
        value: {
            handler(val) {
                // 设置当前的值
                this.setCurrentValue(val);
            },
            immediate: true
        }
    },
    mounted() {
        // ready事件
        this.$emit('ready', this);
    },
    methods: {
        // 设置当前的值
        setCurrentValue(value) {
            this.currentValue = value;
        },

        // 清空值
        clickClear: function () {
            // 重置当前选中得数据
            this.resetCurrentItem();
            // 分发事件
            this.emitEvent();
        },

        // 重置当前选中得数据
        resetCurrentItem() {
            // 清空当前的对象
            this.setCurrentItem({});
        },
        // 设置当前选中得数据
        setCurrentItem(item) {
            // 当前选中的条目
            this.currentItem = item;
            // 设置当前的值
            this.setCurrentValue(this.currentItem[this.dName]);
            // 触发事件
            this.emitEvent();
        },
        // 触发事件
        emitEvent() {
            // 触发select 事件
            this.$emit('select', this.currentItem, this);
        },

        //  远程搜素
        remoteSearch: function (query) {
            // 开始远程搜索
            this.setRemoting(true);
            // 先判断用户是否自己写了请求数据的方法
            if (typeof this.remoteRequestMethod === 'function') {
                this.remoteRequestMethod(query).then((dataList) => {
                    // 处理当前数据
                    this.slOperateData(dataList);
                })
                    .finally(() => {
                        // 结束远程搜索
                        this.setRemoting(false);
                    });
            } else {
                // 请求远程数据的方法
                this.requestRemoteData(query).then((dataList) => {
                    // 处理当前数据
                    this.slOperateData(dataList);
                })
                    .finally(() => {
                        // 结束远程搜索
                        this.setRemoting(false);
                    });
            }
        },
        // 请求远程数据的方法
        requestRemoteData(query) {
            return new Promise((resolve, reject) => {
                // 那么必须先指定url(远程搜索的url)
                if (this.remoteUrl) {
                    // 请求参数, 合并参数 （远程搜索的额外参数）
                    const _data = {};
                    // 合并其他参数
                    if (Object.prototype.toString.call(this.remoteExtendsParams) === '[object Object]') {
                        for (const _name in this.remoteExtendsParams) {
                            _data[_name] = this.remoteExtendsParams[_name];
                        }
                    }
                    // 请求参数
                    _data.query = query;
                    // ajax 参数
                    const _ajaxOptions = {
                        method: this.remoteMethod,
                        data: _data,
                        url: this.remoteUrl
                    };
                    // 发送请求
                    this.requestDataList(_ajaxOptions, '请求数据').then((dataList) => {
                        resolve(dataList);
                    }, (data) => {
                        reject(data);
                    });
                } else {
                    reject(new Error('必须指定远程所有地址：remoteUrl'));
                }
            });
        }
    }
};



