/**
 * @author    wangye
 * @createTime    2021-05-21
 * @updataTime     2021-05-21
 * @description   【多个table展示组件】
 */
// 模块
const module = {
    props: {
        // 数据的lable字段
        dTabKey: {
            type: String,
            default: 'key'
        },
        // 数据值字段
        dTabName: {
            type: String,
            default: 'name'
        },
        dTabIndex: {
            type: String,
            default: 'index'
        },
        dTabNumberShow: {
            type: String,
            default: 'show'
        },
        // 数据值字段
        dTabChildren: {
            type: String,
            default: 'children'
        },
        // 数据值字段
        dCategory: {
            type: String,
            default: 'category'
        },
        // 数据值字段
        dValue: {
            type: String,
            default: 'value'
        },
        // 数据对象
        dataObject: {
            type: Object,
            // 验证
            validator(dateItem) {
                // 必须包含 dataTitle 和 data
                return dateItem.hasOwnProperty('dataTitle') && dateItem.hasOwnProperty('data');
            },
            // 默认值
            default() {
                return {};
            }
        }
    },
    // 数据
    data() {
        return {
            // tab信息
            tabList: [],
            // 查询项
            filterData: [],
            // 日期选择
            dateSection: '',
            // 总条数
            total: 10,
            // 第几页
            offset: 0,
            // 当前第几页
            current: 1,
            // 每页条数
            limit: window.fdConfig.limit,
            // 是否有序号
            hasOrder: true,
            // table数据
            tableData: [],
            // 表头数据
            tableTh: [],
            // 缓存查询数据
            cacheSearchData: null,
            // 查询数据
            searchData: {}
        };
    },
    // 创建
    created() {
        // 绑定全局事件
        this.bindGlobalEvent();
        this.init(this.dataObject);
    },
    beforeDestroy() {
        // 取消全局事件
        this.unbindGlobalEvent();
    },
    mounted() {
        // 默认展示第一个
        this.updateTableHead(this.dataObject[0].tableData);
        this.updateTable(this.dataObject[0].tableData);
    },
    methods: {
        init(moreTable) {
            // 根据order值进行排序
            moreTable.sort((a, b) => parseInt(a.order) - parseInt(b.order));
            // 初始tab信息
            moreTable.forEach((item) => {
                this.tabList.push({
                    [this.dTabName]: item[this.dTabName],
                    [this.dTabKey]: item[this.dTabKey],
                    [this.dTabNumberShow]: item[this.dTabNumberShow],
                    [this.dTabIndex]: item[this.dTabIndex]
                });
            });
        },
        // 清空查询条件
        cleanSerarchData() {
            // 清空时间段
            this.dateSection = '';
            // 清空查询数据
            this.searchData = {};
            // 清空下拉框
            this.filterData.forEach((item) => {
                item.value = '';
            });
            // 清空缓存查询数据
            this.cacheSearchData = {};
        },
        updateTable(dataTable) {
            //  必须是table类型的数据
            if (!(Object.prototype.toString.call(dataTable, null) === '[object Object]' && dataTable.category === 'table')) {
                return false;
            }
            // 更新滚动条
            this.$emit('scrollUpdate');
            // 分页数据变量赋值
            this.limit = dataTable.limit;
            this.total = dataTable.total;
            this.offset = dataTable.offset;
            this.current = this.offset / this.limit + 1;
        },
        updateTableHead(dataTable) {
            // (String) 组件id， 在后端返回的数据中有！字段统一叫 componentId
            this.componentId = dataTable.componentId;
            // 表头数据
            this.tableTh = dataTable.dataTitle;
            // table数据
            this.tableData = dataTable.data;
            // 左上角搜索数据
            this.filterData = dataTable.filterData ? dataTable.filterData : [];
            // table表头名称
            this.categoryName = dataTable.categoryName;
        },
        //  点击页签
        selectTab(item) {
            if (item) {
                // 清空搜索数据
                this.cleanSerarchData();
                // 设置表头
                const table = this.dataObject.find(k => k.index === item.index);
                this.updateTableHead(table.tableData);
                this.updateTable(table.tableData);
            }
        },
        emitGlobalEvent(callback) {
            if (this.globalBus) {
                callback();
            }
        },
        // 重置
        clickReset() {
            this.cleanSerarchData();
            // 展示清空左上角搜索条件之后的数据
            this.clickSearch();
        },
        //  操作按钮
        clickOperate(itemCC, item) {
            // debugger
            const linkType = itemCC.linkType;
            switch (linkType) {
                case 'view':
                case 'subtable':
                    item.sourseType = 'table';
                    // 触发事件
                    this.emitGlobalEvent(() => {
                        // 组件内部不做处理，全部交给外围处理
                        this.globalBus.$emit('global-cxtj-main-operate', 'table', itemCC, item);
                    });
                    break;
                default:
                    break;
            }
        },
        // 查询按钮
        clickSearch() {
            this.offset = 0;
            this.current = this.offset / this.limit + 1;
            // 触发请求数据
            this.emitRequestData(true);
        },
        // 改变查询条件
        changeCondition(condition) {
            // 触发请求数据
            this.emitRequestData(false, condition);
        },
        // 分页接口
        pagingChange(page) {
            this.offset = page.offset;
            this.limit = page.limit;
            this.current = this.offset / this.limit + 1;
            // 触发请求数据
            this.emitRequestData(false);
        },
        // 触发请求数据
        // isSearch 代表 是否是点击的查询
        emitRequestData(isSearch = false, condition) {
            const _condition = condition || this.$refs.moreTable.getCondition();
            const searchData = this.getSearchData(isSearch);
            // 查询条件
            const _queryCondition = {
                // 获取查询数据
                searchData,
                // 表头的筛选条件
                ..._condition
            };
            // 最终的请求数据
            const queryData = {
                // 请求字段
                queryCondition: _queryCondition,
                // 限制页数
                limit: window.fdConfig.limit,
                // 当前页数
                offset: this.offset,
                // (String) 组件id， 在后端返回的数据中有！字段统一叫 componentId
                componentId: this.componentId
            };
            // 触发全局事件
            this.globalBus.$emit('global-query-search', queryData);
        },
        getSearchData(isSearch = true) {
            let searchData = this.searchData;
            if (isSearch || !this.cacheSearchData) {
                // 创建缓存对象
                this.cacheSearchData = {};
                this.filterData.forEach((item) => {
                    switch (item.filterType) {
                        case 'text':
                            searchData[item.key] = item.value || '';
                            // 设置缓存数据
                            this.cacheSearchData[item.key] = searchData[item.key];
                            break;
                        case 'select':
                            searchData[item.key] = item.value || '';
                            // 设置缓存数据
                            this.cacheSearchData[item.key] = searchData[item.key];
                            break;
                        case 'date':
                            // 开始时间
                            searchData[item.key] = this.dateSection;
                            // 设置缓存数据
                            this.cacheSearchData[item.key] = searchData[item.key];
                            break;
                        default:
                            break;
                    }
                });
            } else {
                // 用缓存的数据
                searchData = this.cacheSearchData;
            }

            return searchData;
        },
        updateTableData(searchResultData) {
            this.total = searchResultData.total;
            this.offset = searchResultData.data.offset;
            this.current = this.offset / this.limit + 1;
            this.tableData = searchResultData.data.data;
        },
        // 绑定全局事件
        bindGlobalEvent() {
            if (this.globalBus) {
                // 绑定更新表格的事件
                this.globalBus.$on('global-change-table-data', this.updateTableData);
            }
        },
        // 取消全局事件
        unbindGlobalEvent() {
            if (this.globalBus) {
                // 取消绑定更新table表格的事件
                this.globalBus.$off('global-change-table-data', this.updateTableData);
            }
        },
        // 左上角搜索条件label宽度自适应
        getLabelWidth(str) {
            return str.length ? (str.length + 1) * 17 : 120;
        }
    }
};
export default module;



