/**
 * @author    niufj
 * @createTime    2019-11-1
 * @updataTime    2019-11-1
 * @description   table 组件
 */
// 模块
const module = {
    name: '',
    props: {
        // 表示数据条目的类型
        dType: {
            type: String,
            default: 'type'
        },
        // 数据条目的key值
        dKey: {
            type: String,
            default: 'key'
        },
        // 数据条目的名称
        dName: {
            type: String,
            default: 'name'
        },
        // 表示数据条目的类型
        dId: {
            type: String,
            default: 'id'
        },
        // 表示数据条目类型为link时的名称
        dLinkName: {
            type: String,
            default: 'name'
        },
        // 展示的数据table的表头(用于页面渲染的数据)
        tableTh: {
            type: Array,
            default() {
                // 案件动态--案件审理---除人均案件提交量之外的所有模块表头数据
                return [
                    {
                        key: 'c_bh',
                        isLink: true,
                        name: '问题线索编号'
                    }, {
                        key: 'd_slrq',
                        isLink: false,
                        name: '收件日期'
                    }
                ];
            }
        },
        // 展示的数据table(用于页面渲染的数据)
        tableData: {
            type: Array,
            default() {
                return [
                    {
                        'c_bh': '572D7A5958696C80ECA3AFC9E4D45DBF',
                        'd_slrq': '2019-04-16'
                    },
                    {
                        'c_bh': '572D7A5958696C80ECA3AFC9E4D45DBF',
                        'd_slrq': '2019-04-17'
                    }
                ];
            }
        },
        // 是否有序号
        hasOrder: {
            type: Boolean,
            default: true
        },
        // 序号默认宽度
        orderWidth: {
            type: Number,
            default: 60
        },
        offset: {
            type: Number,
            default: 0
        },
        // 是否固定宽度
        fixColWidth: {
            type: Boolean,
            default: true
        },
        // 表头的字体大小
        fontSize: {
            type: Number,
            default: 16
        },
        // 表头的左右间距
        thPadding: {
            type: Number,
            default: 56
        }
    },
    data() {
        return {
            isActive: -1,
            tableWidth: '100%',
            colList: []
        };
    },
    computed: {
        // 最终的数据， 是加了随机id
        resultTableData() {
            return this.tableData.map((item) => {
                // 随机id
                item.randomId = this.getRandomId();
                return item;
            });
        }
    },
    watch: {
        // 监听表头发生变化
        tableTh() {
            // 重新初始化col列表
            this.initColList(this.tableTh);
        }
    },
    mounted() {
        // 初始化的方法， 因为这里需要找到 $el, 所以必须是 mounted 钩子函数中执行
        this.init();
    },
    // 方法
    methods: {
        // 滚动条加载完毕
        // readyScrollBar(scrollBar) {
        //     this.scrollBar = scrollBar;
        // },
        // 初始化的方法
        init() {
            // 初始化col列表
            this.initColList(this.tableTh);
        },
        // 初始化col列表
        initColList(dataList) {
            // 默认table的宽度为100%
            this.tableWidth = '100%';
            // 重新获取table宽和col列表
            let {
                tableWidth,
                maxIndex,
                colList
            } = this.getColList(dataList);
            // 如果是固定宽
            if (this.fixColWidth) {
                // 如果最大索引不等于-1，那么将这列设置为自动宽度
                if (maxIndex !== -1) {
                    colList[maxIndex].width = 'auto';
                }
                // 如果table宽度不为0,那么将table的宽度设置为计算后的宽度
                if (tableWidth !== 0) {
                    // 需要判断是否存在讯号
                    this.tableWidth = this.hasOrder ? tableWidth + this.orderWidth : tableWidth;
                }
                // 如果总宽度小于可视的宽度，那么就不需要给每列定宽
                if (this.tableWidth < this.$el.clientWidth) {
                    // 重新获取列表
                    colList = this.getColList(colList, false, this.tableWidth).colList;
                    this.tableWidth = '100%';
                } else {
                    this.tableWidth = `${this.tableWidth}px`;
                }
            }
            this.colList = colList;
        },
        // 获取随机id
        getRandomId() {
            return (Math.random()
                .toString()
                .substr(2, 12)); // eslint-disable-line
        },
        // 获取列的列表
        getColList(tableTh, setWidthFlag = true, tableWidth) {
            // table的宽度
            let _tableWidth = 0;
            // 最大值
            let _maxCount = 0;
            // 最大值索引
            let _maxIndex = -1;
            // 循环th列表，获得col列表
            const _colList = tableTh.map((item, index) => {
                // 1.处理后的数据条目
                const _item = {
                    name: item.name,
                    width: item.width,
                    // 随机id
                    randomId: this.getRandomId()
                };
                // 随机id
                item.randomId = this.getRandomId();
                // 如果需要设置宽
                if (setWidthFlag) {
                    // 2.判断激活的排序
                    if (item.value && item.filter && item.filterType === 'sort') {
                        this.isActive = index;
                    }

                    // 3.如果固定宽
                    if (this.fixColWidth) {
                        // 每列的宽度
                        const _itemWidth = item.width ? parseInt(item.width) : (item.name.length * this.fontSize + this.thPadding);
                        // 找到最大列宽
                        if (_itemWidth > _maxCount) {
                            _maxCount = _itemWidth;
                            _maxIndex = index;
                        }
                        // 重新设置每列的宽度
                        _item.width = `${_itemWidth}px`;

                        // 设置table的宽度
                        _tableWidth = _tableWidth + _itemWidth;
                    }
                    //  序号不参与重新计算， auto 宽度不参与计算
                } else if (!(parseInt(_item.width) === this.orderWidth || isNaN(parseInt(_item.width)))) {
                    _item.width = `${parseInt(_item.width) / tableWidth * 100}%`;
                }
                // 处理后的数据条目
                return _item;
            });
            // 返回table的宽度，最大值，最大值索引，col的列表
            return {
                tableWidth: _tableWidth,
                maxCount: _maxCount,
                maxIndex: _maxIndex,
                colList: _colList
            };
        },
        // 获取条件项
        getCondition() {
            const condition = {};
            this.tableTh.forEach((item) => {
                // 如果是过滤项
                if (item.filter) {
                    // 如果不是排序
                    if (item.filterType !== 'sort') {
                        // 赋值
                        condition[item.key] = item.value;
                        // 是排序，必须value中有值，才传给后端
                    } else if (item.value) {
                        // 赋值
                        condition[item.key] = item.value;
                    }
                }
            });
            return condition;
        },
        // 有操作的链接
        clickOperate(itemCC, item) {
            // debugger
            this.$emit('operate', itemCC, item);
        },
        // 点击表头筛选的元素时触发
        clickChange(item, value) {
            const _item = item;
            _item.value = value.code;
            // 触发事件
            this.$emit('changeSelect', _item, value);
            // 获取条件项
            const _condition = this.getCondition();
            // 改变条件
            this.$emit('changeCondition', _condition);
        },
        // 点击排序
        clickSort(item, index) {
            this.isActive = index;
            //  sortList = ['desc' ， 'asc'];,  // 排序的列表取值 desc降序，asc 升序
            const sortList = item.sortList;
            // item.value = 'desc'; // 默认的取值， 后端默认取值是降序  desc
            const _index = sortList.indexOf(item.value);
            // 重新取值
            item.value = (_index + 1) >= sortList.length ? sortList[0] : sortList[_index + 1];
            // 重置其他的排序条件
            this.resetOtherSortCondition(item, index);
            // 1.触发事件
            this.$emit('changeSort', item);
            // 获取条件项
            const _condition = this.getCondition();
            // 2.改变条件
            this.$emit('changeCondition', _condition);
        },
        // 重置其他的排序选项
        resetOtherSortCondition(targetItem, targetIndex) {
            this.tableTh.forEach((item, index) => {
                // 如果是非目标选选项的排序，那么就将其排序的值设置为空值
                if (targetIndex !== index && item.filter && item.filterType === 'sort') {
                    // 赋值
                    item.value = '';
                }
            });
        },
        // 触发更新
        emitUpdate() {
            // 初始化列列表
            this.initColList();
        }
    }
};
export default module;



