/**
 * @version 20190401
 * @author wuwg
 * @createTime  20190408
 * @updateTime  20190424
 * @decription  数据处理模块，处理原始数据
 */
import type from '../utils/tools/type.js';
// 深度克隆
import deepClone from '../utils/tools/deepClone.js';
// import emptyFolderToggleHandler from './eventHandler/emptyFolderToggleHandler.js';
// 多选数据
import dmOperateTreeData from './dmOperateTreeData.js';
// 性能优化数据
import dpOperateData from './performance/dpOperateData.js';

export default {
    mixins: [dmOperateTreeData, dpOperateData],
    data() {
        return {dResultShowDataListLength: 0};
    },
    methods: {
        // 初始化当前数据常规数据变量
        dInitVariable() {
            // 1. 当前的数据列表
            this.dSetDataList(this.dData, this.isUniqueTreeData);
            // 重置部分变量
            this.dResetVariableData();
        },
        // 重置分类
        dResetDivideCategory() {
            // 10. 如果存在分组
            if (this.divideCategory) {
                this.divideCategoryObject = Object.create(null);
            }
        },
        // 添加分类条目
        dAddDivideCategoryItem(dataItem) {
            //  需要分类才会进行添加
            if (this.divideCategory) {
                // 先判断是否是数组
                if (!Array.isArray(this.divideCategoryObject[dataItem[this.dCategory]])) {
                    this.divideCategoryObject[dataItem[this.dCategory]] = [];
                }
                // 添加数据
                this.divideCategoryObject[dataItem[this.dCategory]].push(dataItem);
            }
        },
        // 设置数据列表
        dSetDataList(dataList, isUniqueTreeData) {
            // 是唯一的树数据，那么直接赋值就行
            if (isUniqueTreeData) {
                this.dataList = dataList;
            } else {
                //  非唯一的树数据，那么需要进行递归转换
                this.dataList = deepClone(dataList);
            }
        },

        // 清空文件夹中的值
        dResetdFolderList() {
            this.dFolderList = [];
        },
        // 在所有空文件夹数据列表中添加数据
        dAddFolderList(dataItem) {
            // 是文件夹
            if (this.dIsFolderCallback(dataItem)) {
                this.dFolderList[this.dFolderList.length] = dataItem;
            }
        },

        // 清空空文件夹中的值
        dResetdEmptyFolderList() {
            this.dEmptyFolderList = [];
        },
        // 在所有空文件夹数据列表中添加数据
        dAddEmptyFolderList(dataItem) {
            if (this.dIsEmptyFolderCallback(dataItem)) {
                this.dEmptyFolderList[this.dEmptyFolderList.length] = dataItem;
            }
        },
        // 添加树节点
        dAddTreeNode() {
            // 添加数据
            this.dAddTreeNodeHandlerCallback(this.gGetActiveItem(), this).then((data) => {
                // 获取当前数据
                const {
                    newNode,
                    targetNode,
                    position,
                    isBefore
                } = data;
                // 1.插入节点
                this.dInsertTreeNode(newNode, targetNode, position, isBefore);
                // 2.进行一次数据转换
                this.dTransformTreeData();
                // 3.触发一次滚动事件，更新当前数据
                this.eUpdateView(() => {
                    // 4.执行回调函数
                    this.dAddTreeNodeAfterCallback(newNode, this);
                });
            });
        },

        /**
         * @description 目标节点的内部插入数据
         * @param {object} newNode 需要添加的数据条目
         * @param {object} targetNode 目标节点数据
         * @param {boolean} isBefore 是前插入还是后面插入
         * @returns {*} 无返回值
         */
        dAddDataInner(newNode, targetNode, isBefore) {
            // 改变父id
            newNode[this.dpKey] = targetNode[this.dKey];
            // 添加数据（往前添加）
            targetNode[this.dChildren][isBefore ? 'unshift' : 'push'](newNode);
            // 展开数据
            targetNode.extend = true;
        },

        /**
         * @description 向目标区域上面或者下面添加数据
         * @param {object} newNode 需要添加的数据条目
         * @param {object} targetNodeP 目标节点数据的父节点
         * @param {object} targetNode 目标节点数据
         * @param {string}position 放置的位置，top|bottom
         * @returns {*} 无返回值
         */
        dAddData(newNode, targetNodeP, targetNode, position) {
            let _dataList = this.dataList;
            // 如果存在父级
            if (targetNodeP) {
                _dataList = targetNodeP[this.dChildren];
                // 改变父id
                newNode[this.dpKey] = targetNodeP[this.dKey];
                // 展开数据
                targetNodeP.extend = true;
            } else {
                // 改变父id
                newNode[this.dpKey] = -1;
            }
            // 添加数据
            _dataList.every((item, index) => {
                // 查找drop 数据
                if (item[this.dKey] === targetNode[this.dKey]) {
                    // 判断放置在前，还是放置在后面
                    const _index = position === 'top' ? index : index + 1;
                    // 添加数据到目标位置
                    _dataList.splice(_index, 0, newNode);
                    return false;
                }
                return true;
            });
        },

        // 插入节点
        dInsertTreeNode(newNode, targetNode, position, isBefore) {
            const _newNode = newNode;
            // 放置地方的数据
            const _targetNode = this.cGetCache(targetNode[this.dKey]);
            const _position = position;
            const _targetNodeP = this.cGetCache(targetNode[this.dpKey]) || false;
            // 中间添加
            if (_position === 'middle') {
                // 向放置区域内部添加拖拽数据
                this.dAddDataInner(_newNode, _targetNode, isBefore);
            } else {
                // 向放置区域上面或者下面添加拖拽数据
                this.dAddData(_newNode, _targetNodeP, _targetNode, _position);
            }
        },

        // 删除树节点
        dRemoveTreeNode(key) {
            // 批量删除
            if (type(key) === 'array') {
                // 批量删除
                this.dRemoveBatchTreeNode(key);
            } else {
                // 单条删除
                this.dRemoveSingleTreeNode(key);
            }
        },
        // 批量删除
        dRemoveBatchTreeNode(keys) {
            // 当前激活的数据是否被删除
            let _isDeleted = false;
            // 1.循环所有的key，单个删除数据节点
            keys.forEach((itemKey) => {
                const _targetNode = this.cGetCache(itemKey);
                // 如果存在目标节点
                if (_targetNode) {
                    // 当前激活的节点
                    const _activeNode = this.gGetActiveItem();
                    // 当前激活的id
                    let _activeNodeId = '';
                    // 如果存在激活节点
                    if (_activeNode) {
                        _activeNodeId = _activeNode[this.dKey];
                    }
                    // 如果是删除的当前激活的节点，那么需要设置下一个激活的节点
                    if (_activeNodeId === _targetNode[this.dKey]) {
                        // 1.1 删除树节点，后执行回调函数
                        this.dDeleteTreeNode(_targetNode, (dataList, index, targetNode) => {
                            // 设置激活的节点
                            const _currentActiveNode = this.dGetCurrentActiveNode(dataList, index, targetNode);
                            // 如果存在激活条目
                            if (_currentActiveNode) {
                                _isDeleted = true;
                                // 设置激活条目,无回调函数，无激活
                                this.gSetActiveItem(_currentActiveNode, null, false);
                            }
                        });
                    } else {
                        // 1.2 单条删除
                        this.dDeleteTreeNode(_targetNode);
                    }
                }
            });
            // 2.进行一次数据转换
            this.dTransformTreeData();
            // 3.触发一次滚动事件，更新当前数据
            this.eUpdateView(() => {
                // 如果被删除
                if (_isDeleted) {
                    // 当前激活的节点
                    const _activeNode = this.gGetActiveItem();
                    // 如果存在id
                    if (_activeNode[this.dKey]) {
                        // 获取目标元素
                        const _targetNode = this.cGetCache(_activeNode[this.dKey]);
                        // 如果存在目标元素
                        if (_targetNode) {
                            // 触发事件
                            this.$emit('select', JSON.parse(JSON.stringify(_targetNode)), this);
                        } else {
                            // @todo 暂时不处理激活，这个需要按照规则来计算 2019-04-24
                        }
                    }
                }
            });
        },
        // 单条删除
        dRemoveSingleTreeNode(key) {
            // 当前激活的节点
            const _activeNode = this.gGetActiveItem();
            // 需要删除的目标节点
            const _targetNode = type(key) === 'string' ? this.cGetCache(key) : _activeNode;
            // 如果存在目标节点
            if (_targetNode) {
                // 删除树节点的处理函数
                this.dRemoveTreeNodeHandlerCallback(_targetNode, this).then(() => {
                    // 如果是删除的当前激活的节点，那么需要设置下一个激活的节点
                    if (_activeNode[this.dKey] === _targetNode[this.dKey]) {
                        // 1.删除树节点，后执行回调函数
                        this.dDeleteTreeNode(_targetNode, (dataList, index, targetNode) => {
                            // 设置激活的节点
                            const _currentActiveNode = this.dGetCurrentActiveNode(dataList, index, targetNode);
                            // 如果存在激活条目
                            if (_currentActiveNode) {
                                // 设置激活条目
                                this.gSetActiveItem(_currentActiveNode);
                            }
                        });
                    } else {
                        // 1.删除树节点
                        this.dDeleteTreeNode(_targetNode);
                    }
                    // 2.进行一次数据转换
                    this.dTransformTreeData();
                    // 3.触发一次滚动事件，更新当前数据
                    this.eUpdateView();
                });
            }
        },
        // 获取当前的激活节点
        dGetCurrentActiveNode(dataList, index, targetNode) {
            const _len = dataList.length;
            let _currentActiveNode = null;
            if (_len > 0) {
                if (index + 1 <= _len) {
                    _currentActiveNode = dataList[index];
                } else {
                    _currentActiveNode = dataList[index - 1];
                }
            } else {
                _currentActiveNode = this.cGetCache(targetNode[this.dpKey]) || false;
            }
            return _currentActiveNode;
        },

        // 删除树节点
        dDeleteTreeNode(targetNode, callback) {
            // 最外层的元素是没有父级的，所以这里需要进行判断
            let _dataList = this.dataList;
            let _index = -1;
            const _targetNodeP = this.cGetCache(targetNode[this.dpKey]) || false;
            // 判断是否存在父级
            if (_targetNodeP) {
                _dataList = _targetNodeP[this.dChildren];
            }
            // 删除数据
            _dataList.every((item, index) => {
                if (item[this.dKey] === targetNode[this.dKey]) {
                    // 删除目标位置数据
                    _dataList.splice(index, 1);
                    _index = index;
                    return false;
                }
                return true;
            });
            if (type(callback) === 'function') {
                // 执行回调函数
                callback(JSON.parse(JSON.stringify(_dataList)), _index, targetNode);
            }
        },
        // 设置当前节点与之相关的节点的状态
        dSetCurrentNodeConnectNodeStatus(dataItem) {
            // 设置成显示
            dataItem.show = true;
            // 更新缓存对象的数据
            this.cUpdateCache(dataItem, 'show');
            // 查看父元素
            let _parent = this.cGetCache(dataItem[this.dpKey]);
            // 所有祖先对象
            const _parentArray = [];
            // 父节点是一个对象
            while (type(_parent) === 'object') {
                // 添加到数组
                _parentArray.push(_parent);
                // 继续查找父节点
                _parent = this.cGetCache(_parent[this.dpKey]);
            }
            // 循环父亲元素，将节点设置成true
            _parentArray.forEach((item) => {
                // 设置成显示
                item.show = true;
                // 设置展开
                item.extend = true;
                // 更新缓存对象的数据
                this.cUpdateCache(item, ['show', 'extend']);
                // 循环子元素
                item[this.dChildren].forEach((itemC) => {
                    // 设置子元素全部显示
                    itemC.show = true;
                    // 更新缓存对象的数据
                    this.cUpdateCache(itemC, 'show');
                });
            });
        },
        // 在所有数据中显示的数据列表中添加数据
        dAddResultShowDataList(dataItem) {
            // 当前数据显示，
            if (dataItem.show) {
                // 添加函数
                this.dAddResultShowDataListFunc(dataItem);
            }
            // 设置数据的长度
            this.dResultShowDataListLength = this.dResultShowDataList.length;
        },
        // 添加函数
        dAddResultShowDataListFunc(dataItem) {
            // 父级
            const _parent = this.cGetCache(dataItem[this.dpKey]);
            // 并且存在父节点
            if (_parent) {
                // 需要判断父节点是否展开， 展开的情况下，该条数据才会显示
                if (_parent.extend) {
                    this.dResultShowDataList[this.dResultShowDataList.length] = dataItem;
                }
                // 不存在父节点说明在第一级直接添加进去
            } else {
                this.dResultShowDataList[this.dResultShowDataList.length] = dataItem;
            }
        },
        // 清除所有数据中显示的数据列表
        dClearResultShowDataList() {
            this.dResultShowDataList = [];
        },
        // 设置所有数据中显示的数据列表
        dSetResultShowDataList(dataList) {
            this.dResultShowDataList = dataList;
        },
        // 获取所有数据中显示的数据列表
        dGetResultShowDataList() {
            return JSON.parse(JSON.stringify(this.dResultShowDataList));
        },
        // 重置所有数据中显示的数据列表
        dResetResultShowDataList() {
            // 1.清除所有数据中显示的数据列表
            this.dClearResultShowDataList();
            // 2.循环数据与每条数据进行对比，然后得出最终数据
            this.dataList.forEach((item) => {
                // 单条数据递归，这里是简单递归
                this.dSingleRecursionSimple(item);
            });
        },

        /**
         *
         * @param {object} dataItem 数据条目
         * @description 单条数据递归，这里是简单递归
         * @returns {*} 无返回值
         */
        dSingleRecursionSimple(dataItem) {
            // 1.在所有数据中显示的数据列表中添加数据
            this.dAddResultShowDataList(dataItem);
            // 2.查找父级节点
            const _parent = this.cGetCache(dataItem[this.dpKey]);
            // 3.判断是否有子子集,并且当前元素是展开的
            if (dataItem.hasOwnProperty(this.dChildren)) {
                // 如果存在父节点
                if (_parent) {
                    // 父节点必须展开
                    if (_parent.extend) {
                        // 递归循环子集
                        this.dLoopChildrenSimple(dataItem);
                    }
                } else {
                    // 不存在父节点,直接循环子集
                    this.dLoopChildrenSimple(dataItem);
                }
            }
        },
        // 循环子集
        dLoopChildrenSimple(dataItem) {
            // 有子集的情况下，循环子集
            dataItem[this.dChildren].forEach((item) => {
                // 继续递归数据
                this.dSingleRecursionSimple(item);
            });
        },

        // 在所有数据列表中添加数据
        dAddResultDataList(dataItem) {
            // 不要用push,下标会快1倍
            this.dResultDataList[this.dResultDataList.length] = (dataItem);
        },
        // 清除所有数据列表中的数据
        dClearResultDataList() {
            this.dResultDataList = [];
        },

        // 当前层级++
        dCurrentLevelAdd() {
            this.dCurrentLevel++;
        },
        // 设置当前层级
        dSetCurrentLevel(currentLevel) {
            this.dCurrentLevel = currentLevel;
        },
        // 获取当前层级
        getCurrentLevel() {
            return this.dCurrentLevel;
        },
        // 重置当前层级
        resetCurrentLevel() {
            this.dCurrentLevel = 0;
        },
        // 重置一维数组
        dResetOneDimensionalList() {
            this.dOneDimensionalList = [];
        },
        // 重置缩略图列表
        resetPreviewList() {
            this.previewList = [];
        },

        // 重置部分变量
        dResetVariableData() {
            // 1.清空数据
            this.dClearResultDataList();
            // 2.清除所有数据中显示的数据列表
            this.dClearResultShowDataList();
            // 3. 重置文件夹列表数据
            this.dResetdFolderList();
            // 4. 空文件夹数据列表
            this.dResetdEmptyFolderList();
            // 5.重置缩略图列表
            this.resetPreviewList();
            // 6.重置一维数组
            this.dResetOneDimensionalList();
            // 7. 如果有复选框
            if (this.multiple) {
                // 初始化选中的数据
                this.dmInitCheckedData();
            }
            // 8.重置当前索引
            this.resetCurrentLevel();
            // 9.重置缓存
            this.cResetCache();
            // 10. 重置分类
            this.dResetDivideCategory();
            // 11.重置页码参数 【页码相关】
            this.calcResetPages();
        },
        // 转换树数据
        dTransformTreeData() {
            const _startTimePre = window.fdGlobal.performance.getCurrentTime();
            // 测试数据的条数
            this.dataI = 0;
            // 重置部分变量
            this.dResetVariableData();
            window.fdGlobal.performance.execute('【【树组件】=>【组装数据】递归数据【0开始】】消耗时间 :', _startTimePre);
            // 总数据长度
            const currentDataLen = this.dataList.length;
            const _startTime = window.fdGlobal.performance.getCurrentTime();
            // 循环数据与每条数据进行对比，然后得出最终数据
            this.dataList.forEach((item, index) => {
                // 重置当前索引
                this.resetCurrentLevel();
                // 单条数据递归
                this.dSingleRecursion(item, index, null, index, currentDataLen);
            });
            window.fdGlobal.performance.execute('【【树组件】=>【组装数据】递归数据【1结束】】消耗时间 :', _startTime);
            // 设置第一个Item
            this.setFirstItem(this.dataList);
            // 设置最后一个Item
            this.setLastItem(this.dataList);
            // // 如果有复选框，还激活复选框的事件
            // if (this.multiple) {
            //     this.$emit('checked', null, _this);
            // }
            window.fdGlobal.performance.execute('【【树组件】=>【组装数据】递归数据【2完成】】消耗时间 :', _startTime);
            window.fdGlobal.performance.execute('【【树组件】=>【组装数据】递归数据【3完成】】，完成数组组装后页面共消耗时间');
            window.fdGlobal.performance.execute(`【最【外层】数据条数】：【${this.dataList.length}】条`);
            window.fdGlobal.performance.execute(`【数据【总】条数】：【${this.dataI}】条`);
        },

        /**
         *
         * @param {object} dataItem 数据条目
         * @param {string} index 数据的索引值
         * @param {object} dataItemP 当前数据条目的父级
         * @param {number} currentIndex 当前数据条目的索引值
         * @param {number} currentDataLen 当前数据条目所在父元素得所有子元素个数用于判断，当前元素是否是最后一个元素
         * @description 单条递归数据
         * @returns {*} 无返回值
         */
        dSingleRecursion(dataItem, index, dataItemP, currentIndex, currentDataLen) {
            this.dataI++;
            // 1.获取当前数据的层级
            const _currentLevel = this.getCurrentLevel();
            // 2.添加属性
            this.dAddProperty(dataItem, _currentLevel, index, dataItemP, currentIndex, currentDataLen);
            // 3.缓存数据
            this.cSetCache(dataItem);
            // 4.在所有数据列表中添加数据
            this.dAddResultDataList(dataItem);
            // 在所有文件夹数据列表中添加数据
            // this.dAddFolderList(dataItem);
            // 5.在所有数据中显示的数据列表中添加数据
            this.dAddResultShowDataList(dataItem);
            // 6. 如果是目录(是否计算页码需要在前面【页码计算1】) 【页码相关】 @todo  不计算页码，可以提升性能15%  wuwg
            this.calcSingleRecursionDirectory(dataItem, dataItemP);
            // 7. 单条递归（【页码计算】） 【页码相关】    不计算页码，可以提升性能 15%  wuwg
            this.calcSingleRecursion(dataItem, dataItemP);
            // 8. 一维数组处理, 【基本不消耗性能】
            this.dOneDimensionalCallback(dataItem, this.dOneDimensionalList, this);
            // 9.判断是否有子子集
            const _children = dataItem[this.dChildren];
            // 当前层级++
            this.dCurrentLevelAdd();
            // // 当前数据长度
            const _currentDataLen = _children.length;
            // 进行递归循环,循环后代
            for (let i = 0; i < _currentDataLen; i++) {
                // 当前数据的索引值，包含父元素
                const _indexC = `${index}-${i}`;
                // 当前数据条目
                const itemC = _children[i];
                // 继续递归数据
                this.dSingleRecursion(itemC, _indexC, dataItem, i, _currentDataLen);
            }
            // 10.设置第一个Item
            this.setFirstItem(dataItem[this.dChildren]);
            // 11. 设置最后一个Item
            this.setLastItem(dataItem[this.dChildren]);
            // 生成缩略图列表函数, 此方法会增加成倍时间。  @todo  不组装缩略图数据，可以提升性能50% wuwg
            // 12. 将 dMakeListCallback 放在这里， 保证父元素在previewList后面
            this.dMakeListCallback(dataItem, this.previewList, this);
            // 如果是节点的最后一个,才进行计算父元素，大幅度提升性能 【页码相关】
            if (currentIndex === currentDataLen - 1) {
                // 改变目标父级数据的值
                this.calcChangeTargetParentPageInfo(dataItemP);
            }
            // 13. 设置当前索引（还原层级）
            this.dSetCurrentLevel(_currentLevel);
        },

        /**
         * 设置第一个一个信息项
         * @param {Array<Object>} dataList 数据列表
         * @returns {void}
         * */
        setFirstItem(dataList) {
            // 遍历第一个节点设置first为true
            dataList.every((dataItem) => {
                if (dataItem.show) {
                    dataItem.first = true;
                    return false;
                }
                return true;
            });
        },

        /**
         * 设置最后一个信息项
         * @param {Array<Object>} dataList 数据列表
         * @returns {void}
         * */
        setLastItem(dataList) {
            // 遍历最后一个节点设置last为true
            for (let i = dataList.length - 1; i >= 0; i--) {
                const dataItem = dataList[i];
                if (dataItem.show) {
                    dataItem.last = true;
                    break;
                }
            }
        },

        /**
         *
         * @param {object} dataItem 数据条目
         * @param {number} currentLevel 当前层级
         * @param {string} index 索引值，包含父元素
         * @param {object} dataItemP 数据条目的父节点
         * @param {number} currentIndex 当前数据条目得索引值，不包含父元素
         * @param {number} currentDataLen 当前数据条目所在父元素得所有子元素个数用于判断，当前元素是否是最后一个元素
         * @description  数据条目增加其他树需要的字段
         * @returns {*} 无返回值
         */
        dAddProperty(dataItem, currentLevel, index, dataItemP, currentIndex, currentDataLen) { // eslint-disable-line
            // 1. 必须加子元素 【!important】
            if (!Array.isArray(dataItem[this.dChildren])) {
                dataItem[this.dChildren] = [];
            }
            // 2.设置数据的显示状态
            dataItem.show = type(dataItem.show) === 'boolean' ? dataItem.show : true;
            //  3.设置数据的层级
            dataItem.level = currentLevel;
            // 4.自己的索引值（当前父数据下的索引）
            dataItem.selfIndex = currentIndex;
            // 5.数据的索引（包含父元素的以及祖先元素的索引）
            dataItem.index = index;
            //  6.是否是第一个节点 默认false
            dataItem.first = false;
            // 7.是否是最后一个节点 默认false
            dataItem.last = false;
            //  8.设置（索引id）,  如果存在父元素，才需要添加父元素的索引， 否则只添加自己的索引
            if (this.addIndexIdArray) {
                dataItem.indexIdArray = dataItemP ? `${dataItemP.indexIdArray}-${dataItem[this.dKey]}` : dataItem[this.dKey];
            }
            // 9. 设置数据的索引值对应的name 【这句话非常消耗性能】 4万数据，要多出20ms-50ms， 为了提升性能，将名称的获取，用函数处理
            if (this.addIndexName) {
                // 用户如果需要，那么可以在自定义属性中添加
                dataItem.indexName = dataItemP ? dataItemP.indexName.concat(dataItem[this.dName]) : [dataItem[this.dName]];
            }
            // 10.如果计算页码
            if (this.calcPage) {
                // 页码信息
                dataItem.pageInfo = type(dataItem.pageInfo) !== 'undefined' ? dataItem.pageInfo : '';
            }
            // 11.设置层级类名
            dataItem.classLevel = `level-${currentLevel}`;
            // 如果异步加载数据
            if (this.asyncNodeData) {
                // 12.是否正在异步加载子节点的数据
                dataItem.asyncNodeDataFlag = false;
            }
            // 13.如果有复选框
            if (this.multiple) {
                //  多选框（默认不选中）
                dataItem.checked = type(dataItem.checked) === 'boolean' ? dataItem.checked : false;
                // 如果有选中的
                if (dataItem.checked) {
                    // 初始化选中的数据
                    this.dmAddCheckedData(dataItem);
                }
            }
            // 14.是否开启禁用功能，默认不开启
            if (this.canDisabled) {
                dataItem.disabled = type(dataItem.disabled) === 'boolean' ? dataItem.disabled : false;
            }
            // 15.是否有叶子节点
            dataItem.leaf = type(dataItem.leaf) === 'boolean' ? dataItem.leaf : dataItem[this.dChildren].length > 0;
            // 16.展开属性
            dataItem.extend = type(dataItem.extend) === 'boolean' ? dataItem.extend : (currentLevel < this.showLevel);
            // 是否可以拖拽
            if (this.edDraggable) {
                //  17.（默认全部可以拖拽）
                dataItem.canDrag = type(dataItem.canDrag) === 'boolean' ? dataItem.canDrag : true;
                // 18. 是否是拖拽元素的子元素（正在拖拽元素是其祖先元素）
                dataItem.draggingancestors = false;
            }
            // 是否可以重命名
            if (this.canMendName) {
                // 19.正在重命名 （默认全部未在重命名）
                dataItem.renaming = type(dataItem.renaming) === 'boolean' ? dataItem.renaming : false;
                // 如果保留名称后置
                if (this.keepNameSuffix) {
                    const _pResult = (/(.*)(\..*)$/).exec(dataItem[this.dName]);
                    // 24.名称后缀
                    dataItem.nameSuffix = _pResult ? _pResult[2] : ''; // eslint-disable-line
                }
            }
            //  以下三个函数为用户准备的钩子函数
            // 20.添加其他属性
            this.dAddOtherPropertyCallback(dataItem, dataItemP, this);
            // 21.图标组
            dataItem.iconGroup = this.dAddIconGroupCallback(dataItem, this);
            // 22.单独的className
            dataItem.className = this.dAddClassCallback(dataItem, dataItemP, this);
            // 23.如果存在类型
            this.dAddDivideCategoryItem(dataItem);
        }
    }
};



