/**
 *@file index
 *@version 1.0.0
 *@author wuwg
 *@createTime 2020/12/08 - 11:33
 *@updateTime 2020/12/08 - 11:33
 *@see [jsDoc中文文档]{@link  http://www.dba.cn/book/jsdoc/JSDOCKuaiBiaoQianBLOCKTAGS/CONSTRUCTS.html}
 @description  v-tips  当元素出现..., 需要出现提示的指令
 */

import adjustTipsPosition from './adjustTipsPosition.js';
// 默认提示框的颜色
const TIPS_OPTIONS = {
    style: {
        default: {
            'position': 'fixed',
            'top': '0',
            'left': '0',
            'z-index': 92,
            'background-color': '#fff',
            'font': '12px/20px "Microsoft YaHei"',
            'color': '#333',
            'max-width': '400px',
            'padding': '5px 10px',
            'border-radius': '5px',
            'border': '0px  solid rgba(0,0,0,0.3)',
            'box-shadow': '1px 1px 5px rgba(0,0,0,0.3)',
            'word-break': 'break-all',
            'visibility': 'hidden'
        },
        info: {
            // #5bc0de
            'background-color': '#fff',
            // 'border-color' : '#46b8da',
            'border': '1px  solid #46b8da',
            // #fff
            'color': '#333'
        },
        warning: {
            // #f0ad4e
            'background-color': '#fff',
            // 'border-color' : '#eea236',
            'border': '1px  solid #eea236',
            // #fff
            'color': '#333'
        },
        success: {
            // #5cb85c
            'background-color': '#fff',
            // 'border-color' : '#4cae4c',
            'border': '1px  solid #4cae4c',
            // #fff
            'color': '#333'
        },
        error: {
            // #d9534f
            'background-color': '#fff',
            // 'border-color' : '#d43f3a',
            'border': '1px  solid #d43f3a',
            'color': '#333'
        }
    },
    //  偏移的距离
    offsetDistance: {
        // 提示框top偏移
        top: 5,
        // 提示框left偏移
        left: 5
    }
};
// 提示框容器
const TIPS = {
    // dom 元素
    DOM: null,
    // 创建 tips 容器函数
    createTips() {
        // 提示框 容器
        const _tipsDom = document.createElement('div');
        // 添加class
        _tipsDom.classList.add('fd-tips-contain');
        // 设置默认的样式
        _tipsDom.setAttribute('style', TIPS_OPTIONS.style.default);
        // 获取提示框的元素
        TIPS.DOM = _tipsDom;
        // 添加到页面
        document.querySelector('body')
            .appendChild(_tipsDom);
        // 返回TIPS.DOM对象
        return TIPS;
    },

    // 隐藏提示框
    hideTips() {
        // TIPS.DOM.style.display = 'none';
        TIPS.DOM.style.visibility = 'hidden';
        TIPS.DOM.style.zIndex = '98';
        // 返回TIPS.DOM对象
        return TIPS;
    },

    // 显示提示框
    showTips() {
        // TIPS.DOM.style.display = 'block';
        TIPS.DOM.style.visibility = 'visible';
        TIPS.DOM.style.zIndex = '98';
        // 返回TIPS.DOM对象
        return TIPS;
    },

    // 设置提示框的位置
    setTipsPostion(event) {
        // 占位元素的left和top值
        const _left = event.pageX;
        const _top = event.pageY;
        //  调整位置
        const {
            top,
            left,
            position
        } = adjustTipsPosition(_left, _top, TIPS.DOM);
            //  如果提示框出现在左侧
        const resultLeft = position[0] === 'left' ? left - TIPS_OPTIONS.offsetDistance.left : left + TIPS_OPTIONS.offsetDistance.left;
        //  如果提示框出现在右侧
        const resultTop = position[0] === 'top' ? top - TIPS_OPTIONS.offsetDistance.top : top + TIPS_OPTIONS.offsetDistance.top;
        TIPS.DOM.style.left = `${resultLeft}px`;
        TIPS.DOM.style.top = `${resultTop}px`;
        // 返回TIPS.DOM对象
        return TIPS;
    },
    // 设置值
    setValue(value) {
        // 3.1.设置目标的值
        TIPS.DOM.innerHTML = value;
        return TIPS;
    },
    // 设置class
    setClass(value) {
        // 3.1.设置目标的值
        TIPS.DOM.className = value.join(' ');
        return TIPS;
    },
    // 设置样式
    setStyle(style) {
        if (Object.prototype.toString.call(style, null) === '[object Object]') {
            // 3.2设置基础样式
            for (const _name in style) {
                // 设置样式
                TIPS.DOM.style[_name] = style[_name];
            }
        }
        return TIPS;
    }

}
;


// 注册
export default {
    // 只调用一次，指令第一次绑定到元素时调用。在这里可以进行一次性的初始化设置。
    bind(el, binding) {
        // 如果不存在提示框，那么需要创建提示框
        if (!TIPS.DOM) {
            // 1.创建提示框
            TIPS.createTips();
        }
        // 提示的类型
        //  default, info, warning, success,error
        const _category = binding.arg ? binding.arg : 'default';
        // 增加class
        const _className = ['fd-tips-contain'];
        for (const _name in binding.modifiers) {
            _className.push(_name);
        }
        // 2. 获取目标提示框的样式
        const _tipsStyle = TIPS_OPTIONS.style[_category];
        // 是否判断省略号
        const _ellipsis = binding.modifiers.ellipsis || false;

        /**
         * 3. 移动的处理函数
         * @param {Event} event event对象
         * @returns {void} 无返回值
         */
        function documentHandler(event) {
            // 如果判断省略号， 那先进行判断，需要配合 v-line-clamp指令一起使用， 在父元素的data上增加  ellipsis=true 属性
            let _show = true;
            // 如果需要判断出现...， 那么就需要判断【自身】或者【父元素】在dataset中是否有 ellipsis=true
            if (_ellipsis) {
                // 查找父元素节点，查看  ellipsis 属性
                _show = el.dataset.ellipsis === 'true' || el.parentNode.dataset.ellipsis === 'true';
            }
            // 如果存在值，那么才走下面的赋值方法
            if (binding.value && _show) {
                // 值可能是函数，也可能是直接的值
                const _value = typeof binding.value === 'function' ? binding.value(event) : binding.value;
                // 3.1设置值
                TIPS.setValue(_value)
                    // 3.2 设置class
                    .setClass(_className)
                    // 3.3设置样式
                    .setStyle(_tipsStyle)
                    // 3.4  设置提示框的位置
                    .setTipsPostion(event)
                    // 3.5  设置显示
                    .showTips();
            }
            //  如果包含直接返回
            if (el.contains(event.target)) {
                return false;
            }
            // 如果存在划出的处理函数，那么取消
            if (typeof el.onmouseleave === 'function') {
                el.onmouseleave();
            }
        }

        //  移动入
        el.onmouseenter = function () {
            // 将documentHandler函数，赋值给el 对象
            el.__vueMouseMove = documentHandler;
            // 绑定事件
            document.addEventListener('mousemove', documentHandler);
        };
        //  移动出
        el.onmouseleave = function () {
            // 隐藏提示框
            TIPS.hideTips();
            // 解除事件绑定
            document.removeEventListener('mousemove', el.__vueMouseMove);
            // 删除属性
            delete el.__vueMouseMove;
        };
        el.onmousewheel = function () {
            el.onmouseleave();
        };
    },
    // 被绑定元素插入父节点时调用 (仅保证父节点存在，但不一定已被插入文档中)。
    inserted() {
        // 被绑定元素插入父节点时调用
    },
    // ：所在组件的 VNode 更新时调用，但是可能发生在其子 VNode 更新之前。指令的值可能发生了改变，也可能没有
    // 。但是你可以通过比较更新前后的值来忽略不必要的模板更新 (详细的钩子函数参数见下)。
    update() {
        // 所在组件的 VNode 更新时调用
    },
    // 指令所在组件的 VNode 及其子 VNode 全部更新后调用。
    componentUpdated() {
        // 指令所在组件的
    },
    // 只调用一次，指令与元素解绑时调用。
    unbind(el) {
        // 接触事件绑定
        document.removeEventListener('mousemove', el.__vueMouseMove);
        // 删除属性
        delete el.__vueMouseMove;
        //  鼠标移入
        el.onmouseenter = null;
        //  鼠标移出
        el.onmouseleave = null;
    }
};



