/**
 * @version 20190401
 * @author wuwg
 * @createTime  20190401
 * @updateTime  20190408
 * @updateTime  20200815  代码优化，功能添加
 * @decription  树组件性能优化，入口文件
 */
// 类型判断工具
// import scrollbar from '../scrollbar/index.vue';
// 类型判断工具
import type from './utils/tools/type.js';
// 性能库
import performance from './utils/tools/performance.js';
// 空文件夹判断
// import isEmptyDirectory from './js/yw/isEmptyDirectory.js';
// 添加属性
// import yAddProperty from './js/yAddProperty.js';
// 业务属性方法
// import ywIndex from './js/yw/index.js';
// 生成材料列表
// import makeMaterialList from './js/yw/makeMaterialList.js';
// 生成一维列表
import makeOneDimensionalList from './js/makeOneDimensionalList.js';

/**
 *      方法集合
 **********************/
// 数据
// import treeList from './mock/treeList.js';
// 处理数据模块
import dOperateTreeData from './js/dOperateTreeData.js';
// 缓存数据模块
import cache from './js/performance/cache.js';
// 全局方法模块
import globalMethods from './js/globalMethods.js';
// 计算
import calc from './js/cacl/calc.js';
// 事件触发
import emitEvent from './js/emitEvent.js';


/**
 *      对象扩展
 **********************/
// 计算整体高度
import computedHeight from './js/performance/computedHeight.js';
// 样式表
import style from './js/style.js';
// 绑定事件
import bindEvent from './js/bindEvent.js';

// 组件
export default {
    name: 'vcc-tree',
    // components
    // components: {scrollbar},
    mixins: [
        dOperateTreeData,
        globalMethods,
        cache,
        computedHeight,
        style,
        bindEvent,
        calc,
        emitEvent
        // ywIndex
    ],
    props: {

        // 该树的id
        id: {
            type: String,
            default: function () {
                return `jsTreeId${(Math.random() * Math.pow(10, 6)).toFixed(0)}`; // eslint-disable-line
            }
        },
        // 该树的中文名称
        name: {
            type: String,
            default: '树组件'
        },

        // 数据节点的中的key的父key
        dpKey: {
            type: String,
            default: 'pid'
        },

        // 数据节点的中的key
        dKey: {
            type: String,
            default: 'id'
        },
        // 数据节点的中的name
        dName: {
            type: String,
            default: 'name'
        },
        // 数据节点的中的子集
        dChildren: {
            type: String,
            default: 'children'
        },
        // 自定义名称
        customName: {type: Function},

        // 类型名称
        dCategory: {
            type: String,
            default: 'category'
        },
        // 是否分类
        divideCategory: {
            type: Boolean,
            default: false
        },

        /**
         * #property
         *
         * @name    dData
         * @cn      树的数据
         * @value   []
         * @description 树的数据，为一个数组
         */
        dData: {
            type: Array,
            default() {
                // return treeList;
                return [];
            }
        },

        /**
         * #property
         *
         * @name    hasLine
         * @cn      是否有线
         * @group   控制
         * @type    boolean
         * @value   false
         * @description 是否有线,默认为true，有线
         */
        hasLine: {
            type: Boolean,
            default: true
        },
        // 每个节点的高度
        itemHeight: {
            type: Number,
            default: 30
        },
        // 可视的节点数量， 注意可视的节点数量越多，性能越慢 !important
        clientCount: {
            type: Number,
            default: 30
        },
        // 预加载数量，（可视的节点的前面一部分节点数），特别注意预加载数量，会和可视的节点数量一起计算，
        // 所以可视的节点数量一定要预留它的位置
        prepareCount: {
            type: Number,
            default: 10
        },

        /**
         * #property
         *
         * @name    hasIcon
         * @cn      是否有图标
         * @group   控制
         * @type    boolean
         * @value   false
         * @description 是否有图标,默认为false，无图标
         */
        hasIcon: {
            type: Boolean,
            default: false
        },

        // 长按时长
        longTapTime: {
            type: Number,
            default: 300
        },
        // 是否是复选框
        multiple: {
            type: Boolean,
            default: false
        },
        // 关联选中
        connectChecked: {
            type: Boolean,
            default: true
        },
        // 复选框多选时是否关联父级选中
        connectParent: {
            type: Boolean,
            default: true
        },
        // 复选框多选时是否关联子级选中
        connectChildren: {
            type: Boolean,
            default: true
        },

        /**
         * #property
         *
         * @name    canMendName
         * @cn      是否可以重命名
         * @type    boolean
         * @value   false
         * @description 是否可以重命名,默认为false
         */
        canMendName: {
            type: Boolean,
            default: false
        },
        // 重命名时，是否保留名称后缀
        keepNameSuffix: {
            type: Boolean,
            default: false
        },

        // 是否可以禁用，默认不可以
        canDisabled: {
            type: Boolean,
            default: false
        },

        // 是否可以重新命名，返回一个boolean
        edCanMendNameCallback: {
            type: Function,
            default() {
                return true;
            }
        },

        /**
         * #property
         *
         * @name    edPreviewId
         * @cn      拖拽缩略图
         * @description 拖拽缩略图,默认为#jsTreePreview
         */
        edPreviewId: {
            type: String,
            default: '#jsTreeDragContent'
        },

        /**
         * #property
         *
         * @name    edDraggable
         * @cn      是否可以拖拽
         * @type    boolean
         * @value   false
         * @description 是否可以拖拽,默认为false
         */
        edDraggable: {
            type: Boolean,
            default: false
        },

        // 该节点是否可以拖拽，返回一个boolean
        edCanDragCallback: {
            type: Function,
            default() {
                return true;
            }
        },
        // 目标区域是否可以放下，返回一个boolean
        edCanDropCallback: {
            type: Function,
            default() {
                return true;
            }
        },
        // 可以放置的位置
        edCanDropPosition: {
            type: Function,
            default(dropItem, dragItem, event, vm) { // eslint-disable-line
                return ['top', 'middle', 'bottom'];
            }
        },
        // 放置拖拽视图的内容
        edSetDragPreviewHtmlCallback: {
            type: Function,
            default(dragItem, vm) {
                // 非材料
                if (dragItem.type !== 'material') {
                    return [
                        `<div class="text  icon ${dragItem.type}">`,
                        dragItem[vm.dName] ? `${dragItem[vm.dName]}` : '',
                        '</div>'
                    ].join('');
                }
                // 缩略图
                return [
                    `<div class="preview ${dragItem.type}">`,
                    '<img src="./static/img/cailiao-3.jpg">',
                    '</div>'
                ].join('');
            }
        },
        // 目标区域放置的处理函数（会与后端交互），返回一个promise，成功才会放置。
        edDropHandlerCallback: {
            type: Function,

            /**
             * @param {object}dragItem 拖拽的条目
             * @param {object}dropItem 放置的条目
             * @param {string}dropPostion 放置的位置
             * @param {object} vm  该组件对象
             * @returns {Promise} 返回一个promise对象
             */
            default(dragItem, dropItem, dropPostion, vm) { // eslint-disable-line
                return new Promise((resolve, reject) => {
                    if (this.edDraggable) {
                        resolve();
                    } else {
                        reject(new Error('失败'));
                    }
                });
            }
        },
        // 重命名规则判断
        eRenamePatternCallback: {
            type: Function,

            /**
             * @param {object}dataItem 当前的数据条目
             * @param {object} vm  该组件对象
             * @returns {boolean} 返回一个boolean
             */
            default(dataItem, vm) { // eslint-disable-line
                let _value = dataItem[this.dName];
                // 去除空格
                _value = _value.replace(/(^\s+)|(\s+$)/igm, '');
                if (_value === '') {
                    return false;
                }
                // return /^[A-Za-z0-9-(-)-.-_\uff08-\uff09-\u4e00-\u9fa5-\u3001]+$/.test(value)  正则匹配
                return !(/[\/\\:\*\?<>"\|]/img).test(_value); // eslint-disable-line
            }
        },
        // 重命名的处理函数（会与后端交互），返回一个promise，成功才会重命名
        eRenameHandlerCallback: {
            type: Function,

            /**
             * @param {object}dataItem 当前的数据条目
             * @param {object} vm  该组件对象
             * @returns {Promise} 返回一个promise对象
             */
            default(dataItem, vm) { // eslint-disable-line
                return new Promise((resolve, reject) => {
                    if (this.canMendName) {
                        resolve();
                    } else {
                        reject(new Error('失败'));
                    }
                });
            }
        },
        // 添加树节点的处理函数（会与后端交互），返回一个promise，成功才会添加节点
        dAddTreeNodeHandlerCallback: {
            type: Function,

            /**
             * @param {object}dataItem 当前的数据条目
             * @param {object} vm  该组件对象
             * @returns {Promise} 返回一个promise对象
             */
            default(dataItem, vm) {
                return new Promise((resolve) => {
                    const _count = 2;
                    const _newNode = JSON.parse(JSON.stringify(dataItem));
                    const _position = ['top', 'middle', 'bottom'][Math.round(Math.random() * _count)];
                    const _data = {};
                    // id,随机生成新的id
                    _newNode[vm.dKey] = `${_newNode[vm.dKey]}new${Math.round(Math.random() * Math.pow(10, 6))}`;
                    // 父id
                    _newNode[vm.dpKey] = dataItem[vm.dpKey];
                    // 名称
                    _newNode[vm.dName] = '新节点';
                    // 清空子集
                    _newNode[vm.dChildren] = [];
                    // 内部插入
                    if (_position === 'middle') {
                        _newNode[vm.dpKey] = dataItem[vm.dKey];
                    }
                    // 设置数据
                    _data.targetNode = dataItem;
                    _data.newNode = _newNode;
                    _data.position = _position;
                    _data.isBefore = true;
                    // 成功的处理函数
                    resolve(_data);
                    // reject()
                });
            }
        },
        // 删除树节点的处理函数，（会与后端交互），返回一个promise，成功才会移除节点
        dRemoveTreeNodeHandlerCallback: {
            type: Function,

            /**
             * @param {object}dataItem 当前的数据条目
             * @param {object} vm  该组件对象
             * @returns {Promise} 返回一个promise对象
             */
            default(dataItem, vm) { // eslint-disable-line
                return new Promise((resolve) => {
                    // 成功的处理函数
                    resolve();
                });
            }
        },
        // 添加树节点后的处理函数
        dAddTreeNodeAfterCallback: {
            type: Function,

            /**
             * @param {object}dataItem 当前的数据条目
             * @param {object} vm  该组件对象
             * @returns {Promise} 返回一个promise对象
             */
            default(dataItem, vm) {
                // 当前的数据列表
                vm.currentDataList.every((item) => {
                    if (item[vm.dKey] === dataItem[vm.dKey]) {
                        // 正在重命名
                        item.renaming = true;
                        vm.$nextTick(() => {
                            // 获得焦点
                            vm.$refs[`jsInput${dataItem[vm.dKey]}`][0].focus();
                            // 选中文本
                            vm.$refs[`jsInput${dataItem[vm.dKey]}`][0].select();
                            // 触发事件
                            vm.$emit('focus', dataItem);
                        });
                        // 获得焦点
                        return false;
                    }
                    return true;
                });
            }
        },
        // 添加其他节点属性
        dAddOtherPropertyCallback: {
            type: Function,

            /**
             * @param {object}dataItem 当前的数据条目
             * @param {object}dataItemP 当前的数据条目的父亲条目
             * @param {object} vm  该组件对象
             * @returns {Promise} 返回一个promise对象
             */
            default(dataItem, dataItemP, vm) { // eslint-disable-line
                // 如果存在详情字段，说明是卷宗
                if (dataItem && dataItem.details) {
                    // 添加其他属性
                    // @todo  2019-11-10  wuwg 业务代码自己去写
                    // yAddProperty.addOtherProperty(dataItem, vm);
                }
            }
        },
        // 添加独有的组图标函数，返回一个数组
        dAddIconGroupCallback: {
            type: Function,
            default(dataItem, vm) { // eslint-disable-line
                // 如果存在详情字段，说明是卷宗
                // if (dataItem && dataItem.details) {
                //     // return  yAddProperty.setIcon(dataItem);
                //     return [];
                // }
                return [];
            }
        },
        // 添加独有的class
        dAddClassCallback: {
            type: Function,
            default(dataItem, dataItemP, vm) { // eslint-disable-line
                // 如果存在详情字段，说明是卷宗
                // if (dataItem && dataItem.details) {
                //     return yAddProperty.setClass(dataItem, dataItemP);
                // }
                return '';
            }
        },
        // 生成缩略图列表回调函数
        dMakeListCallback: {
            type: Function,
            default(dataItem, previewList) { // eslint-disable-line
                // previewList[]
                // if (dataItem.details) {
                //     makeMaterialList(dataItem, previewList);
                // }
            }
        },
        // 是空文件夹回调函数
        dIsEmptyFolderCallback: {
            type: Function,
            default(dataItem, vm) { // eslint-disable-line
                // previewList[]
                // if (dataItem.type === 'directory') {
                //     // 判断是否是空文件夹
                //     return isEmptyDirectory(dataItem, vm);
                // }
                return false;
            }
        },

        // 是文件夾
        dIsFolderCallback: {
            type: Function,
            default(dataItem) {
                // previewList[]
                return dataItem.type === 'directory';
            }
        },


        // 一维数处理
        dOneDimensionalCallback: {
            type: Function,
            default(dataItem, oneDimensionalList, vm) { // eslint-disable-line
                // 生成一维数据
                makeOneDimensionalList(dataItem, oneDimensionalList, vm);
            }
        },

        // 是否添加indexName属性
        // 添加数据的索引值对应的name 【这句话非常消耗性能】 4万数据，要多出20ms-50ms， 为了提升性能，将名称的获取，用函数处理
        addIndexName: {
            type: Boolean,
            default: false
        },
        // 是否添加indexIdArray属性（索引id）， 包含父元素的以及祖先元素的id用 - 分割
        addIndexIdArray: {
            type: Boolean,
            default: false
        },

        // 滚动条开始计算数据的索引值 默认索引为0
        // 在第一个节点容易被覆盖时，该值可以多增加几条，然后将目标区域的数据往下撑开
        startIndex: {
            type: Number,
            default: 0
        },

        // 默认展示多少级
        showLevel: {
            type: Number,
            default: 1
        },

        // 图标宽
        iconWidth: {
            type: Number,
            default: 20
        },

        // 复选框款
        checkboxWidth: {
            type: Number,
            default: 24
        },

        // 滚动条是否是响应式
        responseType: {
            type: Boolean,
            default: false
        },

        //  滚动条节流时间，毫秒
        eScrollDelay: {
            type: Number,
            default: 1000
        },
        // 因为对象是引用类型，如果多棵树用同一套树数据源的话， 彼此都会产生影响，
        // 因此 treeDataList 必须是唯一的，非唯一就得转换成唯一的树数据，
        isUniqueTreeData: {
            type: Boolean,
            default: true
        },
        // 因为对象是引用类型，如果多棵树用同一套树数据源的话， 彼此都会产生影响，
        // 因此 treeDataList 必须是唯一的，非唯一就得转换成唯一的树数据，

        // 是否在点击节点的时候展开或者收缩节点， 默认值为 true
        expandOnClickNode: {
            type: Boolean,
            default: true
        },

        // 是否在点击节点的时候选中节点，默认值为 false
        checkOnClickNode: {
            type: Boolean,
            default: false
        },

        // 异步加载数据
        asyncNodeData: {
            type: Boolean,
            default: false
        },

        // 异步加载节点数据的回调函数，（会与后端交互），返回一个promise，成功才会展开节点
        asyncNodeDataHandlerCallback: {
            type: Function,

            /**
             * @param {object}dataItem 当前的数据条目
             * @param {object} vm  该组件对象
             * @returns {Promise} 返回一个promise对象,promise 返回数据类表
             */
            default(dataItem, vm) { // eslint-disable-line
                return new Promise((resolve) => {
                    const _dataList = [];
                    resolve(_dataList);
                });
            }
        },

        // ...出现省略号的处理函数
        gAdjustEllipsisHandler: {
            type: Function,
            default(dataItem, vm) { // eslint-disable-line
                return dataItem.name.length > 5; // eslint-disable-line
            }
        }

    },
    computed: {
        // 每个条目的样式表
        getStyle() {
            // 图标宽
            const _iconWidth = this.iconWidth;
            const _checkbox = this.checkboxWidth;
            let _marginLeft = this.multiple ? _checkbox : 0;
            _marginLeft = this.hasIcon ? _marginLeft + _iconWidth : _marginLeft;
            return {'margin-left': `${_marginLeft}px`};
        }
    },
    data() {
        return {
            // 当前的数据列表
            currentDataList: [],
            // 高亮列表
            lightList: null,

            /*  lightList: {
             "B05236DCD6DE479DAE4C2033D9E051E6":true
             }*/
            // 判断是否存在这个层级的节点
            noLevel1: false,
            noLevel2: false,
            noLevel3: false,
            noLevel4: false,
            noLevel5: false,
            treeScrollbar: null
        };
    },
    beforeCreate() {
        // 创建performance对象
        if (!window.fdGlobal) {
            window.fdGlobal = {performance: performance};
        }
        if (!window.fdGlobal.performance) {
            window.fdGlobal.performance = performance;
        }
    },
    // 创建对象
    created() {
        // 初始化的方法
        this.init();
    },
    //  dom 插入后
    mounted() {
        // console.log(this.dData)
        // 树插入到页面后触发
        this.emitEvent('ready');
        // mounted,默认加载一遍数据
        this.eScroll();
    },
    methods: {
        // 初始化的方法
        init() {
            // 1.初始化当前数据常规数据变量
            this.dInitVariable();
            // 2. 初始化性能缓存数据
            this.dpInitVariable();
            // 3. 处理数据,转换成一维数组
            this.dTransformTreeData();
            // 4. 创建缓存对象
            this.createWindowCacheObject();
            // 5. 绑定resize事件
            this.eBindResize();
        },

        /**
         * 滚动条组件加载完成函数
         * @param {object} treeScrollbar 滚动条对象
         * @returns {undefined}  无返回值
         */
        readyScrollbar(treeScrollbar) {
            this.treeScrollbar = treeScrollbar;
        },
        // 更新滚动条
        updateScrollbar() {
            this.$nextTick(() => {
                // 更新滚动条
                this.treeScrollbar.refresh(false);
            });
        },
        // 处理高亮状态
        operateLightStatus(data) {
            if (this.lightList) {
                data.forEach((item) => {
                    // 设置高亮状态
                    item.light = this.lightList[item[this.dKey]] === true;
                });
            }
        },
        // 获取高亮列表
        getLightList() {
            return this.lightList;
        },
        // 设置高亮列表
        setLightList(dataList) {
            if (Array.isArray(dataList)) {
                this.lightList = {};
                dataList.forEach((item, index) => { // eslint-disable-line
                    // 高亮
                    this.lightList[item[this.dKey]] = true;
                });
            }
        },
        // 清除高亮列表
        clearLightList() {
            this.lightList = null;
        },


        /**
         * @param {'array'} dataList 当前的数据列表为数组
         * @description 设置当前的数据【currentDataList】
         * @returns {*} 无返回值
         */
        setCurrentDataList(dataList) {
            if (Array.isArray(dataList)) {
                // 处理高亮状态
                this.operateLightStatus(dataList);
                // 设置当前数据
                this.currentDataList = dataList;
            } else if (dataList === 'highlight') {
                const data = this.currentDataList;
                this.operateLightStatus(data);
                this.currentData = data;
            }
        },
        // 重新刷新树列表
        refresh(callback, isTurnPage = true) {
            // 更新视图重新计数
            this.eUpdateViewCount = 0;
            const _name = `【${this.name}】---【refresh】`;
            const _this = this;
            const _startTime = window.fdGlobal.performance.getCurrentTime();
            // 1.重新转换数据
            this.dTransformTreeData();
            window.fdGlobal.performance.execute(`${_name}【1.数据组装】更新时间 :`, _startTime);
            // 2. 触发一次滚动事件，更新当前展示数据
            this.eUpdateView(() => {
                window.fdGlobal.performance.execute(`${_name}【2.更新视图】更新时间】 :`, _startTime);
                // 当前激活节点赋值
                if (this.gActiveItem[this.dKey]) {
                    const _currentItem = this.cGetCache(this.gActiveItem[this.dKey]);
                    // 如果设置当前页
                    if (_currentItem && isTurnPage) {
                        // 设置当前的分册列表
                        this.caclSetcaclCurrentPagesList(_currentItem);
                    }
                }
                if (type(callback) === 'function') {
                    callback(_this);
                }
            });
            // 触发树刷新事件
            this.emitEvent('refresh');
        },
        // 刷新当前视图
        refreshCurrentView(callback) {
            // 更新视图
            this.eUpdateView(callback);
        },
        // 更新数据的方法
        update(dataList, callback, isTurnPage = true, isUniqueTreeData) {
            const _this = this;
            const _name = `【${this.name}】---【update】`;
            const _startTime = window.fdGlobal.performance.getCurrentTime();
            if (type(dataList) === 'array') {
                window.fdGlobal.performance.execute(`${_name}更新时间【0开始更新前】 :`, _startTime);
                // 1. 更新数据
                this.dSetDataList(dataList, typeof isUniqueTreeData === 'boolean' ? isUniqueTreeData : this.isUniqueTreeData);
                window.fdGlobal.performance.execute(`${_name}更新时间【1 dataList赋值】 :`, _startTime);
                // 2. 转换数据
                this.dTransformTreeData();
                window.fdGlobal.performance.execute(`${_name}更新时间【2组装数据完成】 :`, _startTime);
                // 3. 触发一次滚动事件，更新当前展示数据
                this.eUpdateView(() => {
                    window.fdGlobal.performance.execute(`${_name}更新时间【4更新视图结束】 :`, _startTime);
                    // 当前激活节点赋值
                    if (this.gActiveItem[this.dKey]) {
                        // 当前激活的节点
                        const _currentItem = this.cGetCache(this.gActiveItem[this.dKey]);
                        // 如果设置当前页
                        if (_currentItem && isTurnPage) {
                            // 设置当前的分册列表
                            this.caclSetcaclCurrentPagesList(_currentItem);
                        }
                    }
                    window.fdGlobal.performance.execute(`${_name}更新时间【5更新视图结束分页】 :`, _startTime);
                    if (type(callback) === 'function') {
                        callback(_this);
                    }
                    window.fdGlobal.performance.execute(`${_name}更新时间【6更新视图结束的回调函数】 :`, _startTime);
                });
                // 触发树更新事件
                this.emitEvent('update');
                window.fdGlobal.performance.execute(`${_name}更新时间【3更新视图】 :`, _startTime);
            }
        },
        // 树渲染完后的方法
        nextTick(callback) {
            const _this = this;
            if (type(callback) === 'function') {
                _this.$nextTick(() => {
                    callback(_this);
                });
            }
        }

    }
};



