import Vue from 'vue';
import clickoutside from './clickoutside/index.js';
import lineClamp from './line-clamp/index.js';
import tips from './tips/index.js';
// 点击外围
Vue.directive('clickoutside', clickoutside);
Vue.directive('line-clamp', lineClamp);
Vue.directive('tips', tips);



