/**
 * @version 20190301
 * @author wuwg
 * @createTime  20190410
 * @updateTime  20190410
 * @decription  事件绑定|取消函数
 */

export default {

    /**
     * @name   on
     * @time   2019-04-10
     * @param  element  { object}  需要绑定事件的对象或者元素
     * @param  event   ｛string｝ 需要绑定的事件名
     * @param  handler ｛function｝ 事件的回调函数
     * @description      为元素或对象绑定事件
     */
    on: (() => {
        if (document.addEventListener) {
            return (element, event, handler, capture) => {
                if (element && event && handler) {
                    element.addEventListener(event, handler, Object.prototype.toString.call(capture) === '[object Boolean]' ? capture : false);
                }
            };
        }
        return (element, event, handler) => {
            if (element && event && handler) {
                element.attachEvent(`on${event}`, handler);
            }
        };
    })(),

    /**
     *
     * @name   off
     * @time    2019-04-10
     * @param  element  { object}  需要解除事件的对象或者元素
     * @param  event   ｛string｝ 需要解除的事件名
     * @param  handler ｛function｝ 解除事件的回调函数
     * @description      为元素或对象解除事件
     */
    off: (() => {
        if (document.removeEventListener) {
            return (element, event, handler, capture) => {
                if (element && event) {
                    element.removeEventListener(event, handler, Object.prototype.toString.call(capture) === '[object Boolean]' ? capture : false);
                }
            };
        }
        return (element, event, handler) => {
            if (element && event) {
                element.detachEvent(`on${event}`, handler);
            }
        };
    })()
};



