/**
 * @function
 * @version 1.0.1
 * @author wuwg  <wuwg@thunisoft.com>
 * @name  adjustScrollBarPosition
 * @memberof FdGlobal
 * @param {number} left 当前的left
 * @param {number} top  当前的top
 * @param {object} element  目标元素
 * @description 根据
 * @returns {object}  返回一个对象，携带判断之后的top和left, position
 * @example
 * adjustScrollBarPosition(top,left, element)
 */
function adjustTipsPosition(left, top, element) {
    // 当前的left 和top
    let _left = left;
    let _top = top;
    // window 的宽和高
    const _winW = window.innerWidth;
    const _winH = window.innerHeight;
    // 元素的宽和高
    const _elementW = element.clientWidth;
    const _elementH = element.clientHeight;
    // 最终的位置
    const _position = ['right', 'bottom'];
    // 如果已经到最右边
    if (_left + _elementW > _winW) {
        _left = _left - _elementW;
        _position[0] = 'left';
    }
    if (_top + _elementH > _winH) {
        _top = _top - _elementH;
        _position[1] = 'top';
    }
    // 返回一个对象
    return {
        top: _top,
        left: _left,
        position: _position
    };
}
// 导出一个模块
export default adjustTipsPosition;



