/**
 * @version 20190501
 * @author wuwg
 * @createTime  20190512
 * @updateTime  20190512
 * @name   type
 * @time   2019-05-12
 * @param {object}   obj 要检测的对象
 * @description  深度拷贝对象
 * @returns {*} 返回拷贝后的对象
 */
// 类型判断工具
import type from './type.js';

/**
 * 深度拷贝
 * @param {object|Array} obj 需要拷贝的对象
 * @returns {*} 返回拷贝后的对象
 */
function deepClone(obj) {
    const result = type(obj) === 'array' ? [] : {};
    for (const _key in obj) {
        const _copy = obj[_key];
        if (type(_copy) === 'object') {
            result[_key] = deepClone(_copy);
        } else if (type(_copy) === 'array') {
            result[_key] = deepClone(_copy);
        } else {
            result[_key] = obj[_key];
        }
    }
    return result;
}
export default deepClone;



