/**
 * @author  wuwg
 * @name   makeOneDimensionalList
 * @time   2020-06-20
 * @param {object}   dataItem 当前的对象
 * @param {array}   oneDimensionalList 当前的一维列表
 * @description  生成一维数组列表
 * @returns {*} 無返回值
 */
function makeOneDimensionalList(dataItem, oneDimensionalList) {
    if (dataItem) {
        const _len = oneDimensionalList.length;
        // 设置一维数组的索引值
        dataItem.oneDimensionalIndex = _len;
        // 添加数组
        oneDimensionalList[_len] = dataItem;
    }
}

export default makeOneDimensionalList;



