/**
 * @version 20190401
 * @author wuwg
 * @createTime  20190416
 * @updateTime  20190416
 * @decription  列表性能优化，添加占位元素
 */
export default {
    methods: {
        // 判断占位数据的位置
        edAdjustPosition(event, dataItem) {
            // 可以放置的位置，默认【top', 'midle', 'bottom】都行
            // const _edCanDropPosition = this.edCanDropPosition(dataItem, this.edCurrentDragDataItem, event, this);
            //
            // 【originTree】 2019-05-21
            const dragNode = this.getTreeCacheObjectNode(this.edCurrentDragDataItem.id) ? this.getTreeCacheObjectNode(this.edCurrentDragDataItem.id) : this.dragNode;
            const hoverNode = this.getTreeCacheObjectNode(dataItem.id);
            const _edCanDropPosition = this.allowPosition(hoverNode, dragNode, event, this);
            // 获取event对象
            const _target = event.target;
            const _count = 2;
            // 偏移位置
            const _distance = 6;
            const _top = _target.getBoundingClientRect().top;
            const _height = _target.offsetHeight;
            const _pageY = event.clientY;
            let _targetPosition = '';
            if (_edCanDropPosition.length) {
                // 如果包含中间
                if (_edCanDropPosition.indexOf('middle') !== -1) {
                    // 中间
                    _targetPosition = 'middle';
                    if (_edCanDropPosition.indexOf('top') !== -1) {
                        // 上面
                        if (_pageY <= _top + _distance) {
                            _targetPosition = 'top';
                            // 下面
                        } else if (_pageY >= _top + _height - _distance) {
                            _targetPosition = 'bottom';
                        }
                    }
                } else {
                    // 如果不包含中间值，那么就只有上和下
                    _targetPosition = _pageY - _top < _height / _count ? 'top' : 'bottom';
                }
            }
            return _targetPosition;
        },
        // 添加占位
        edAddPlaceholder(event, dataItem, index) {
            // 放置的位置
            const _targetPosition = this.edAdjustPosition(event, dataItem);
            // 如果不等于空
            if (_targetPosition !== '') {
                // 设置当前插入的位置
                this.edSetDropPostion(_targetPosition);
                if (_targetPosition !== 'middle') {
                    // 设置放置条目内部的条目id
                    this.edSetDropInnerItemId('');
                    // 索引值
                    const _index = _targetPosition === 'top' ? index : index + 1;
                    // 拿原始数据进行递归查找
                    const _placeholderCurrentDataList = JSON.parse(JSON.stringify(this.edCopyCurrentDataList));
                    // 占位的数据
                    const _placeholderItem = JSON.parse(JSON.stringify(dataItem));
                    // 改变id
                    _placeholderItem[this.dKey] = `placeholder${_placeholderItem[this.dKey]}`;
                    _placeholderItem[this.dName] = '';
                    _placeholderItem.details.name = '';
                    _placeholderItem.type = 'placeholder';
                    // 添加属性
                    this.dAddProperty(_placeholderItem, _placeholderItem.level, _placeholderItem.index);
                    // 添加到当前视图
                    // 修改数据
                    _placeholderCurrentDataList.splice(_index, 0, _placeholderItem);
                    // 设置当前的数据列表为数组
                    this.setCurrentDataList(_placeholderCurrentDataList);
                } else {
                    // 设置放置条目内部的条目id
                    this.edSetDropInnerItemId(dataItem[this.dKey]);
                    // 设置当前的数据列表为数组
                    this.setCurrentDataList(JSON.parse(JSON.stringify(this.edCopyCurrentDataList)));
                }
            }
        }
    }
};



