/**
 *@file dpOperateData
 *@version 1.0.1
 *@author wuwg
 *@createTime 2020/08/15 - 21:42
 *@updateTime 2020/08/15 - 21:42
 *@see [jsDoc中文文档]{@link  http://www.dba.cn/book/jsdoc/JSDOCKuaiBiaoQianBLOCKTAGS/CONSTRUCTS.html}
 @description operate 的描述， 处理数据的模块，所有数据都在这个模块中
 @namespace    dp (命名空间) d: data 的意思p:performance  性能优化的意思

 */
// 获取当前的列表
import getCurrentDataList from './getCurrentDataList.js';
// 缓存对象
import dpCache from './dpCache.js';
// 导出模块
export default {
    mixins: [dpCache],
    data() {
        return {
            // 当前显示的数据列表
            currentDataList: [],
            // 第一条数据
            dpFirstDataItem: null,
            // 当前的滚动条值
            dpCurrentScrollTop: 0,
            // 滚动条需要减去的值
            dpScrollTopReduce: 0
        };
    },
    // 计算样式
    computed: {
        // 样式
        dpStyle() {
            return {
                transform: `translate(0,${this.dpCurrentScrollTop - this.dpScrollTopReduce}px)`,
                msTransform: `translate(0,${this.dpCurrentScrollTop - this.dpScrollTopReduce}px)`
            };
        }
    },
    methods: {
        // 初始化变量
        dpInitVariable() {
            // 初始化
            this.dpcInitCache();
            this.dpFirstDataItem = null;
        },
        // 设置当前的滚动条位置
        dpSetCurrentScrollTop(top) {
            // 当前的滚动条滚动的距离
            this.dpCurrentScrollTop = top;
            // 设置当前的数据列表
            this.dpSetCurrentDataList();
        },

        // 设置当前的数据列表
        dpSetCurrentDataList() {
            const _startTime = window.fdGlobal.performance.getCurrentTime();
            // 1. 重置缓存
            this.dpcResetCache();
            window.fdGlobal.performance.execute('【【树组件】=>【设置当前的数据列表】重置缓存 :', _startTime);
            // 获取数据和需要减去的top值
            // 需要修改的就是 this.dDataList
            const {
                noDataLine,
                resultData,
                scrollTopReduce
            } = getCurrentDataList(this.dResultShowDataList, this.sCurrentScrollTop, {
                // 分类的高
                categoryHeight: this.hItemHeight,
                // 可是区域的数据条目
                clientCount: this.hClientCount,
                // 是否有线------
                hasLine: this.hasLine,
                // 数据的id
                id: this.dKey,
                // 子数据的key
                children: this.dChildren,
                // 开始计算滚动条的索引值
                startIndex: this.startIndex,
                // 预加载数量
                prepareCount: this.prepareCount
            });
            window.fdGlobal.performance.execute('【【树组件】=>【设置当前的数据列表】计算数据 :', _startTime);
            // 添加当前数据的所有缓存
            this.dpcAddAllCache(resultData);
            window.fdGlobal.performance.execute('【【树组件】=>【设置当前的数据列表】添加缓存 :', _startTime);
            // 设置当前的数据
            this.setCurrentDataList(resultData);
            window.fdGlobal.performance.execute('【【树组件】=>【设置当前的数据列表】设置当前数据 :', _startTime);
            // 需要减去的高度
            this.dpScrollTopReduce = scrollTopReduce;
            // 设置无数据时线的状态
            this.sSetNoDataLineStatus(noDataLine);
            // 设置滚动条应该减去的值
            this.sSetScrollTopReduce(scrollTopReduce);
            window.fdGlobal.performance.execute('【【树组件】=>【设置当前的数据列表】其他赋值 :', _startTime);
        },


        //  处理头部选中
        dpOperateHeaderChecked(item) {
            // 设置开始时间
            const _startTime = window.fdGlobal.performance.getCurrentTime();
            // 1.设置当前选中的条目 【界面数据】
            item.checked = !item.checked;
            const _item = item;
            const _checked = item.checked;
            // 2.子类全部选中/不选中  【界面数据】
            _item.children.forEach((itemC) => {
                // 如果选中状态跟父状态不一致，需要改成一致
                if (itemC.nodeType === 'material') {
                    if (itemC.checked !== _checked) {
                        itemC.checked = _checked;
                    }
                }
            });
            window.fdGlobal.performance.execute('【制卷目录=>全选】子类花费的时间 :', _startTime);
            // 设置开始时间
            const _startTime2 = window.fdGlobal.performance.getCurrentTime();
            // 3.对应缓存数据要被选中 【缓存数据】
            this.dpCache[`${item.id}`].checked = _checked;
            // 4.子类全部选中/不选中 【缓存数据】
            this.dpCache[`${item.id}`].children.forEach((itemC) => {
                // 如果选中状态跟父状态不一致，需要改成一致
                if (itemC.nodeType === 'material') {
                    if (itemC.checked !== _checked) {
                        itemC.checked = _checked;
                        this.dpCache[`${itemC.id}`].checked = itemC.checked;
                    }
                }
            });
            window.fdGlobal.performance.execute('【制卷目录=>全选】缓存数据花费的时间 :', _startTime2);
            // 设置开始时间
            const _startTime3 = window.fdGlobal.performance.getCurrentTime();
            // 5.处理选中列表
            this.dpOperateCheckedKeys();
            window.fdGlobal.performance.execute('【制卷目录=>全选】处理选中列表花费的时间 :', _startTime3);
            window.fdGlobal.performance.execute('【制卷目录=>全选】总花费的时间 :', _startTime);
        },
        // 处理选中列表
        dpOperateCheckedKeys() {
            // 1. 清空多选
            this.dClearCheckedKeys();
            // 循环所有的列表
            for (const _name in this.dpCache) {
                // 如果是材料，并且被选中，那么就添加到数组
                if (this.dpCache[_name].nodeType === 'material' && this.dpCache[_name].checked) {
                    // 2.设置当前选中的item
                    this.dSetCheckedKeys(this.dpCache[_name], false);
                }
            }
            // 3.触发全局的【当前选中材料节点信息】事件 vGlobal-index-content-checkedMaterils
            this.gEmitIndexContentCheckedMaterilsEvent();
        },

        // 单独点击复选框
        dpSingleCheck(item) {
            //  【界面数据】
            item.checked = !item.checked;
        },

        // 判断父元素是否选中
        dpAdjustParentChecked(item, indexP) {
            const _pid = item.pid;
            // 判断是否存在
            if (_pid && this.dpCache[`${_pid}`]) {
                // 1.如果父元素存在,修改原始数据 【缓存数据】
                const _pItem = this.dpCache[`${_pid}`];
                _pItem.checked = _pItem.children.every((itemC) => itemC.checked);
                // 2. 处理当前展示数据 【界面数据】
                this.currentDataList[indexP].checked = _pItem.checked;
            }
        },
        // 取消全部选中【可视列表】
        dpCancleAllChecked() {
            // 1.可视区域数据取消选中 【界面数据】
            this.currentDataList.forEach((item, index) => {
                // 条目下的附件this.cache
                item.children && item.children.forEach((itemC) => {
                    itemC.checked = false;
                });
                // 条目
                item.checked = false;
                // 先删除
                const _target = this.currentDataList.splice(index, 1)[0];
                // 后追加进去
                this.currentDataList.splice(index, 0, _target);
            });
            // 2. 取消缓存数据选中 【缓存数据】
            // 取消缓存中的数据选中态,
            Object.keys(this.dpCache)
                .forEach((key) => {
                    this.dpCache[key].checked = false;
                });
        },
        // 更新数据条目
        dpUpdateDataItem(index, item) {
            // 后追加进去
            this.currentDataList.splice(index, 0, item);
        }
    }
};



