/**
 * #info
 *
 *@author   wuwg
 *@createTime   2018/03/20
 *@updateTime   2018/04/23
 *@description  breadcrumb  component  面包屑组件
 */
// module
const module = {
    props: {

        /**
         * #property
         *
         * @name    separator
         * @cn      分隔符符号
         * @type    string
         * @value   /
         * @description 分隔符符号
         */
        separator: {
            type: String,
            default: '>'
        },

        /**
         * #property
         *
         * @name    data
         * @cn      面包屑数据
         * @type    array
         * @required true
         * @bind    must
         * @value   []
         * @description 面包屑数据。数据格式化为： {name:'home', href:'http://www.baidu.com',icon:'ios-home-outline',className:'test1'}，如引入了vue-router，href将作为路由跳转
         */
        dataList: {
            type: Array,
            default() {
                return [

                    /*
                     *  {
                     * name:'home',
                     * disabled: false,
                     * href:'http://www.baidu.com',
                     * icon:'ios-home-outline',
                     * className:'test1'
                     *}
                     */
                ];
            }
        }

    },
    computed: {
        // 数据长度
        len() {
            return this.dataList.length;
        }
    },
    methods: {
        // 当前点击的条目
        clickItem(item, index, event) {
            if (index + 1 < this.len) {
                this.$emit('click', item, index, event);
            }
        }
    }
};
export default module;



