/**
 * @author    wuwg
 * @createTime    2010-09-18
 * @updataTime     2010-10-27
 * @description   【单个列表展示组件】
 */

// import mockData from './mockData.js';
// 混入文件
// 全局事件
import globalEvent from './extend/globalEvent.js';
// 模块
const module = {
    mixins: [globalEvent],
    props: {
        // 数据对象
        dataObject: {
            type: Object,
            // 验证
            validator(dateItem) {
                // 必须包含 dataTitle 和 data
                return dateItem.hasOwnProperty('dataTitle') && dateItem.hasOwnProperty('data');
            },
            // 默认值
            default() {
                return {};
            }
        }
    },
    // 数据
    data() {
        return {
            // 是否有序号
            hasOrder: true,
            // 表头数据
            tableTh: [],
            // table数据
            tableData: [],
            // 右上角搜素数据
            filterData: [],
            // 时间段
            dateSection: '',
            // 总条数
            total: 10,
            // 第几页
            offset: 0,
            // 当前第几页
            current: 1,
            // 下载密码
            rules: {
                downloadPassword: [{
                    required: true,
                    validator: function (rule, value, callback) {
                        if (value === '') {
                            callback(new Error('请输入密码'));
                        } else if (!(/^(?=.*\d)(?=.*[a-zA-Z]).{10,100}$/).test(value)) {
                            callback(new Error('密码至少包含数字和字母两种字符，位数要在10位以上（包含10位）'));
                        }
                        callback();
                    },
                    message: '提示：密码至少包含数字和字母两种字符，位数要在10位以上（包含10位）',
                    trigger: 'blur'
                }]
            },
            downloadPassword: '',
            // 数据项的正则验证，支持字符串或者绑定RegExp对象
            pattern: '^(?=.*\\d)(?=.*[a-zA-Z]).{10,100}$',
            // 数据项的正则验证失败后的消息提示
            patternMessage: '提示：密码至少包含数字和字母两种字符，位数要在10位以上（包含10位）',
            // 每页条数
            limit: window.fdConfig.limit,
            // 下載的表格id
            downloadId: '',
            // 缓存查询数据
            cacheSearchData: null,
            // table加表头
            categoryName: '',
            // table显示与否
            isTableShow: true,
            // 查询数据
            searchData: null
        };
    },
    // 创建
    created() {
        this.init();
    },
    activated() {
        this.init();
    },
    // watch: {
    //     // 监听参数变化，请求数据
    //     dataObject(newVal, oldVal) {
    //         if (newVal !== oldVal) {
    //             this.update(this.dataObject);
    //         }
    //     }
    // },
    methods: {

        /**
         * @description 初始化的方法
         * @name init
         * @return {*} 无
         */
        init() {
            this.update(this.dataObject);
        },
        // 初始化数据
        update(dataObject, type = 'table') {
            // 清空时间段
            this.dateSection = '';
            // 清空查询数据
            this.searchData = {};
            // 清空缓存查询数据
            this.cacheSearchData = {};
            //  必须是table类型的数据
            if (!(Object.prototype.toString.call(dataObject, null) === '[object Object]' && dataObject.category === 'table' && type === 'table')) {
                return false;
            }
            // 更新滚动条
            this.$emit('scrollUpdate');
            // 分页数据变量赋值
            this.limit = dataObject.limit;
            this.total = dataObject.total;
            this.offset = dataObject.offset;
            this.current = this.offset / this.limit + 1;
            // (String) 组件id， 在后端返回的数据中有！字段统一叫 componentId
            this.componentId = dataObject.componentId;
            // 表头数据
            this.tableTh = dataObject.dataTitle;
            // table数据
            this.tableData = dataObject.data;
            // 左上角搜索数据
            this.filterData = dataObject.filterData ? dataObject.filterData : [];
            // table表头名称
            this.categoryName = dataObject.categoryName;
        },
        // 切换显示隐藏状态
        clickToggle() {
            this.isTableShow = !this.isTableShow;
            this.$forceUpdate();
        },
        //  操作按钮
        clickOperate(itemCC, item) {
            // debugger
            const linkType = itemCC.linkType;
            switch (linkType) {
                case 'view':
                case 'subtable':
                    item.sourseType = 'table';
                    // 触发事件
                    this.emitGlobalEvent(() => {
                        // 组件内部不做处理，全部交给外围处理
                        this.globalBus.$emit('global-cxtj-main-operate', 'table', itemCC, item);
                    });
                    break;
                case 'download':
                    // 下载
                    this.$refs.jsModalDownload.open();
                    this.$refs.downloadForm.clearValidateInfo();
                    this.downloadId = item.id;
                    break;
                default:
                    break;
            }
        },
        // 获取查询字段数据
        // isSearch代表 是否是点击的查询
        getSearchData(isSearch = true) {
            let searchData = this.searchData;
            if (isSearch || !this.cacheSearchData) {
                // 创建缓存对象
                this.cacheSearchData = {};
                this.filterData.forEach((item) => {
                    switch (item.filterType) {
                        case 'text':
                            searchData[item.key] = item.value || '';
                            // 设置缓存数据
                            this.cacheSearchData[item.key] = searchData[item.key];
                            break;
                        case 'select':
                            searchData[item.key] = item.value || '';
                            // 设置缓存数据
                            this.cacheSearchData[item.key] = searchData[item.key];
                            break;
                        case 'date':
                            // 开始时间
                            searchData[item.key] = this.dateSection;
                            // 设置缓存数据
                            this.cacheSearchData[item.key] = searchData[item.key];
                            break;
                        default:
                            break;
                    }
                });
            } else {
                // 用缓存的数据
                searchData = this.cacheSearchData;
            }

            return searchData;
        },

        // 下载弹框点击确定按钮
        onOk() {
            const _this = this;
            _this.$refs.downloadForm.validate()
                .then(function () {
                    const downloadObject = {
                        id: _this.downloadId,
                        password: _this.downloadPassword
                    };
                    _this.emitGlobalEvent(() => {
                        // 组件内部不做处理，全部交给外围处理
                        _this.globalBus.$emit('global-xz-main-operate', downloadObject);
                    });
                    _this.downloadPassword = '';
                    _this.$refs.jsModalDownload.close();
                });
        },
        // 下载弹框点击取消按钮
        onCancel() {
            // 清空输入框内容
            this.downloadPassword = '';
            // 清空验证提示信息
            this.$refs.downloadForm.clearValidateInfo();
        },
        // 下载弹框点击关闭按钮
        onClose() {
            // 清空输入框内容
            this.downloadPassword = '';
            // 清空验证提示信息
            this.$refs.downloadForm.clearValidateInfo();
        },
        // 更新table数据
        updateTableData(data) {
            this.total = data.total;
            this.offset = data.offset;
            this.tableData = data.data;
        },
        // 查询按钮
        clickSearch() {
            this.offset = 0;
            this.current = this.offset / this.limit + 1;
            // 触发请求数据
            this.emitRequestData(true);
        },
        // 重置按钮
        clickReset() {
            // 时间段清空
            this.dateSection = '';
            // 下拉框和输入框的值清空
            this.filterData.forEach((item) => {
                item.value = '';
            });
            // 展示清空左上角搜索条件之后的数据
            this.clickSearch();
        },
        // 改变查询条件
        changeCondition(condition) {
            // 触发请求数据
            this.emitRequestData(false, condition);
        },
        // 分页接口
        pagingChange(page) {
            this.offset = page.offset;
            this.limit = page.limit;
            this.current = this.offset / this.limit + 1;
            // 触发请求数据
            this.emitRequestData(false);
        },
        // 触发请求数据
        // isSearch 代表 是否是点击的查询
        emitRequestData(isSearch = false, condition) {
            const _condition = condition || this.$refs.jsTable.getCondition();
            const searchData = this.getSearchData(isSearch);
            // 查询条件
            const _queryCondition = {
                // 获取查询数据
                // ...this.getSearchData(),
                searchData,
                // 表头的筛选条件
                ..._condition
            };
            // 最终的请求数据
            const queryData = {
                // 请求字段
                queryCondition: _queryCondition,
                // 限制页数
                limit: window.fdConfig.limit,
                // 当前页数
                offset: this.offset,
                // (String) 组件id， 在后端返回的数据中有！字段统一叫 componentId
                componentId: this.componentId
            };
            // 触发全局事件
            this.globalBus.$emit('global-query-search', queryData);
        },
        // 左上角搜索条件label宽度自适应
        getLabelWidth(str) {
            return str.length ? (str.length + 1) * 17 : 120;
        }
    }
};
export default module;



