/**
 *@file config.js
 *@version 1.0.1
 *@author wuwg
 *@createTime 2019/10/17 - 20:37
 *@updateTime 2019/10/17 - 20:37
 *@see [jsDoc中文文档]{@link  http://www.dba.cn/book/jsdoc/JSDOCKuaiBiaoQianBLOCKTAGS/CONSTRUCTS.html}
 * @description    server-config.js.js ,所有服务器的url配置
 */
const dirServicePath = window.serverConfig.dirServicePath;
// 服务数据
const serverUrl = {
    // 树数据
    tree: `${dirServicePath}/api/tree`,
    // 下载， 下载全部，下载部分， 下载单个
    download: `${dirServicePath}/api/download`,
    // 导出【确认是否有新结果可以导出】
    isExport: `${dirServicePath}/api/isExport`,
    // 导出【用于后台准备数据】
    exports: `${dirServicePath}/api/export`,
    // 导出【下载后台生成的zip包】
    exportzip: `${dirServicePath}/api/exportzip`,
    // 分页+查询
    pagingSearch: `${dirServicePath}/api/pagingSearch`,
    //  总览
    allScan: `${dirServicePath}/api/v1/cxsq/fkjd/`,
    //  获取table列表数据的接口
    getList: '',
    //  获取数据列表，有五类数据
    // table, form-table, from-table-mixture， from-table-group,   category-form-table
    getData: `${dirServicePath}/api/getData`
};
export default serverUrl;



