/**
 * @version 20190401
 * @author wuwg
 * @createTime  20190401
 * @updateTime  20190417
 * @decription  树组件绑定事件
 */
// import fdEventBus from '@/js/app/common/fdEventBus.js';
// 获取当前的列表
// import getCurrentDataList from './performance/getCurrentDataList.js';
// 拖拽事件
import dragEvent from './drag/dragEvent.js';
// 事件库
import tools from '../utils/index.js';
// 展开收起
import toggleHandler from './eventHandler/toggleHandler.js';
// 显示隐藏
import emptyFolderToggleHandler from './eventHandler/emptyFolderToggleHandler.js';
// 复选框
import checkboxHandler from './eventHandler/checkboxHandler.js';
// import store from '@/store/index.js';
// 当前模块
export default {
    mixins: [
        dragEvent,
        toggleHandler,
        emptyFolderToggleHandler,
        checkboxHandler
    ],
    data() {
        return {
            // 更新视图的次数
            eUpdateViewCount: 0,
            // 时间控制器
            eRequestAnimationFrameTimer: null,
            // 结束事件
            eEndTime: 0,
            // 名称后缀
            eNameSuffix: ''
        };
    },
    computed: {
        // 开始拖拽
        dragstartevent() {
            return this.edDraggable ? 'mousedown' : '';
        },
        // 拖拽进入
        dragenterevent() {
            return this.edDraggable ? 'mouseenter' : '';
        },
        // 拖拽划过
        dragoverevent() {
            return this.edDraggable ? 'mousemove' : '';
        },
        // 拖拽离开
        dragleaveevent() {
            return this.edDraggable ? 'mouseleave' : '';
        },
        //  拖拽放下
        dropevent() {
            return this.edDraggable ? 'mouseup' : '';
        },
        //  拖拽放下
        draggableFlag() {
            return this.edDraggable ? 'draggable' : '';
        }
    },
    beforeDestroy() {
        // 取消resize事件
        this.eUnbindResize();
    },
    methods: {
        // 获取event对象
        eGetEvent(event) {
            return event || window.event;
        },
        // 阻止默认事件
        ePreventDefault(event) {
            // 如果是FF下执行这个
            if (event && event.preventDefault) {
                event.preventDefault();
            } else {
                // 如果是IE下执行这个
                window.event.returnValue = false;
            }
        },

        // 右键菜单事件
        eContextmenu(event, item) {
            // 阻止默认事件
            this.ePreventDefault(event);
            // 触发事件
            this.emitEvent(event, 'contextmenu', item);
        },
        // 点击扩展图标
        eClickExtendIcon(event, dataItem, dataItemP, index) {
            // 触发事件
            this.emitEvent('clickExtendIcon', event, dataItem, dataItemP, index);
        },

        /**
         * @param {event} event event对象
         * @param {object} dataItem 当前的对象条目
         * @description 回车事件 ，触发失去焦点事件
         * @returns {*} 无返回值
         */
        eFocusHandler(event, dataItem) { // eslint-disable-line
            // 触发回车事件
            this.emitEvent('focus', event, dataItem);
        },

        /**
         * @param {event} event event对象
         * @param {object} dataItem 当前的对象条目
         * @description 回车事件 ，触发失去焦点事件
         * @returns {*} 无返回值
         */
        eEnterHandler(event, dataItem) { // eslint-disable-line
            //  获取目标节点
            const _targetNode = event.target;
            // 触发是去焦点事件
            _targetNode.blur();
            // 触发回车事件
            this.emitEvent('enter', event, dataItem);
        },

        /**
         * @param {event} event event对象
         * @param {object} dataItem 当前的对象条目
         * @description 双击 ，触发事件dblclick
         * @returns {*} 无返回值
         */
        eBlurHandler(event, dataItem) {
            const _this = this;
            // 取消正在重命名
            dataItem.renaming = false;
            // 失去焦点
            _this.$nextTick(() => {
                const _targetItem = _this.getTreeCacheObjectNode(dataItem[_this.dKey]);
                // @todo 20201127 逻辑需要梳理
                if (_this.eRenamePatternCallback(dataItem, _this) && this.validateMendName(dataItem[this.dName])) {
                    // 将名字加上后缀
                    dataItem[_this.dName] = dataItem[_this.dName] + _this.eNameSuffix;
                    // 还原后缀
                    _this.eResetNameSuffix();
                    const _dataItem = _this.cGetCache(dataItem[_this.dKey]);
                    // 与原来的名字进行对比
                    if (dataItem[_this.dName] !== _dataItem[_this.dName]) {
                        // 更新信息--去掉在这块儿更新，因为有可能重命名失败之后需要还原，在成功了之后去更新
                        // _this.cUpdateCache(dataItem, _this.dName)
                        // 设置名称
                        _targetItem.setSingleCurrentName(dataItem, dataItem[_this.dName]);
                        // 【originTree】 2019-05-21
                        // 设置节点名称
                        _targetItem.setSingleCurrentName(dataItem[_this.dName]);
                        // 1.重新转换数据  此处可能带来性能问题
                        this.dTransformTreeData();
                        // 设置它是活跃的节点，因为要更新对应的头部工具栏，强制更新当前选中节点，这个是因为在添加的时候没有更新头部工具栏
                        this.setActiveId(dataItem.id, null, true);
                        // 修改名称
                        this.emitEvent('mend-name', event, _targetItem);
                        // 【originTree】 2019-05-21
                        /*   // 执行回调函数
                         _this.eRenameHandlerCallback(dataItem, _this).then(() => {
                         // 更新缓存数据
                         _this.cUpdateCache(dataItem, _this.dName);
                         // 触发事件
                         _this.$emit('mend-name', true, dataItem);
                         // 修改名称
                         this.tree.emitEvent('mend-name', event, this)
                         }, () => {
                         // 失败恢复名称
                         dataItem[_this.dName] = _this.cGetCache(dataItem[_this.dKey])[_this.dName];
                         // 触发事件, false :
                         // 2 请求失败
                         _this.$emit('mend-name', false, 2, dataItem);
                         // 修改名称
                         this.tree.emitEvent('mend-name', event, this)
                         });*/
                    }
                } else {
                    // 失败恢复名称
                    dataItem[_this.dName] = _this.cGetCache(dataItem[_this.dKey])[_this.dName];
                    // 触发事件
                    // 【originTree】 2019-05-21
                    // 1 正则匹配失败
                    // _this.$emit('mend-name', false, 1, dataItem);
                    // 修改名称
                    this.emitEvent('mend-name-fail', event, _targetItem);
                }
                // 无论成功还是失败都需要触发 blur事件触发事件
                this.emitEvent('blur', event, dataItem);
            });
        },
        // 还原后缀
        eResetNameSuffix() {
            this.eNameSuffix = '';
        },
        // 设置后缀名称
        eSetNameSuffix(dataItem) {
            const _pResult = (/(.*)(\..*)$/).exec(dataItem[this.dName]);
            // 业务匹配
            const _ywParttern = /^material$|^transitmaterial$|^attachment$|/igm;
            if (_pResult && _ywParttern) {
                // 名称后缀
                this.eNameSuffix = _pResult[2]; // eslint-disable-line
                // 当前的名称
                dataItem[this.dName] = _pResult[1];
            } else {
                this.eNameSuffix = '';
            }
        },

        /**
         * @param {Event} event 当前的event对象
         * @param {object} dataItem 当前的对象条目
         * @description 单击数据条目 ，触发事件click
         * @returns {*} 无返回值
         */
        eClickItemHandler(event, dataItem) {
            // 1.如果禁用，直接返回
            if (dataItem.disabled) {
                return;
            }
            // 2. 是否在点击节点的时候，复选框选中|取消选中
            if (this.checkOnClickNode) {
                this.eCheckBoxChange(dataItem);
            }
            // 3. 点击条目【失去焦点】 @todo  需要控制条目的focus
            this.isFocus = false;
            // 重置当前获得焦点的数据条目
            this.gResetFocusItem();
            // 4. 如果当前激活的id不等于本条数据id，那么就需要处理
            if (this.gActiveItem[this.dKey] !== dataItem[this.dKey] && dataItem.type === 'material') {
                // 设置当前激活的数据条目
                this.gSetActiveItem(dataItem);
            }
            // 5. 是否在点击节点的时候展开或者收缩节点（这个操作会刷新当前视图）
            if (this.expandOnClickNode) {
                this.eClickToggle(dataItem);
            }
            // 6. 触发事件
            this.emitEvent('click', event, dataItem);
        },

        /**
         * @param {Event} event 当前的event对象
         * @param {object} dataItem 当前的对象条目
         * @description 双击 ，触发事件dblclick
         * @returns {*} 无返回值
         */
        eDblclickItemHandler(event, dataItem) {
            const _this = this;
            // 1. 如果可以重命名|非禁用| 重命名回调函数判断是true
            if (this.canMendName && !dataItem.disabled && this.edCanMendNameCallback(dataItem, _this)) {
                // 将所有的树节点都置为不可拖动   【originTree】 2019-05-21
                this.edSetCurrentDraggable(false);
                // 是否更新当前节点
                // 正在重命名 @todo  20201127 重命名逻辑需要改写
                dataItem.renaming = true;
                // 双击时，需要更新当前选中节点的renaming属性
                this.gActiveItem.renaming = dataItem.renaming;
                // 后缀名称处理
                this.eSetNameSuffix(dataItem);
                // 获得焦点
                this.$nextTick(() => {
                    // 获得焦点
                    this.$refs[`jsInput${dataItem[this.dKey]}`][0].focus();
                    // 选中文本
                    this.$refs[`jsInput${dataItem[this.dKey]}`][0].select();
                });
            }
            // 2. 触发事件
            this.emitEvent('dblclick', event, dataItem);
        },
        // 点击展开收起
        eClickToggle(dataItem) {
            // 设置当前激活的数据条目
            this.eToggleHandler(dataItem);
        },
        // 显示隐藏空文件夹数据
        eToggleEmptyFolder(flag) {
            this.eToggleEmptyFolderHandler(flag);
        },

        /**
         * @param {object} dataItem 当前的对象条目
         * @description 多选框 ，触发事件checked
         * @returns {*} 无返回值
         */
        eCheckBoxChange(dataItem) {
            // 设置当前激活的数据条目
            this.eCheckboxHandler(dataItem);
        },
        // 更新视图
        eUpdateView(callback) {
            // 更新视图次数
            this.eUpdateViewCount++;
            const _startTime = window.fdGlobal.performance.getCurrentTime();
            window.fdGlobal.performance.execute('【【树组件】=>【更新视图】开始更新视图 :');
            // 1.重置当前的dResultShowDataList列表  @author  wuwg 此处有优化空间, 不是每次更新视图都需要重置显示数据。 20200816
            this.dResetResultShowDataList();
            window.fdGlobal.performance.execute('【【树组件】=>【更新视图】【1.重置当前显示的列表】 :', _startTime);
            // 2.设置当前的滚动条位置，内部会根据当前的滚动条高度，设置当前的数据
            this.dpSetCurrentScrollTop(this.sCurrentScrollTop);
            window.fdGlobal.performance.execute('【【树组件】=>【更新视图】【2.设置当前显示的数据】 :', _startTime);
            // 执行回调函数
            if (typeof callback === 'function') {
                this.$nextTick(() => {
                    callback();
                });
            }
            // 如果滚动的回调函数是一个方法，那么执行回调函数
            this.$nextTick(() => {
                if (typeof this.isScrollAfterCallback === 'function') {
                    this.isScrollAfterCallback();
                    this.isScrollAfterCallback = null;
                }
                // 更新滚动条
                this.updateScrollbar();
                // 1.更新视图
                this.$emit('updateView', this.currentDataList, this);
            });
            window.fdGlobal.performance.execute('【【树组件】=>【更新视图】【3.完成视图更新消耗时间】 :', _startTime);
            window.fdGlobal.performance.execute(`【【树组件】=> 【更新视图】完成视图更新，显示出来的耗时，已经更新视图【${this.eUpdateViewCount}】次`);
        },
        // 滚动事件
        eScroll(top, left, callback) {
            // return false
            // 1.滚动时，如果有正在重命名的数据，手动触发重命名input框的失去焦点事件
            // @todo wuwg 20201127  这里的处理有点不妥，需要优化， 滚动后应该再次获得焦点
            if (this.gActiveItem.renaming) {
                // 失去焦点
                this.$refs[`jsInput${this.gActiveItem.id}`][0].blur();
                // 将当前正在重命名节点的renaming值置false
                this.gActiveItem.renaming = false;
            }
            // 2.设置当前的滚动条位置
            this.sSetCurrentScrollTop(top);
            // 当前时间
            const _currentTime = new Date().getTime();
            // 清除定时器
            if (this.eRequestAnimationFrameTimer) {
                cancelAnimationFrame(this.eRequestAnimationFrameTimer);
            }
            // 滚动事件处理函数
            // 3.当前时间超出，立即执行【函数节流】
            if (_currentTime - this.eEndTime > this.eScrollDelay) {
                //  滚动事件处理函数
                this.eScrollHandle(callback);
                // 结束时间
                this.eEndTime = _currentTime;
            } else {
                // 设置定时器
                this.eRequestAnimationFrameTimer = requestAnimationFrame(() => {
                    // 滚动事件处理函数
                    this.eScrollHandle(callback);
                });
            }
            // 3.触发滚动事件
            this.emitEvent('scroll');
        },
        // 滚动事件处理函数
        eScrollHandle(callback) {
            // 更新视图
            this.eUpdateView(callback);
            // 清空定时器
            this.eRequestAnimationFrameTimer = null;
        },
        // 滚轮事件
        eMousewheel(event) {
            // const _event = tools.getEvent(event);
            // 如果正在拖动，那么就禁止滚动
            if (this.edDragging) {
                event.preventDefault();
                return false;
            }
        },
        // resize 处理函数
        eResizeHanlder() {
            // 重新设置页面总高
            // this.hSetCountTotalHeight();
        },
        // 绑定resize事件
        eBindResize() {
            // 先取消绑定
            this.eUnbindResize();
            // 绑定事件
            tools.on(window, 'resize', this.eResizeHanlder);
        },
        // 取消resize事件
        eUnbindResize() {
            tools.off(window, 'resize', this.eResizeHanlder);
        }
    }
};



