/**
 * @author    wuwg
 * @createTime    2010-09-18
 * @updataTime     2010-09-18
 * @description   【单个fomr-table展示组件】
 */

// import mockData from './mockData.js';
// 全局事件
import globalEvent from '../common/globalEvent.js';
// 模块
const module = {
    mixins: [globalEvent],
    props: {
        // 数据值字段
        dCategory: {
            type: String,
            default: 'category'
        },
        // 数据值字段
        dValue: {
            type: String,
            default: 'value'
        },
        // 数据对象
        dataObject: {
            type: Array,
            // 默认值
            default() {
                return [];
            }
            // type: Object,
            // // 验证
            // validator(dateItem) {
            //     return dateItem.hasOwnProperty('category') && dateItem.category === 'form-table' && dateItem.hasOwnProperty('data');
            // },
            // // 默认值
            // default() {
            //     return {};
            // }
        },
        isNavigation: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            // 是否有序号
            hasOrder: true,
            // 当前的数据
            currentDataList: [],
            // 右侧楼梯导航的数据
            fixNavDataList: [],
            // 滚动容器
            connectId: '#jsMainInBox > .fd-scroll-area',
            // 固定导航对象
            fixNavObject: null,
            isHaveFixNav: false,
            // 每页条数
            limit: window.fdConfig.limit,
            // 点击分页时的index下标
            clickIndex: 0,
            dataList: [],
            // 是否存在图片
            hasImg:false
        };
    },
    computed: {
        // 总列数
        totalCol() {
            return this.hasImg ? 4 : 6; // eslint-disable-line
        }
    },
    // 创建
    created() {
        // 绑定全局事件
        this.bindGlobalEvent();
        this.init();
    },
    beforeDestroy() {
        // 取消全局事件
        this.unbindGlobalEvent();
    },
    activated() {
        this.init();
    },
    // watch:{
    //     // 监听参数变化，请求数据
    //     dataObject(newVal, oldVal) {
    //         if (newVal !== oldVal) {
    //             this.update(this.dataObject);
    //         }
    //     }
    // },
    methods: {

        /**
         * @description 初始化的方法
         * @name init
         * @return {*} 无
         */
        init() {
            this.update(this.dataObject);
        },
        // 初始化数据
        update(dataList, type = 'form-table') {
            this.dateSection = '';
            if (!(Array.isArray(dataList) && dataList.length && (dataList[0].category === 'form-table') && type === 'form-table')) {
                return false;
            }
            // 更新滚动条
            this.$emit('scrollUpdate');
            this.dataList = dataList;
            // 循环数据列表
            const _dataList = dataList.map((item) => {
                // 添加显示属性
                item.show = true;
                // 条目下的子数据, !important, 这是约定字段不能修改！
                const _cDataList = item.data;
                // 是否是二维数组
                // 先判断数组的长度，再判断数组的第一个值是否是数组， 程序只支持二维
                item.isTwoDimensionArray = _cDataList.length > 0 ? Array.isArray(_cDataList[0]) : false;
                item.count = _cDataList.length;
                return item;
            });
            // 设置当前的数据
            this.setCurrentDataList(_dataList);
        },
        // 设置当前的数据
        setCurrentDataList(dataList) {
            const _this = this;
            // 为了清理组件残留数据
            this.currentDataList = [];
            this.$nextTick(() => {
                _this.currentDataList = dataList;
                // 判断是否有图片
                _this.currentDataList.every((item) => {
                    let _flag = true;
                    if (item.category === 'form-table') {
                        _flag = false;
                        let imgFlag = true;
                        item.data.every((itemC) => {
                            if (itemC.type === 'img') {
                                imgFlag = false;
                                _this.hasImg = true;
                            }
                            return imgFlag;
                        });
                    }
                    return _flag;
                });
            });
        },
        // 切换显示隐藏状态
        clickToggle(item) {
            item.show = !item.show;
            this.$forceUpdate();
        },
        //  操作按钮---form-table
        clickOperateFormTable(itemCC, item, index) {
            item.sourceType = 'form-table';
            // 触发事件
            this.emitGlobalEvent(() => {
                // 组件内部不做处理，全部交给外围处理
                this.globalBus.$emit('global-cxtj-main-operate', 'form-table', itemCC, item, index);
            });
        },
        //  操作按钮---table
        clickOperateTable(itemCC, item) {
            item.sourceType = 'table';
            // 触发事件
            this.emitGlobalEvent(() => {
                // 组件内部不做处理，全部交给外围处理
                this.globalBus.$emit('global-cxtj-main-operate', 'table', itemCC, item);
            });
        },
        //  操作按钮
        clickOperate(itemCC, item) {
            window.console.log(itemCC, item);
        },
        // 更新table数据
        updateTableData(data) {
            this.total = data.total;
            this.currentDataList[this.clickIndex].data = data.data;
            this.currentDataList[this.clickIndex].offset = data.offset;
            // 根据clickIndex确定切换第几个组件的数据
        },
        // 点击分页时触发的事件
        paginationChange(page, item, index) {
            console.log(page, item, index);
            this.offset = page.offset;
            this.limit = page.limit;
            this.clickIndex = index;
            this.componentId = item.componentId;
            const pageRequest = {
                offset: page.offset,
                limit: page.limit,
                componentId: this.componentId
            };
            this.globalBus.$emit('global-query-search', pageRequest);
        },
        // 绑定全局事件
        bindGlobalEvent() {
            if (this.globalBus) {
                // 绑定更新表格的事件
                this.globalBus.$on('global-change-table-data', this.updateTableData);
            }
        },
        // 取消全局事件
        unbindGlobalEvent() {
            if (this.globalBus) {
                // 取消绑定更新table表格的事件
                this.globalBus.$off('global-change-table-data', this.updateTableData);
            }
        }
    }
};
export default module;



