/**
 *
 *@version 1.0.1
 *@author wuwg
 *@createTime 2020/06/13 - 10:57
 *@updateTime 2020/06/13 - 10:57
 *@see [jsDoc中文文档]{@link  http://www.dba.cn/book/jsdoc/JSDOCKuaiBiaoQianBLOCKTAGS/CONSTRUCTS.html}
 @description 固定导航的条目
 */
// 事件绑定函数
import eventTool from '../tools/event.js';
// 导出模块
export default {
    // 混入工具函数
    mixins: [eventTool],
    props: {
        // 关联内容容器的id
        connectId: {
            required: false,
            validate(value) {
                return value.length && value.slice(0, 1) === '#';
            }
        },
        // 滚动后的处理函数
        scrollHandler: {type: Function},
        // 防抖时常
        delay: {
            type: Number,
            default: 50
        },
        // 移动滚动条时，动画运行的次数
        tweenTimes: {
            type: Number,
            default: 20
        }
    },
    data() {
        return {
            // 滚动容器对象
            scrollContain: null,
            // 模块数组
            moduleGroup: [],
            // 模块对象
            moduleGroupObject: {},
            // 是否事点击导航条目出发的滚动
            isClickNavItem: false,
            // 是否是初始化
            isInitComponent: true
        };
    },
    mounted() {
        this.init();
    },
    beforeDestroy() {
        // 取消绑定
        this.unbindEvent();
    },
    methods: {
        // 设置初始化组件标识
        setInitComponent(flag) {
            this.isInitComponent = flag;
        },
        init() {
            // 1.初始化参数
            this.initParams();
            // 2.设置初始化组件标识
            this.setInitComponent(true);
            // 3.绑定事件
            this.bindEvent();
            // 4.同步激活条目 (因为激活条目需要同步更新内容，所以它必须在【绑定事件】之后执行)
            this.syncActiveItem(this.activeKey);
        },
        // 刷新的方法
        refresh() {
            // 初始化参数
            this.init();
        },

        // 初始化参数
        initParams() {
            // 模块数组
            this.moduleGroup = [];
            // 模块对象
            this.moduleGroupObject = [];
            // 滚动容器对象
            this.scrollContain = document.querySelector(this.connectId);
            // 获取所有模块
            this.dataList.forEach((item) => {
                const _element = this.scrollContain.querySelector(item.moduleId);
                // 模块元素数组 （scroll事件用）
                this.moduleGroup[this.moduleGroup.length] = _element;
                // 模块元素对象 （click事件用）
                this.moduleGroupObject[item.moduleId] = _element;
            });
        },
        // 绑定事件
        bindEvent() {
            // 先解除事件绑定
            this.unbindEvent();
            // 绑定scroll 事件
            this.on(this.scrollContain, 'scroll', this.scrollOperateHandler);
        },
        // 取消事件绑定
        unbindEvent() {
            // 取消绑定scroll事件
            this.off(this.scrollContain, 'scroll', this.scrollOperateHandler);
        },
        // 滚动处理函数函数
        scrollOperateHandler(event) {
            // 如果是点击导航触发的滚动那么直接返回
            if (this.isClickNavItem) {
                return false;
            }
            // 滚动后的处理函数 (用户自定义函数)
            if (typeof this.scrollHandler === 'function') {
                // 执行回调函数
                this.scrollHandler(event, this);
            } else {
                if (this.timer) {
                    clearTimeout(this.timer);
                }
                // 执行回调函数
                this.timer = setTimeout(() => {
                    // 获取当前的模块对应的条目
                    const _activeItem = this.getCurrentModuleItem();
                    // 如果存在激活条目
                    if (_activeItem) {
                        // 设置激活的条目
                        this.setActiveItem(_activeItem);
                    }
                }, this.delay);
            }
        },
        // 获取当前的模块对应的条目
        getCurrentModuleItem() {
            let _activeItem = null;
            // 当前的scrollTop
            const rectContain = this.scrollContain.getBoundingClientRect();
            // 容器的 top值
            const rectContainTop = rectContain.top;
            // 最小值条目
            const _minValueItem = {
                rectTargetTop: 0,
                item: {}
            };
            // 循环数据
            const flag = this.dataList.every((item, index) => {
                let _flag = true;
                // 如果非隐藏
                if (!item.hide) {
                    //  获取矩形容器
                    const rectTarget = this.moduleGroupObject[item.moduleId].getBoundingClientRect();
                    const rectTargetTop = rectTarget.top;
                    const rectTargetBottom = rectTarget.bottom;
                    // 初始化_minValueItem数据
                    if (index === 0) {
                        _minValueItem.rectTargetTop = rectTargetTop;
                        _minValueItem.item = item;
                        // 跟后面的数据进行对比，如果后面数据比其小，那么就应该替换
                    } else if (_minValueItem.rectTargetTop > rectTargetTop) {
                        _minValueItem.rectTargetTop = rectTargetTop;
                        _minValueItem.item = item;
                    }
                    // 查找合适的模块，找到直接返回
                    // 判断条件： 目标模块top值小于 容器的 top 值，&&  目标的bottom值，大于容器的top值
                    if (rectTargetTop <= rectContainTop && rectTargetBottom > rectContainTop) {
                        _flag = false;
                        _activeItem = item;
                    }
                }
                return _flag;
            });
            //  如果没找到条目，那么就应该显示最小条目
            // 满足这个还需要一个条件，  当前 scrollTop < top
            if (flag && this.scrollContain.scrollTop < _minValueItem.rectTargetTop) {
                // 指向最小的条目
                _activeItem = _minValueItem.item;
            }
            // 返回需要激活的导航条目
            return _activeItem;
        },
        // 是否事点击导航条目出发的滚动
        setIsClickNavItem(flag) {
            this.isClickNavItem = flag;
        },
        // 设置滚动条的位置
        setScrollbarPosition(item) {
            // 当前的scrollTop
            const rectContain = this.scrollContain.getBoundingClientRect();
            const rectContainTop = rectContain.top;
            //  获取矩形容器
            const rectTarget = this.moduleGroupObject[item.moduleId].getBoundingClientRect();
            const rectTargetTop = rectTarget.top;
            // 目标元素和容器的差值
            const _distanceTotal = rectContainTop - rectTargetTop;
            // 次数减1， 是为了最后一次需要绝对到位
            const _times = this.tweenTimes - 1;
            // 一次运动的距离
            const _distanceSingle = _distanceTotal / _times;
            // 最终滚动条的位置
            const _scrollTop = this.scrollContain.scrollTop - _distanceTotal;
            // 如果是初始化，那么不需要动画，直接到目标位置
            if (this.isInitComponent) {
                //  1.设置滚动条位置
                this.scrollContain.scrollTop = _scrollTop;
                // 2.还原到非点击导航触发的滚动
                this.setIsClickNavItem(false);
                // 3.设置初始化组件标识(不再是初始化)
                this.setInitComponent(false);
            } else {
                // 移动滚动条
                this.moveScrollbar(_distanceSingle, _times, () => {
                    // 还原到非点击导航触发的滚动
                    this.setIsClickNavItem(false);
                }, _scrollTop);
            }
        },

        /**
         * @function
         * @description  移动滚动条的处理函数
         * @param {number} distanceTop  需要移动的距离
         * @param {number}  times 需要移动的次数
         * @param  {function} callback 移动完的回调函数
         * @param  {number}  top 最终的scrollTop 位置
         * @return {undefined} 无返回值
         */
        moveScrollbar(distanceTop, times, callback, top) {
            // 先取消动画
            cancelAnimationFrame(this.requestAnimationFrameTimer);
            // 执行动画
            this.requestAnimationFrameTimer = requestAnimationFrame(() => {
                // 老的 scrollTop
                const _oldScrollTop = this.scrollContain.scrollTop;
                //  设置滚动条位置
                this.scrollContain.scrollTop -= distanceTop;
                // 赋值后新的 scrollTop
                const _newScrollTop = this.scrollContain.scrollTop;
                // 判断次数
                if (times !== 0 && _oldScrollTop !== _newScrollTop) {
                    // 递归调用
                    this.moveScrollbar(distanceTop, times - 1, callback, top);
                } else {
                    //  设置滚动条位置
                    this.scrollContain.scrollTop = top;
                    // 执行回调函数
                    this.$nextTick(() => {
                        requestAnimationFrame(() => {
                            callback();
                        });
                    });
                }
            });
        }
    }
};



