import Vue from 'vue';
import Router from 'vue-router';
import pageA from '@/pages/page-a/index.vue';
import pageB from '@/pages/page-b/index.vue';
import pageFormTable from '@/pages/page-form-table/index.vue';
import pageCxjgMain from '@/components/app/module-cxjg/cxtj-main/index.vue';
import pageAside from '@/pages/page-aside/index.vue';


// 审批管理页面
import pageSpgl from '@/pages/page-spgl/index.vue';
import pageUnit from '@/pages/page-unit/index.vue';
// 测试界面
const pageTest = {template: '<h1>测试页面</h1>'};
// // 问题： 在写项目的时候，遇到 Avoided redundant navigation to current location的问题
// // 解决方案：，在router文件目录index.js添加下面代码
// 经过测试： 以下代码无效
// const originalPush = Router.prototype.push;
// Router.prototype.push = function (location) {
//     return originalPush.call(this, location).catch(err => err);
// };
// 解决报错Uncaught (in promise) Error: Redirected when going from ......
const originalPush = Router.prototype.push;
Router.prototype.push = function push(location, onResolve, onReject) {
    if (onResolve || onReject) {
        return originalPush.call(this, location, onResolve, onReject);
    }
    return originalPush.call(this, location).catch(err => err);
};

// 解决Vue-Router升级导致的Uncaught(in promise) navigation guard问题
const originalReplace = Router.prototype.replace;
Router.prototype.replace = function replace(location, onResolve, onReject) {
    if (onResolve || onReject) {
        return originalReplace.call(this, location, onResolve, onReject);
    }
    return originalPush.call(this, location).catch(err => err);
};
// 使用路由
Vue.use(Router);

// 导出路由模块
const router = new Router({
    routes: [
        {
            path: '/',
            redirect: '/spgl'
        },
        {
            path: '/a',
            name: 'pageA',
            component: pageA
        },
        {
            path: '/b',
            name: 'pageB',
            component: pageB
        },
        {
            path: '/unit',
            name: 'pageUnit',
            component: pageUnit
        },
        {
            path: '/test',
            name: 'pageTest',
            component: pageTest
        },
        {
            path: '/spgl',
            name: 'pageSpgl',
            component: pageSpgl,
            // 重新定向
            redirect: '/spgl/main/all',
            children: [
                {
                    // 当 /user/profile 匹配成功，
                    // pageSpgl 会被渲染在 User 的 <router-view> 中
                    path: 'main/:id',
                    name: 'main',
                    component: pageCxjgMain,
                    props: function (route) {
                        return {
                            id: route.params.id,
                            params: route.params,
                            query: route.query
                        };
                    }
                }
            ]
        },
        {
            path: '/form-table',
            name: 'pageFormTable',
            component: pageFormTable
        },
        {
            path: '/aside',
            name: 'pageAside',
            component: pageAside
        }
    ]
});
// 如果是刷新页面，直接跳转到首页
let _isRefresh = false;
// 全局守卫
router.beforeEach((to, from, next) => {
    // 基本信息编号 id 来源于第一期系统
    // jbxxbhId:window.fdConfig.jbxxbhId,
    // token  来源登录
    // token:window.fdConfig.token,
    if (_isRefresh) {
        _isRefresh = false;
        next({path: '/spgl'});
        //  基本信息编号 id 来源于第一期系统
    } else if (to.query.id) {
        next();
    } else {
        // 基本信息编号 id 来源于第一期系统 【必须参数】，在首次进入页面url中叫id，
        // 在 main.js 中获取并暂存
        //  因为其他系统传入的标识是 id， 所以这里不要做变更，还是保持一致，叫id，不要换名字叫jbxxbhId
        to.query.id = window.fdConfig.jbxxbhId;
        // token  来源登录, @todo  20201204  暂时不需要token，已经写入到cookie
        // to.query.token = window.fdConfig.token;
        next({
            path: to.path,
            params: to.params,
            query: to.query
        });
    }
});
export default router;



