/**
 * @version 20190401
 * @author wuwg
 * @createTime  20190401
 * @updateTime  20190430
 * @decription  树组件计算整体高度
 * 命名空间（name space）H
 */
export default {
    props: {},
    data() {
        return {
            // 分段高
            hSubsectionHeight: 10000,
            // 每个li的高度
            hItemHeight: this.itemHeight,
            // 可视的数量
            hClientCount: this.clientCount
        };
    },
    computed: {
        // 总高度
        hCountTotalHeight() {
            return this.dResultShowDataListLength * this.hItemHeight;
        },
        // 计算占位元素高
        hCountPlaceholderHeight() {
            // 一共分了多少段
            const _howSubsection = Math.ceil(this.hCountTotalHeight / this.hSubsectionHeight);
            const _residue = this.hCountTotalHeight % this.hSubsectionHeight;
            const _html = [];
            // 循环总共多少段
            for (let i = 0; i < _howSubsection; i++) {
                if (i < _howSubsection - 1) {
                    _html.push(`<div style="height:${this.hSubsectionHeight}px"></div>`);
                } else {
                    _html.push(`<div style="height:${_residue !== 0 ? _residue : this.hSubsectionHeight}px"></div>`);
                }
            }
            return _html.join('');
        }
    }
};




