/**
 * @version 20190401
 * @author wuwg
 * @createTime  20190416
 * @updateTime  20190422
 * @decription  放置处理，处理模块
 */
export default {
    methods:{
        // 拖拽后代【单条数据递归】
        edcSingleRecursion(data, flag) {
            data.forEach((item, index) => { // eslint-disable-line
                // 存在子元素，并且子元素长度大于0， 并且当前元素是展开的并且显示的
                if (Array.isArray(item[this.dChildren]) && item[this.dChildren].length && item.extend && item.show) {
                    this.edcSingleRecursion(item.children, flag);
                }
                // 当前节点
                const _currentTreeNode = window.treeCacheObject[this.id][item.id];
                // 当前节点
                if (_currentTreeNode) {
                    // 是否是拖拽元素的子元素（正在拖拽元素是其祖先元素）
                    _currentTreeNode.treeNodeData.draggingancestors = flag;
                    // 跟新页面数据
                    this.updateCurrentDataItem(_currentTreeNode.treeNodeData, 'draggingancestors');
                }
            });
        },
        // 设置后代元素状态
        edcSetDragNodeDescendantState() {
            const _dragNode = this.dragNode;
            if (_dragNode && _dragNode.treeNodeData && _dragNode.treeNodeData.children && _dragNode.treeNodeData.children.length) {
                // 递归元素
                this.edcSingleRecursion(_dragNode.treeNodeData.children, true);
            }
        },
        // 设置后代元素状态
        setDragNodeDescendantState() {
            const _dragNode = this.dragNode;
            if (_dragNode && _dragNode.treeNodeData && _dragNode.treeNodeData.children && _dragNode.treeNodeData.children.length) {
                // 递归元素
                this.edcSingleRecursion(_dragNode.treeNodeData.children, true);
            }
        },
        // 重置后代元素状态
        edcResetDragNodeDescendantState() {
            const _dragNode = this.dragNode;
            if (_dragNode && _dragNode.treeNodeData && _dragNode.treeNodeData.children && _dragNode.treeNodeData.children.length) {
                // 递归元素
                this.edcSingleRecursion(_dragNode.treeNodeData.children, false);
                this.edCopyCurrentDataList = this.edCopyCurrentDataList.map((item) => {
                    item.draggingancestors = false;
                    return item;
                });
            }
        },
        // 重置后代元素状态
        resetDragNodeDescendantState() {
            const _dragNode = this.dragNode;
            if (_dragNode && _dragNode.treeNodeData && _dragNode.treeNodeData.children && _dragNode.treeNodeData.children.length) {
                // 递归元素
                this.edcSingleRecursion(_dragNode.treeNodeData.children, false);
                this.edCopyCurrentDataList = this.edCopyCurrentDataList.map((item) => {
                    item.draggingancestors = false;
                    return item;
                });
            }
        }
    }
};



